import { Injectable, Injector } from '@angular/core';
import { Observable } from 'rxjs';
import { map, take } from 'rxjs/operators';
import { CommonService } from '../../pure/services/common.service';
import { PlatformService } from '../../pure/services/platform.service';
import { SentryService } from '../../pure/services/sentry.service';
import { SubscribeService } from '../../pure/services/subscribe.service';
import { DataService } from '../services/data.service';
import { StorageService } from '../services/storage.service';

@Injectable({
    providedIn: 'root'
})
export class BaseContext {

    public fields: string[];
    public dataService: DataService;
    public contextId: string;
    public storageService: StorageService;
    public subscribeService: SubscribeService;
    public platformService: PlatformService;
    public sentryService: SentryService;

    constructor(injector: Injector) {

       this.storageService = injector.get(StorageService);
       this.subscribeService = injector.get(SubscribeService);
       this.platformService = injector.get(PlatformService);
       this.sentryService = injector.get(SentryService);
       this.dataService = injector.get(DataService);
    }

    get(fieldName: string, url: string, params:{} = {}){

        this.dataService.get(url, params).pipe(map(jo => this.setData(fieldName, jo)));

    }

    private setData(fieldName: string, data: any){

        this[fieldName] = data;
        return data;
    }

    /*
    bind(obj: any, fieldName: string){

        var bs : BehaviorSubject<any> = this[fieldName];

        if(bs){

        }

    }*/

    //Child should overwrite this, or nothing will be saved
    protected makeSaveData(): {}{
        return null;
    }

    protected restoreSavedData(data: any){

    }

    protected makeGenericKey(context: any = {}, type: string, locale: string = "multi"){

        if(!context.groupId) return null;

        var segs = [];

        segs.push("type=" + type);
        segs.push("groupId=" + context.groupId);

        if(context.courtId){
            segs.push("courtId=" + context.courtId);
        }else if(context.brandId){

            segs.push("brandId=" + context.brandId);

            /*
            if(context.storeId){
                segs.push("storeId=" + context.storeId);
            }
            */
        }

        segs.push("locale=" + locale);

        var key = segs.join('&');

        return key;
    }

    public save(){


        var key = this.contextId;

        console.log("saving context", key);

        if(!key){
            console.log("MISSING contextId");
            return;
        }

        var data = this.makeSaveData();

        if(!data){
            this.storageService.setStorage(key, null);
            return;
        }

        if (!this.platformService.isSDK() || (this.platformService.isSDK() && window['cachedOrderContext'])) {
            var now = new Date().getTime();
            data['saved'] = now;
            data['expire'] = now + 1000 * 60 * 60 * 24 * 7;

            var serialized = this.serialize(data);

            this.storageService.setStorage(key, serialized);

            console.log("saved context");
        }

    }

    private serialize(data: any, compress: boolean = false){

        if(!compress) return data;

        var jsonStr = JSON.stringify(data);
        return jsonStr;
    }

    private deserialize(raw: any, compress: boolean = false){

        if(!compress) return raw;

        var result = JSON.parse(raw);

        console.log("deserialized", result.groupId, result);

        return result;
    }

    public restore(callback: Function = null){

        console.log("restoring context", this.contextId, window.location.href);

        this.storageService.getStorage(this.contextId)
        .then((value)=>{

            var de = this.deserialize(value);

            if(de){
                this.restoreSavedData(de);
                if(callback) callback(this);
            }else{
                if(callback) callback(null);
            }

        }).catch((reason)=>{
            console.log("ERROR", reason);
            if(callback) callback(null);
        });

    }


    setLocal(key: string, object: any) {

        CommonService.setLocal(key, object);
    }

    getLocal(key: string): any {

        return CommonService.getLocal(key);
    }


    bind(fields: string[]){
        this.subscribeService.bind(this, fields);
    }

    next(name, value){
        this[name] = value;
        this.subscribeService.next(name, value);
    }

    listen(name: string, once:boolean = false): Observable<any>{

        var obs : any = this.subscribeService.getObservable(name);

        if(once){
            obs = obs.pipe(take(1));
        }

        return obs;
    }





}
