import { Category } from './category';
import { ItemGroup } from './item-group';
import { Data } from './data';
import { Discount } from './discount';
import { Coupon } from './coupon';

export class Item extends Data {

    class: string = 'Item';
    category: any;
    id: string;
    name: string;
    images: any;

    price: number;
    price0: number;
    price1: number;
    price2: number;
    originalPrice?: number;
    published: boolean = true;
    suspended: boolean = false;
    inventory: number = -1;
    quantity: number = 0;
    backendId: string;
    sku: string;
    printName: string;
    start: number;
    end: number;
    points: number = 0;

    groupIds: string[] = [];
    mgroupIds: string[] = [];

    groups: ItemGroup[] = [];
    mgroup: string;
    group: string;
    mgroups: ItemGroup[] = [];
    tags: string[];
    memberTier: string[];

    origin: Item;
    modifier: boolean = false;

    combo: boolean = false;
    modifiable: boolean = false;
    printers: string[] = [];

    parentId: string;
    absentId: string;

    min: number;
    max: number;
    baseQty: number = 0;
    soldQty: number = 0;
    title: string;

    extra: any;
    dinein: boolean = false;
    pgroupId: string;
    pgroup: ItemGroup;

    handling: number = 0;
    discount: number = 0;
    discountCode: string = '';

    variations: Item[];
    marker: string;
    periods: string[];

    isPrint: boolean = true;
    modes: string[];

    prefix: string = '';
    discounted: boolean = false;
    discounts: Discount[] = [];
    discountable: boolean = true;
    segments: string[];
    rating: number;
    featuredRating: number = 0;
    featuredUsers: string[];
    addTime: number = Number(new Date()) + Math.random();
    popularity: number;

    discountedId: string;
    filters: string[];
    desc: string;
    backendName: string;
    comboId: string;
    comboName: string;
    taxGroup: string;
    featured: boolean = false;
    packing: number;
    nutrients: any; //no nutrients interface yet
    allergy: any; //no allergy interface yet

    minPrice: number;
    maxPrice: number;

    callNo: string;
    message: string;
    receipt: boolean;
    ohId: string; //for e365

    barcode: string;//self checkout
    serviceChargeable: boolean;
    coupons: Coupon[] = [];
    type: string;

    //
    messageLimit: number;
    messageMaxLength: number;
    messageOptional: boolean;
    custom: any;

    //analytics purpose, not part of real object
    igPos: number = 0;

    setLayout: string = null;

    users: string[];

    verificationGroupIds: string[];
    protected toProperty(name: string, value) {

        switch (name) {

            case 'groups':
                value = Data.toDataArray(ItemGroup, value);
                break;
            case 'mgroups':
                value = Data.toDataArray(ItemGroup, value);
                break;
            case 'coupons':
                value = Data.toDataArray(Coupon, value);
                break;
            case 'variations':
                value = Data.toDataArray(Item, value);
                break;
            default:
                value = super.toProperty(name, value);
                break;

        }

        return value;
    }

    static isSet(category: Category, item: Item) {

        if (category && category.igMode == 'single') {
            return false;
        }

        if (item.groups && item.groups.length > 0) {
            return true;
        }

        if (category && category.groups && category.groups.length > 1) {

            return true;
        }

        return false;
    }

    static isVariations(item: Item, singleChildShowVariation: boolean) {
        if(singleChildShowVariation && item.parentId === item.id && item.variations.length == 0){
            return true;
        }
        return item && item.variations && item.variations.length > 0;
    }

    static isModifiable(item: Item): boolean {
        let mgroups = item.mgroups;
        if (!mgroups || mgroups.length == 0) {
            return false;
        }
        return mgroups.some(g => g.items && g.items.length > 0);
    }

    static isSetMessageLimit(item: Item): boolean {
        return item && item.messageLimit && item.messageLimit > 0;
        ;
    }

    public static isPublished(item: Item, now: number): boolean {

        if (!item.start && !item.end) {
            return true;
        }

        let greaterThanStart = true;
        let lessThanEnd = true;

        if (item.start) {
            greaterThanStart = now >= item.start;
        }

        if (item.end) {
            lessThanEnd = now <= item.end;
        }

        return greaterThanStart && lessThanEnd;

    }

    public static filterPublished(items: Item[], now: number) {

        var result = [];

        if (!items || items.length == 0) {
            return result;
        }

        for (let item of items) {
            if (item.published && Item.isPublished(item, now)) {
                result.push(item);
            }
        }

        return result;

    }

    public static filterPublishedItems(items: Item[], cmap: any, catId: string, index: number, filter?: any) {

        var result = [];
        var catIdWithIndex = catId + '.' + index.toString();


        if (!items || items.length == 0) {
            return result;
        }


        for (let item of items) {
            if (item.groups.length == 0) {
                item.combo = false;
            }

            if (cmap && (cmap[catIdWithIndex] || cmap[catId])) {
                var published = cmap[catIdWithIndex] ? cmap[catIdWithIndex] : cmap[catId];

                if (published && published[item.id]) {
                    result.push(item);

                }

            } else {

                if (filter) {
                    if (filter(item)) {
                        result.push(item);
                    }

                } else if (item.published) {

                    result.push(item);
                }

            }
        }

        return result;

    }

    static toItemsData(items: Item[], toServer: boolean = false): any[] {
        var results: any[] = [];
        for (let item of items) {
            var tmp = Item.toItemData(item, toServer);
            if (toServer) {
                // console.log('tem', tmp); BurgerKingfix : baseQty  q=0 item push order,absentItem , frontend mitem(with qua =0) post to server
                if (tmp?.quantity > 0) {
                    results.push(tmp);
                }
            } else {
                results.push(tmp);
            }
        }
        return results;

    }

    static toItemData(item: Item, toServer: boolean = false): any {
        var json = {};
        json['id'] = item.id;
        json['quantity'] = item.quantity;
        json['inventory'] = item.inventory;
        json['name'] = item.name;
        json['price'] = item.price;
        json['price0'] = item.price0;
        json['price1'] = item.price1;
        json['price2'] = item.price2;
        json['modifiable'] = item.modifiable;
        json['modifier'] = item.modifier;
        if (item.points >= 0) {
            json['points'] = item.points;
            json['usedPoints'] = item.points;
        }
        json['title'] = item.title;

        json['discounted'] = item.discounted;
        json['mgroups'] = item.mgroups ? ItemGroup.toItemGroupsData(item.mgroups, toServer) : null;
        json['combo'] = item.combo;
        json['comboId'] = item.comboId;
        json['comboName'] = item.comboName;
        json['taxGroup'] = item.taxGroup;
        json['discountedId'] = item.discountedId;
        json['barcode'] = item.barcode;
        json['custom'] = item.custom;
        json['addtime'] = item.addTime;
        json['type'] = item.type;
        json['baseQty'] = item.baseQty;
        json['absentId'] = item.absentId;


        // it should minus the quantity if baseQty exists when post order to server(server calculate not handle item.baseQty)
        if (toServer && item?.baseQty && item?.quantity > 0) {
            json['quantity'] = item.quantity - item.baseQty;
        }

        // copy i18n item name also for supporting translation when refresh
        const keys = Object.keys(item);
        keys.filter(f => f.includes('name_'))
            .map(o => o)
            .forEach(o => {
                json[o] = item[o];
            });
        return json;

    }

    static getPrice(item: Item, level: number) {

        var result = item.price;

        switch (level) {
            case 1:
                result = item['price1'];
                break;
            case 2:
                result = item['price2'];
                break;
            case 3:
                result = item['price3'];
                break;
            case 4:
                result = item['price4'];
                break;
            case 5:
                result = item['price5'];
                break;
            case 6:
                result = item['price6'];
                break;
            case 7:
                result = item['price7'];
                break;
            case 8:
                result = item['price8'];
                break;
            case 9:
                result = item['price9'];
                break;
            default:
                result = item['price1'];
                break;
        }

        if (!result && result != 0) {
            result = item.price;
        }

        return result;

    }

    public static isItemHasCustomMessage(item: Item): boolean {
        return item && item.custom && item.custom.message && item.custom.message.length > 0;
    }

}
