export class MathUtils {


    public static round(value: number, precision: number) {
        var multiplier = Math.pow(10, precision || 0);
        return Math.round(value * multiplier) / multiplier;
    }

    public static floor(value: number, precision: number) {
        var multiplier = Math.pow(10, precision || 0);
        return Math.floor(value * multiplier) / multiplier;
    }

    public static ceil(value: number, precision: number) {
        var multiplier = Math.pow(10, precision || 0);
        return Math.ceil(value * multiplier) / multiplier;
    }

    public static roundToNearest5(value: number, precision: number) {
        var precisionUnit = 5 / Math.pow(10, precision || 0);

        if (precisionUnit === 0) { return value }
        var unitRawCount = 0;
        unitRawCount = value / precisionUnit;
        var roundedCount = Math.round(unitRawCount)

        return this.round(roundedCount * precisionUnit, precision);
    }
}
