import {Data} from './data';

export class Coupon extends Data {

  class: string = "Coupon";
  sequence: number;
  code: string;
  couponSetId: string;
  shareLink: string;

  protected toProperty(name: string, value) {

    switch (name) {

      default:
        value = super.toProperty(name, value);
        break;

    }

    return value;
  }

}
