import { ChangeDetectorRef, Component, ContentChild, Input, OnInit, TemplateRef } from '@angular/core';

// import Swiper core and required modules
/*
import SwiperCore, {
    Autoplay, Navigation,
    Pagination
} from 'swiper/core';
*/


// install Swiper modules
//SwiperCore.use([Navigation, Pagination, Autoplay]);

@Component({
    selector: 'app-swiper',
    templateUrl: './swiper.component.html',
    styleUrls: ['./swiper.component.scss']
})
export class SwiperComponent implements OnInit {

    @ContentChild(TemplateRef) templateVariable: TemplateRef<any>;

    //@Input('gap') gap: any = 10;
    @Input('contents') contents: any[] = [];
    @Input('config') config: any = {};

    @Input('autoplay') autoplay: boolean | any = false;
    @Input('loop') loop: boolean = true;

    gap: any = 10;

    /*
    config: any = {
        slidesPerView: 'auto',
        spaceBetween: 20,
        breakpoints: {
            // when window width is >= 320px
            320: {
                slidesPerView: 2.5,
                spaceBetween: 10
            },
            640: {
                slidesPerView: 3,
                spaceBetween: 20
            },
            960: {
                slidesPerView: 3.5,
                spaceBetween: 20
            },
            1440: {
                slidesPerView: 4,
                spaceBetween: 20
            }
        }
    };
    */

    //@ViewChild('leftNav', {read: ElementRef, static:false}) leftNav: ElementRef;
    //@ViewChild('rightNav', {read: ElementRef, static:false}) rightNav: ElementRef;

    showLeft: boolean = false;
    showRight: boolean = true;

    constructor(private ref: ChangeDetectorRef) { }

    ngOnInit(): void {
        
        console.log("config", this.config);

        this.gap = this.config?.spaceBetween || 10;
    }

    onSlideChange(event) {

        this.showLeft = !event.isBeginning;
        this.showRight = !event.isEnd;

        //console.log("EVENT", event);

        this.ref.detectChanges();
    }

}
