import { Injectable } from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class SentryService {

    constructor() { }

    private _Sentry: any = null;

    private logError: boolean = false;

    get Sentry() {
        if(this._Sentry) {
            return this._Sentry;
        }
        this._Sentry = (<any> window).Sentry;
        return this._Sentry;
    }

    log(...args: any): void {
        if(this.logError) {
            console.log(...args);
        }
    }

    track(eventName, event): void {
        try {
            this.Sentry?.addBreadcrumb({
                type: 'user',
                category: 'event',
                message: eventName,
                data: {...event},
                level: "info",
            });
        } catch(e) {
            this.log(e);
        }
    }

    captureException(error): void {
        try {
            this.Sentry.captureException(error);
        } catch (e) {
            this.log(e);
        }
    }

    setTag(key: string, value: string): void {
        try {
            this.Sentry.setTag(key, value);
        } catch (e) {
            this.log(e);
        }
    }

    setMember(member: any): void {
        try {
            const sentryMember = { id: member.id }
            if(member?.name) {
                sentryMember['username'] = member?.name;
            }
            if(member?.email) {
                sentryMember['email'] = member?.email;
            }

            this.Sentry.setUser(sentryMember);
        } catch(e) {
            this.log(e);
        }

    }

    removeMember(): void {
        try {
            this.Sentry.setUser(null);
        } catch(e) {
            this.log(e);
        }
    }

    setContext(key: string, context: any): void {
        try {
            this.Sentry.setContext(key, context);
        } catch(e) {
            this.log(e);
        }
    }
}
