import { Data } from './data';
import { Discount } from './discount';
import { Order } from './order';

export class Reward extends Data {
    class: string = "Reward";
    id: number;
    name: string;
    hint: string;
    published: boolean;
    backendId: string;
    price: number;
    start: number;
    end: number;
    cost: number;
    desc: string;
    quota: number;
    spending: number;
    redemptions: number;
    chance: number;
    discount: Discount;
    safeDesc: any;
    extra: any;

    static toRewardData(reward: Reward): any {
        var json = {};
        json["id"] = reward.id;
        json["name"] = reward.name;
        json["hint"] = reward.hint;
        json["published"] = reward.published;
        json["backendId"] = reward.backendId;
        json["price"] = reward.price;
        if (reward.discount) {
            json["discount"] = Discount.toDiscountData(reward.discount);

        }


        return json;

    }

    protected toProperty(name: string, value) {

        if (name == "discount") {
            return Data.toData(Discount, value);
        }

        return value;
    }

    // orderreward(reward flowoffer, apply  discount)
    // Whether to meet the conditions of using reward with Order
    public isReawardSatisfied(order: Order): boolean {
        let ok: boolean = true;
        let promo = this.findPromo_discount(order);
        promo && this.discount && this.discount.conditions.forEach(c => {
            if (!c.isSatisfied(order, promo)) ok = false;
        })
        return ok;
    }

    //  discounts  reward  discount
    public findPromo_discount(order: Order): Discount {
        // order.promotions: original discounts_all information
        if (!order || !order.promotions || order.promotions.length === 0) return null;

        let promos = order.promotions.filter((pro) => {
            return this.discount && pro && this.discount.id === pro.id;
        });

        let promo = promos && promos[0];
        if (!promo) {
            console.log('reward discount in store.menu.discounts(brand.discounts)');
        }
        return promo;
    }

}
