import { Injectable } from '@angular/core';
import { Item, Member, Menu, PriceRule, Store } from 'aigens-data';
import _ from 'lodash-es';

/**
 * Update items' price according to different modes
 * **/

@Injectable({providedIn: 'root'})
export class PriceManager {

    level: number = 1;
    priceRules: PriceRule[] = [];
    debugTime: any;

    priceLevelChange = false;

    directoryUnixTime;

    constructor() {

    }

    getTime() {
        if (this.debugTime) {
            return this.debugTime;
        }
        // return new Date().getTime();
        return this.directoryUnixTime ?? new Date().getTime();
    }

    setDebugTime(time: number) {
        this.debugTime = time;
    }


    pickPriceTierRule(menu: Menu, mode: string, member: Member, store: Store): number {

        var level = 1;
        var priceRules = menu.priceRules;
        if (!priceRules) {

            return level;
        }
        var now = this.getTime();

        console.log("checking priceRules", priceRules);

        var topMPr;
        var topScore = -10000;
        var type = member?.membership?.memberTierPolicy?.tier || member?.membership?.tier;

        for (const pr of priceRules) {
            var score = PriceRule.getMatch(pr, now, mode, member, store, true);
            if (score > topScore) {
                if (!pr.priority) {
                    pr.priority = 0;
                }
                if ((!topMPr || (topMPr && pr.priority > topMPr.priority) && type === pr?.tier)) {
                    topMPr = pr;
                    topScore = score;
                }
            }
        }
        if (topMPr) {
            level = topMPr.level;
        }
        return level;
    }

    spotPeriodLevel(store, spotData): number | undefined {
        const spotOpen = spotData?.['open'];
        const spotPeriod = spotOpen && Store.checkTimeWithinPeriods(store, spotOpen)?.[0];
        if (_.isEmpty(spotData) || _.isEmpty(spotPeriod)) {
            return undefined;
        }
        const spotPeriodCode = spotPeriod?.code;
        const currentPeriod = Store.checkTimeWithinPeriods(store, this.getTime())?.[0];
        const periodOverride = currentPeriod?.periodOverride;

        return periodOverride?.[spotPeriodCode];
    }

    pickPriceRule(menu: Menu, mode: string, member: Member, store: Store, spotData = {}): number {

        var level = 1;

        const spotLevel = this.spotPeriodLevel(store, spotData);
        if (typeof spotLevel !== 'undefined') {
            return spotLevel;
        }

        var priceRules = menu.priceRules?.filter((priceRule) => !priceRule.tier);
        if (!priceRules) {

            return level;
        }
        var now = this.getTime();

        console.log("checking priceRules", priceRules);

        var topPr;
        var topScore = -10000;

        for (const pr of priceRules) {

            var score = PriceRule.getMatch(pr, now, mode, member, store, false);
            if (score > topScore) {
                // if match, then get the highest priority
                if (!pr.priority) {
                    pr.priority = 0;
                }

                if (!topPr || (topPr && pr.priority > topPr.priority)) {
                    topPr = pr;
                    topScore = score;
                }
            }
        }

        if (topPr) {
            level = topPr.level;
        }

        return level;
    }

    updateBasePrice(menu: Menu, mode: string, member: Member, store: Store = null, spotData = {}) {

        console.log('updateBasePrice', mode, member, this.getTime());

        var newLevel = this.pickPriceRule(menu, mode, member, store, spotData);

        console.log('pick level', newLevel);

        if (newLevel == menu.priceLevel) {
            this.priceLevelChange = false;
            return;
        }

        this.priceLevelChange = true;

        this.level = newLevel;
        menu.priceLevel = newLevel;
        this.priceRules = menu.priceRules;

        var gmap = menu['gmap'];

        var keys = Object.keys(gmap);

        for (const key of keys) {
            var ig = gmap[key];

            for (const item of ig.items) {
                var priceX = Item.getPrice(item, this.level);
                item.price = priceX;

            }
        }

    }

    updateMinMaxPrice(menu: Menu){

        console.log("updating min max price");

        var gmap = menu['gmap'];

        if(!gmap){
            console.log("no gmap for this menu", menu.id);
            return;
        }

        var keys = Object.keys(gmap);

        for(const key of keys){
            var ig = gmap[key];

            var maxPrice = null;
            var minPrice = null;

            for(const item of ig.items){
                var price = item.price;
                if(maxPrice == null || price > maxPrice) maxPrice = price;
                if(minPrice == null || price < minPrice) minPrice = price;

            }

            if(ig.min > 0){
                ig.minPrice = minPrice * ig.min;
                ig.maxPrice = maxPrice * ig.min;
            }


        }

        for(const key of keys){
            var ig = gmap[key];

            for(const item of ig.items){

                if(item.groupIds && item.groupIds.length > 0){

                    var price = item.price;
                    var minPrice = price;
                    var maxPrice = price;

                    for(const gid of item.groupIds){
                        var ig = gmap[gid];
                        if(ig){
                            if(ig.minPrice) minPrice += ig.minPrice;
                            if(ig.maxPrice) maxPrice += ig.maxPrice;
                        }
                    }

                    item.minPrice = minPrice;
                    item.maxPrice = maxPrice;

                }

            }

        }



    }



    pickOrderMinMax(menu: Menu, mode: string, member: Member, store: Store) {
        var priceRules = menu.priceRules;
        if(!priceRules) return;
        var now = this.getTime();

        console.log("checking priceRules", priceRules);

        var topPr;
        var topScore = -10000;

        for(const pr of priceRules){

            var score = PriceRule.getMatch(pr, now, mode, member, store, false);
            if (score > topScore) {
                topPr = pr;
                topScore = score;
            }
            console.log('score', score, pr.name);
        }

        return topPr;
    }

    memberTierPrice(menu: Menu, mode: string, member: Member, store: Store = null) {
        const memberTier = member?.membership?.memberTierPolicy?.tier || member?.membership?.tier;
        if (memberTier) {
            var tierLevel = this.pickPriceTierRule(menu, mode, member, store);
            var originalLevel = this.pickPriceRule(menu, mode, member, store);

            var gmap = menu['gmap'];

            var keys = Object.keys(gmap);
            for (const key of keys) {
                var ig = gmap[key];
                for (const item of ig.items) {
                    if (item.memberTier?.includes(memberTier) && tierLevel !== 0 && tierLevel !== originalLevel) {
                        var priceX = Item.getPrice(item, tierLevel);
                        var priceY = Item.getPrice(item, originalLevel);

                        item.price = priceX;
                        item['originalPrice'] = priceY;
                    }
                }
            }
        }
    }
}
