import { Injectable, Injector } from '@angular/core';
import { BehaviorSubject, first } from 'rxjs';
import { CommonService } from './common.service';
import { TranslateService } from '@ngx-translate/core';
import { merge } from 'lodash-es';

//Peter: include specific class from framework instead of whole folder
//Should not include framework class in pure folder. Should remove in long term.
import { StorageService } from '../../framework/services/storage.service';


@Injectable({
    providedIn: 'root'
})
export class SubscribeService {


    FIRST_LOAD_EMBEDDED_CONTEXT = 'firstLoadEmbeddedContext';
    SDK_MEMBER_CONTEXT = 'sdkMemberContext';

    isLoadMemberContext = false;

    constructor(injector: Injector, private storageService: StorageService) {
        this.init();
        this.setTranslation(injector.get(TranslateService));
    }

    getQuery(name: string) {

        const queryString = window.location.search;
        const urlParams = new URLSearchParams(queryString);
        return urlParams.get(name);
    }

    private init() {

        if (!window['aigens']) {
            window['aigens'] = {};
        }

        var aigens = window['aigens'];

        if (aigens && aigens.context) {
            Object.assign(this, aigens.context);
        }

        let userLang;
        let urlLocale = this.getQuery("locale");

        const languages = window['aigens']?.context?.languages;

        if (!languages || languages?.includes(CommonService.getLocal('locale'))) {
            userLang = CommonService.getLocal('locale');
        }

        var lang = urlLocale || userLang || window['aigens']?.context?.language || 'en';

        this['userLang'] = lang;
        CommonService.setLocal('locale', lang); // override locale on local storage
        this.next('language', lang);
        aigens.context = this;


        this.storageService.getStorage(this.getMemberKey())
            .then((value) => {
                const member = value?.member;

                if (member) {
                    this.next('member', member);
                    this.next('sessionId', value?.session);
                }
                this.next('isLoadMemberContext', true); // remark the memberContext loaded
                console.log('=== ', value);


            }).catch((reason) => {
            console.log('ERROR', reason);
        });


        // var member = CommonService.getLocal('member');
        // if (member) {
        //     this.next('member', member);
        // }

    }

    update(): void {
        var aigens = window['aigens'];

        if (aigens && aigens.context) {
            Object.assign(this, aigens.context);
        }
    }

    getObservable(name: string, initValue = null): BehaviorSubject<any> {

        var name2 = name + '$';

        var bs = this[name2];
        if (!bs) {

            bs = new BehaviorSubject(initValue);
            bs.name = name;
            this[name2] = bs;

            //console.log('making bs', name, initValue, bs);
        }

        return bs;
    }

    get(name: string): any {
        return this[name];
    }

    next(name: string, value: any) {

        console.log('next', name, value);

        this[name] = value;
        this.getObservable(name).next(value);


    }

    bind(listener: any, fields: string[]) {

        for (let f of fields) {

            var cb = function (value) {
                //console.log("bind callback!", listener, f, value, this);
                listener[f] = value;

            };

            this.getObservable(f).subscribe(cb);
        }

    }


    /**
     * Merge overwrite translation here to get the latest translation update in home page (home module)
     *
     * @param t
     * @private
     */
    private setTranslation(t: TranslateService): void {
        t.onLangChange.pipe(first()).subscribe((res) => {
            this.mergeTranslation(t);
        });
    }

    public mergeTranslation(t: TranslateService): void {
        const overwrites = this['config']?.data?.translations;

        if(overwrites){
            for (const lang of Object.keys(overwrites)) {
                const x = t.translations[lang];
                merge(x, overwrites[lang]);
            }
        }
    }

    logout() {
        this.storageService.setStorage(this.getMemberKey(), null);
    }


    getMemberKey() {
        if (!window['aigens']) {
            window['aigens'] = {};
        }

        var aigens = window['aigens'];
        let contextId = 'type=member';

        if (aigens && aigens.context) {

            const context = aigens.context;
            const crm = context.crm;

            if (crm) {
                contextId += '&crm=' + crm.type;
                if (crm.source) {
                    contextId += '&source=' + crm.source;
                }
            }
        }

        return contextId;
    }
}
