import { TimeService } from './../../pure/services/time.service';
import { AfterViewInit, Component, Injector } from '@angular/core';
import { Brand, Court, Data, Group, Member, Membership, Store } from 'aigens-ng-core';
import { skip } from 'rxjs/operators';
import { MemberContext, ThemeColorService } from '..';
import { PageParams } from '../../pure/base/page-params';
import { AdService } from '../../pure/services/ad.service';
import { SentryService } from '../../pure/services/sentry.service';
import { ConfigService } from '../services/config.service';
import { ContextManager } from '../services/context-manager.service';
import { Context } from '../services/context.service';
import { BaseComponent } from './base-component';


@Component({ template: '' })
export class BasePage extends BaseComponent implements AfterViewInit{

    member: Member;
    membership: Membership;
    group: Group;
    brand: Brand;
    store: Store;
    court: Court;

    navParams: PageParams;

    protected timer: any;
    protected active = false;

    private cb: any;

    winWidth: number = window.innerWidth;
    autoDismissPreLoader: boolean = true;

    private themeColorService: ThemeColorService;
    contextManager: ContextManager;
    memberContext: MemberContext;
    sentryService: SentryService;

    adService: AdService;

    timeService: TimeService;

    constructor(injector: Injector) {

        super(injector);

        this.navParams = new PageParams();

        this.navParams.route = this.route;

        this.configService = injector.get(ConfigService);
        this.context = injector.get(Context);

        this.navParams.querys = this.route.snapshot.queryParams;
        this.navParams.params = this.route.snapshot.params;

        this.navParams.input = this.routingService.getAuxInput();

        this.member = this.context.member;
        this.membership = this.context.membership;
        this.group = this.context.group;
        this.brand = Data.toData(Brand, this?.context?.brand);
        this.store = this.context.store;
        this.court = this.context.court;

        this.themeColorService = injector.get(ThemeColorService);

        this.contextManager = injector.get(ContextManager);
        this.memberContext = injector.get(MemberContext);

        this.adService = injector.get(AdService);
        this.sentryService = injector.get(SentryService);
        this.timeService = injector.get(TimeService);

    }

    getNav(app: any): void {
        const navs = app.getRootNav();
        if (navs && navs.length > 0) {
            return navs[0];
        }
        return app.getActiveNav('nav');
    }

    ngAfterViewInit(){

        console.log("ngAfterViewInit", this.route.snapshot.data);

        var routeData = this.route.snapshot.data;

        if(routeData && routeData.preloader === true){

        }else{
            this.dialogService.dismissPreLoader();
        }

        this.sentryService.setContext('Aigens', {
            version: window['aigens']?.version ?? ''
        });
        if (window['aigens']?.context?.brandId) {
            this.sentryService.setTag("brandId", window['aigens']?.context?.brandId);
        }
        if (window['aigens']?.context?.courtId) {
            this.sentryService.setTag("courtId", window['aigens']?.context?.courtId);
        }

    }

    canBack(): boolean {
        //return this.routingService.canBack();
        return this.routingService.canBack();
    }

    protected getUIRefreshInterval(): number {
        return 0;
    }

    protected onRefreshUI(): void {
        console.log('you need to implment onRefreshUI');
    }

    private startTimer(interval): void {
        if (!this.timer) {
            console.log('start timer');
            this.timer = setInterval(() => {
                this.onRefreshUI();
            }, interval);
        }
    }

    private stopTimer(): void {

        if (this.timer) {
            console.log('stop timer');
            clearInterval(this.timer);
            this.timer = null;
        }
    }

    ngOnInit(): void {

        const interval = this.getUIRefreshInterval();

        console.log("INT", interval);

        if (interval > 500) {
            this.startTimer(interval);
        }
    }

    ngOnDestroy(): void {
        this.stopTimer();
    }

    hasMenu(): boolean {
        return this.routingService.hasMenu() && !this.settingManager.get('appHideTabbarMenu', false);

    }

    isRoot(): boolean {
        return this.routingService.isRoot();
    }

    isTab() {

        return this.route.snapshot.data.tab == true;
    }
    isMenu() {
        return this.route.snapshot.data && this.route.snapshot.data.menu == true;
    }


    showMenu() {

        console.log('menuClicked');

        this.context.app.openDrawer();
    }

    clearReuse(): void {

        const strat: any = this.router.routeReuseStrategy;
        strat.clear();

    }

    autoContext(fields: string[] = null, params: any = null, callback: Function = null){

        if(!fields) fields = this.context.fields;

        if(!params){
            var groupId = this.navParams.get("groupId");
            var courtId = this.navParams.get("courtId");
            var brandId = this.navParams.get("brandId");
            var storeId = this.navParams.get("storeId");

            params = {
                groupId: groupId,
                courtId: courtId,
                brandId: brandId,
                storeId: storeId
            };
        }

        this.contextManager.autoContext(fields, params, ()=>{

            this.themeColorService.setThemeByContext();
            this.init();
        });

    }



    //overwrite this method to be called after autoContext
    init(){

    }


    isDev() {
        return this.configService.get('dev') == true;
    }


    isPrd() {
        return this.configService.get('production') == true;
    }


    listenParams(callback: Function){

        //must skip first one, or will fire immediately!
        //we don't need to unsubscribe because NG router does that automatically
        this.route.queryParams.pipe(skip(1)).subscribe(p => callback(p));
        this.route.params.pipe(skip(1)).subscribe(p => callback(p));

    }

    setLang(storeLang: string) {
        this.t.use(storeLang);
        this.configService.setLocale(storeLang);
        this.t.setDefaultLang(storeLang);
        this.t.currentLang = storeLang;
        this.context.userLang = storeLang;
        window['aigens']?.context && (window['aigens'].context.userLang = storeLang);
    }

    isLoggedIn(){

        var member = this.context.member;

        if(member && !member.guest){
            return true;
        }

        return false;

    }


    getSetting(name: string, defValue = null){
        return this.settingManager.get(name, defValue);
    }

}
