import { Pipe, PipeTransform } from '@angular/core';
import { Order } from '../../data';

@Pipe({
    name: 'combineOrderStatus',
    pure: false
})
export class CombineOrderStatusPipe implements PipeTransform {

    statusList = [
        'pending',
        'paying',
        'paid',
        'scheduled',
        'sending',
        'received',
        'ready',
        'completed',
    ];

    getStatusLevel(status: string): number {
        return this.statusList.indexOf(status);
    }

    getLowestStatus(order: Order): string {
        let minLevel = 999;
        order?.subs?.forEach((sub) => {
            const level = this.getStatusLevel(sub.status);
            if(level > -1 && level < minLevel) {
                minLevel = level;
            }
        });

        if(minLevel >= this.statusList.length || minLevel < 0) {
            return null;
        }

        return this.statusList[minLevel];
    }

    transform(order: Order): string {
        if(order?.subs?.length <= 0) {
            return order?.status ?? '';
        }

        const lowestStatus = this.getLowestStatus(order);

        if(lowestStatus) {
            return lowestStatus;
        }
        return order?.status ?? '';
    }

}
