import { animate, style, transition, trigger } from '@angular/animations';
import { Component, Injector, OnInit } from '@angular/core';
import { BasePage, StorageService } from 'aigens-framework';
import { NativeStorageService } from '../../../../../../native/src/app/native/services/native-storage.service';

@Component({
    selector: 'app-logout-page',
    templateUrl: './logout-page.component.html',
    styleUrls: ['./logout-page.component.scss'],
    animations: [
        trigger('catInOut', [
            transition(':enter', [
                style({ opacity: 0, transform: 'translateX(50%)' }),
                animate('300ms ease-out', style({ opacity: 1, transform: 'translateX(0)' })),
            ]),
            //   transition(':leave', [
            //     animate('300ms ease-in-out', style({ opacity: 0, transform: 'translateX(-50%)' }))
            //   ])
        ]),




    ]
})
export class LogoutPageComponent extends BasePage implements OnInit {

    showContinue = false;
    constructor(private injector: Injector, private storageService: StorageService, private nativeStorageService: NativeStorageService) {

        super(injector);

        this.clearStation();
    }


    ngOnInit(): void {
        super.ngOnInit();
        setTimeout(() => {
            this.toSetupPage();
        }, 100);

        setTimeout(() => {
            this.showContinue = true;
        }, 600);

    }

    toSetupPage() {
        NativeStorageService.fromLogout = true;
        this.routingService.navigate("/device/setup", {
            queryParams: {}
        });
    }

    continueClicked() {
        this.clearStation();
        this.toSetupPage();
    }

    clearStation() {
        console.log("clearStation");

        this.storageService.setLocal("lastStation", null);
        this.nativeStorageService.setStorage("lastStation", null);

        this.sentryService.track("Clear Station", {});
    }
}
