import { TimeService } from './../../pure/services/time.service';
import { Location } from '@angular/common';
import { Component, Injector } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { PageParams } from '../../pure/base/page-params';
import { SettingManager } from '../../pure/managers/setting-manager';
import { PlatformService } from '../../pure/services/platform.service';
import { RoutingService } from '../../pure/services/routing.service';
import { AnalyticsService } from '../services/analytics.service';
import { ConfigService } from '../services/config.service';
import { Context } from '../services/context.service';
import { DataService } from '../services/data.service';
import { DialogService } from '../services/dialog.service';



// DO NOT ADD PROJECT DEPENDENCY TO THIS CLASS
@Component({ template: '' })
export class BaseComponent {

    t: TranslateService;
    router: Router;
    route: ActivatedRoute;

    configService: ConfigService;
    context: Context;

    routingService: RoutingService;
    dataService: DataService;
    routerLoc: Location;
    //browserService: BrowserService;
    my: any;
    dialogService: DialogService;
    analyticsService: AnalyticsService;
    platformService: PlatformService;

    settingManager: SettingManager;
    navParams: PageParams;

    timeService: TimeService;

    isAnimationOff: boolean = false;

    constructor(injector: Injector) {

        this.configService = injector.get(ConfigService);
        this.t = injector.get(TranslateService);
        this.context = injector.get(Context);
        this.router = injector.get(Router);
        this.route = injector.get(ActivatedRoute);
        this.routerLoc = injector.get(Location);

        this.routingService = injector.get(RoutingService);
        this.dataService = injector.get(DataService);
        this.dialogService = injector.get(DialogService);
        this.analyticsService = injector.get(AnalyticsService);
        this.platformService = injector.get(PlatformService);
        //this.browserService = injector.get(BrowserService);

        this.timeService = injector.get(TimeService);

        this.settingManager = injector.get(SettingManager);
        this.my = window['my'];

        this.navParams = new PageParams();
        this.navParams.route = this.route;

        this.isAnimationOff = this.configService.getAnimationOff();
    }



    back(): void {
        this.routerLoc.back();
    }

    setBusy(busy) {

        this.context.app.setBusy(busy);
    }

    getImage(data: any, name: string, width = 300): string{

        let url = null;

        if (data.images && data.images[name]){
            url = data.images[name].url;
        }

        return url;

    }

    hasImage(content: any, imgName: string = 'default'): boolean {
        const imgs =  content.images  || content[0]?.images;

        if(imgs){
            var img = imgs[imgName] || imgs['logo'];
            if(img){
                return true;
            }
        }
        return false;
    }
    /*

    showError(err) {
        this.context.app.showError(err);
    }
    
    showAlert(title: string, message: string, buttons: any = ['OK']) {

        this.context.app.showAlert(title, message, buttons);

    }

   

    showToast(message: string) {
        this.context.app.showToast(message);
    }

    showSnack(message: string, action: string, fn) {
        this.context.app.showSnack(message, action, fn);
    }

    loading(show, message: string = null){
        this.context.app.loading(show, message);
    }

    superloading(show){
        this.context.app.superloading(show);
    }
    */

    isDev(){
        return this.configService.isDev() || (this.configService.isLocalhost() && !this.platformService.isApp());
    }

    isPrd(){
        return this.configService.isPrd();
    }


    getLangValue(item: any, name: string, lang: string = null){

        if (!lang){
            lang = this.context.contentLang;
        }

        let value = null;

        if (lang && lang.indexOf('en') === -1){

            value = item[name + '_' + lang];

        }

        if (!value){
            value = item[name];
        }

        return value;
    }

    getISOLangCode(): string {
        const langMap = {
            'zh': 'zh-Hant',
            'zh-cn': 'zh-Hans'
          };

        const lang = this?.context?.userLang ?? 'en';

        return (lang in langMap) ? langMap[lang] : lang;
    }

    isAdmin() {
        return this.context.admin;
    }

    async openLazyDialog(componentName: string, data: any, options: any = {}): Promise<any> {

    }

    matchElement(data, element, isElementMatchOnly = false){
        const isElementMatch = data === element;
        return isElementMatchOnly ? isElementMatch : (isElementMatch || data === 'old' || data === 'default')
    }

}

