import { CommonService } from './common.service';
import { Injectable } from '@angular/core';
import { SubscribeService } from './subscribe.service';
import { PlatformService } from '@aigens/core/src/app/pure/services/platform.service';

export interface AdFilterOptions {
    checkBrandOnly?: boolean;
    isBrandOnly?: boolean;
    checkBanner?: boolean;
    orderMode?: string;
    checkSignIn?: boolean;
    isSignIn?: boolean;
    storeId?: string;
    checkFreq?: boolean;
}

@Injectable({
    providedIn: 'root'
})
export class AdService {

    private sessionSeenMap: any = {};
    private onceSeenMap: any = {};

    constructor(private context: SubscribeService,
                private platformService: PlatformService) {
        this.onceSeenMap = CommonService.getLocal(`${this.context?.['brandId']}-freq-once-ad`) ?? {};
        this.sessionSeenMap = CommonService.getSessionStorage(`${this.context?.['brandId']}-freq-session-ad`) ?? {};
    }

    /**
     * Method for filtering ads.
     * options can be vary. Please take a look detail code about attributes that we can set
     *
     * @param ads
     * @param triggerName
     * @param options
     */
    public filterAds(ads: any[], triggerName: string, options: AdFilterOptions): any[] {
        let result = ads;

        result = result.filter(ad => {
            if (!ad.openings || ad.openings.always) {
                return true;
            }

            const now = new Date();
            const currentTime = (('0' + now.getHours().toString()).slice(-2) + ':' + now.getMinutes().toString());

            const start = ad.openings?.weekdays[now.getDay()].startTime;
            const end = ad.openings?.weekdays[now.getDay()].endTime;
            const status = ad.openings?.weekdays[now.getDay()].type;

            return currentTime >= start && currentTime <= end;
        });

        if (options?.checkFreq) {
            result = ads.filter((ad) => {
                return this.checkAdFreq(ad);
            });
        }

        if (options?.checkBrandOnly) {
            if (options?.isBrandOnly) {
                result = result.filter(ad => ad.brandOnly);
            } else {
                result = result.filter(ad => !ad.brandOnly);
            }
        }

        result = result.filter(ad => {

            if (options?.checkBanner && !ad.images?.banner) {
                return false;
            }

            const trigger = ad.trigger || [];
            return trigger.includes(triggerName);
        });


        // if (options?.orderMode) { // commented, to make sure always filter by order mode
        result = result.filter(ad => {
            if (ad.modes && ad.modes.length > 0) {
                return ad.modes.includes(options?.orderMode);
            }

            return true;
        });
        // }

        // if (options?.checkSignIn) { // commented, to make sure always filter by users
        result = result.filter(ad => {
            if (!ad.users || ad.users.length <= 0) {
                return true;
            }

            if (options?.isSignIn == null) {
                return false;
            }

            if (options?.isSignIn && ad.users.indexOf('member') >= 0) {
                return true;
            }
            if (!options?.isSignIn && ad.users.indexOf('guest') >= 0) {
                return true;
            }

            return false;
        });
        // }


        // if (options?.storeId) { // commented, to make sure always filter by store id
        result = result.filter(ad => {
            const stores = (ad.storeIds || []).map(o => String(o));
            if (stores.length == 0) {
                return true;
            }

            return stores.includes(options?.storeId);
        });
        // }

        // filter ads by devices
        result = result.filter(ad => {
            if (!ad.devices || ad.devices.length == 0) {
                return true;
            }

            return ad.devices.includes(this.platformService.platform);
        });

        result = this.sortAds(result);

        return result;
    }

    private checkAdFreq(ad: any): boolean {
        if (!ad?.frequency || ad?.frequency === 'always') {
            return true;
        }

        if (ad.frequency === 'session') {
            return !(ad.id in this.sessionSeenMap && this.sessionSeenMap[ad.id]);
        }

        return !(ad.id in this.onceSeenMap && this.onceSeenMap[ad.id]);
    }

    public markAdsAsSeen(ads: any[]): void {
        ads.forEach((ad) => {
            this.makeAdAsSeen(ad);
        });

        CommonService.setLocal(`${this.context?.['brandId']}-freq-once-ad`, this.onceSeenMap);
        CommonService.setSessionStorage(`${this.context?.['brandId']}-freq-session-ad`, this.sessionSeenMap);
    }

    private makeAdAsSeen(ad: any): void {
        if (!ad?.frequency || ad?.frequency === 'always') {
            return;
        }

        if (ad.frequency === 'session') {
            this.sessionSeenMap[ad.id] = true;
            console.log(this.sessionSeenMap);
        }

        if (ad.frequency === 'once') {
            this.onceSeenMap[ad.id] = true;
            console.log(this.onceSeenMap);
        }
    }

    public sortAds(ads: any[]): any[] {
        let result = ads;

        result = result.sort((a, b) => {
            if (a['featured']) {
                return -1;
            }
            if (b['featured']) {
                return 1;
            }

            return a?.start > b?.start ? -1 : 1;
        });


        result = result.sort((a, b) => {
            const aVal = a.priority ?? 0;
            const bVal = b.priority ?? 0;

            return bVal - aVal;
        });

        return result;
    }
}
