    images: any;
    categories: Category[];
    location: Location;
    brand: Brand;
    brandId: number;
    channels: string[] = [];
    features: string[] = [];
    cateringPickupModes: string[] = [];
    handle: string;
    colors: {};
    currency: string;
    currencySign: string;
    timezone: string;
    country: string;
    menuId: string;
    posId: number;
    groupId: number;
    languages: string[];
    language: string;
    pos: Pos;
