    createOrder(store: Store, mode: string, options: {} = {}, member: Member = undefined) {

        var om = new SingleOrderManager(this.configService, this.analyticsService, this.subscribe);

        this.managers = [om];
        this.manager = om;

        this.multi = false;

        this.fillDefaultOptions(options);

        om.createOrder(store, mode, options);
        om.setUserPoints(member)

        console.log("created options??", options);

        this.order = om.order;
        this.store = store;
        this.mode = mode;

        return this.order;
    }

    private fillDefaultOptions(options: {}) {
        var source = this.platformService.getSource();
        var container = this.platformService.getContainer();

        if (source) {
            options['source'] = source;
        }

        if (container) {
            options['container'] = container;
        }
    }

    createSwitchCourtOrder(court: Court, store: Store, mode: string, options: {} = {}) {

        if (this.court && this.court.id != court.id) {
            console.log("ERROR: CANNOT SUPPORT CHANGE COURT");
            return;
        } else {
            console.log("creating court sub order!");
        }

        this.court = court;
        this.store = store;
        this.multi = true;
        this.mode = mode;

        this.fillDefaultOptions(options);

        var om = this.findOm(store.id);

        if (!this.order) {
            this.order = this.newCourtOrder(court, mode, options);
            if (store) {
                this.order.storeId = store.id; // need use after from court mode to byod
            }
        } else {
            this.order.type = mode;
        }

        if (!om) {
            om = new SingleOrderManager(this.configService, this.analyticsService, this.subscribe);
            om.createOrder(store, mode, options);
            this.managers.push(om);


        } else {
            om.store = store;
        }

        // update the mode/type
        this.managers.forEach(m => {
            m.order.type = mode;
            m.mode = mode;
        });

        this.manager = om;

        this.calculateCourtOrder();

    }

    //calculate court order
    private calculateCourtOrder() {

        var total = 0;
        var grandTotal = 0;
        var serviceCharge = 0;
        var tax = 0;
        var discount = 0;

        var subs = [];

        for (let om of this.managers) {

            let so = om.order;

            total += so.total;
            grandTotal += so.grandTotal;
            serviceCharge += so.serviceCharge;
            tax += so.tax;
            discount += so.discount;

            subs.push(so);
        }

        this.order.total = MathUtils.round(total, 2);
        this.order.grandTotal = MathUtils.round(grandTotal, 2);
        this.order.serviceCharge = MathUtils.round(serviceCharge, 2);
        this.order.tax = MathUtils.round(tax, 2);
        this.order.discount = MathUtils.round(discount, 2);
        this.order.subs = subs;

        console.log("calculated court order", this.order);
    }


