    isEmpty() {

        if (!this.multi) {
            return this.manager?.isEmpty() ?? true;
        }

        if (!this.order) {
            return true;
        }

        if (this.multi) {
            let emptyMulti = true;
            this.order.subs?.forEach(s => {
                if (s.orderitems.length > 0) {
                    emptyMulti = false;
                }
            });
            return emptyMulti;
        }
        return this.order.total == 0;
    }

