    isAllowCheckoutZeroTotal(total: number) {
        let allowed = false;
        let allowZeroTotalType;
        if (this.court) {
            allowZeroTotalType = this.court.allowZeroTotalType;
        } else {
            allowZeroTotalType = this.store?.menu?.allowZeroTotalType;
        }

        // if we have mode in allow zero total type, then should be allowed $0 order
        if (allowZeroTotalType?.length > 0) {
            if (allowZeroTotalType.includes('all') || allowZeroTotalType.includes(this.mode)) {
                allowed = true;
            }
        }

        const catType = [];

        // if we have all order items that all within points category, then should be allowed $0 order
        if (this.order) {
            this.order.orderitems?.forEach(o => {
                if (o.categoryType) {
                    catType.push(o.categoryType);
                }
            });

            this.order.subs?.forEach(sub => {
                sub.orderitems?.forEach(o => {
                    if (o.categoryType) {
                        catType.push(o.categoryType);
                    }
                });
            });

            const uniqCatType = catType.filter((n, i) => catType.indexOf(n) === i);

            if (uniqCatType.includes('points') && uniqCatType.length == 1) {
                allowed = true;
            }
        }

        return !this.isEmpty() && allowed && total == 0;
    }
    // offer (orderItem)
    hasOfferAlone(): boolean {
        const offers = [...this.selectedOffers, ...this.bundledOffer];
        return offers.some(o => o?.reward?.['extra']?.alone);
    }

    setMemberPoints(member: Member) {
        this.manager?.setUserPoints(member);
    }
    isAllItemPoints(): boolean {
        let ois = [];
        this.order.courtId ? this.order.subs?.forEach(s => { ois.push(s.orderitems); })
            : ois = this.order?.orderitems;

        if (ois.length === 0) {
            return false;
        } else {
           return !ois.some((orderItem) => orderItem.categoryType !== 'points');
        }
    }

