import { NavigationMenuItem, NavigationMenuService } from '@aigens/core/src/app/pure/services/navigation-menu.service';
import { Component, Input, OnInit, Output, EventEmitter, Injector } from '@angular/core';
import { Router } from '@angular/router';

export interface Tab {
    i18n: string;
    route: any;
    icon: string;
    activeIcon?: string;
    matchName: string;
    url?: string;
    iconSrc?: string;
    external?: string;
}
@Component({
    selector: 'app-tab-bar',
    templateUrl: './tab-bar.component.html',
    styleUrls: ['./tab-bar.component.scss'],
})
export class TabBarComponent implements OnInit {
    @Input() tabs: NavigationMenuItem[] = []; // tab contents
    @Input() activeColor: any = 'var(--P)'; // the color of tab when active
    @Input() defaultColor: any = '#c4c4c4';
    @Input() backgroundColor: string = '#FFFFFF';
    @Input() enableClickAnimation: boolean = false;
    @Output() tabSelected = new EventEmitter<NavigationMenuItem>();

    protected router: Router;
    protected navigationMenuService: NavigationMenuService;

    constructor(protected injector: Injector) {
        this.router = injector.get(Router);
        this.navigationMenuService = injector.get(NavigationMenuService);

    }

    ngOnInit(): void {
    }

    isTabActive(tab: NavigationMenuItem): boolean {
        const url = this.router.url;
        return this.navigationMenuService.hasRouteMatch(tab, url);
    }

    tabClicked(tab: NavigationMenuItem) {
        this.tabSelected.emit(tab)
    }

    getIconName(tab: NavigationMenuItem): string {
        if(!this.isTabActive(tab)) {
            return tab.icon?.name;
        }

        if(tab.activeIcon?.name) {
            return tab.activeIcon?.name;
        }

        return tab.icon?.name;
    }
}
