@Injectable({
    providedIn: 'root'
})
export class TabbarService {

    selectedTabs = ['home', 'membership', 'ordering', 'stores', 'reorder'];

    selectedMenu = ['home', 'inbox', 'news', 'ordering', 'rewards', 'stamps', 'membership', 'reorder', 'settings'];

    constructor(injector: Injector, private context: SubscribeService) {
    }

    getTabsByArray(tabs: any[]): any[] {
        return this.patchTabs(this.selectedTabs.map(tab => tabs[tab]));
    }

    getTabs(): any[] {
        return this.patchTabs(this.selectedTabs.map(tab => tabs[tab]));
    }

    getMenus(): any[] {
        return this.patchTabs(this.selectedMenu.map(tab => tabs[tab]));
    }

    isRoot(path: string): boolean {
        return this.selectedTabs.some((tab) => {
            return path.indexOf(tabs[tab]?.matchName) >= 0;
        });
    }

    patchTabs(tabs): any[] {
        for (let t of tabs) {
            let link = t.route[0] as string;
            if (t.external !== undefined) {
                link = link.replace('@externalLink', this.context?.['brand']['links'][t.external]['url'])
            }
            link = link.replace('@brandId', '' + this.context?.['brandId']);
            t.route[0] = link;
        }
        return tabs;
    }

}
