import { Component, EventEmitter, Injector, OnInit, Output } from '@angular/core';
import { CommonService } from '../../../../../../core/src/app/pure/services/common.service';
import { SubscribeService } from '@aigens/core/src/app/pure/services/subscribe.service';

@Component({
    selector: 'app-language-select',
    templateUrl: './language-select.component.html',
    styleUrls: ['./language-select.component.scss']
})
export class LanguageSelectComponent implements OnInit {

    showDropdown: boolean = false;
    context: any;

    @Output() changed: EventEmitter<any> = new EventEmitter<any>();

    constructor(private injector: Injector, private subscribeService: SubscribeService) {

        this.context = window['aigens'].context;


        if (!this.context.userLang) {
            this.languageClicked(this.context.language);
        }

    }

    get languages() {
        // sometimes languages in brand lever is different from store level
        return this.context?.languages || this.context?.store?.languages || [];
    }

    ngOnInit(): void {
        setTimeout(() => {
            this.context.userLang = this.getLocale();
        }, 500);

    }

    selectClicked() {

        this.showDropdown = !this.showDropdown;

    }

    dismissClicked() {
        this.showDropdown = false;
    }

    languageClicked(lang) {

        this.showDropdown = false;

        /*
        var oldLang = this.t.currentLang;
        if(oldLang == lang){
            console.log("same lang");
            return;
        }
        */

        this.context.userLang = lang;

        this.subscribeService.next("language", lang)
        this.subscribeService.next("userLang", lang)

        if (this.changed) {
            this.changed.emit(lang);
        }

    }


    getLocale(){
        return CommonService.getLocal("locale");
    }

    setLocale(lang: string){
        return CommonService.setLocal("locale", lang);
    }


}
