import { TranslateService } from '@ngx-translate/core';
import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'formTranslate'
})
export class FormTranslatePipe implements PipeTransform {
    constructor(public translation: TranslateService) {
    }

    transform(content, lang: string, field: string = "name", formName: string = 'register'): string {
        const formKey = `forms.${formName}.${content['title']}.${field}`;
        return `${this.translation.instant(formKey) === formKey ? this.serverTranslate(content, lang, field) : this.translation.instant(formKey)}`
    }

    serverTranslate(content, lang: string, field: string = "name") {

        if (!content) return "";

        //return value.name;
        return this.lookup(content, lang, field);
    }

    private lookup(content, lang: string, field: string) {

        var result = null;

        if (lang) {
            result = content[field + "_" + lang];
        }

        if (!result) {
            result = content[field];
        }

        return result;
    }
}
