import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'numberDisplay'
})
export class NumberDisplayPipe implements PipeTransform {
    constructor() {

    }

    //  3 1,234,560
    transform(input: number | string): string {
        console.log('numberDisplay', input);
        // 
        const flag = (input || input === 0) ? true : false;
        const arr = typeof input === 'number' ? Math.abs(input)?.toString()?.split('.') : input?.split('.');
        let left = arr[0];
        const right = arr[1] ? arr[1] : 0;
        let tmp = '';
        while (left.length > 3) {
            tmp = ',' + left.slice(-3) + tmp;
            left = left.slice(0, left.length - 3);
        }
        if (right === 0){
            return flag ? left + tmp : '-' + left + tmp;
        }
        return flag ? left + tmp + '.' + right : '-' + left + tmp + '.' + right;
    }

}
