import { Directive, ElementRef, AfterViewInit, Injector, Inject, Injectable } from '@angular/core';
// import { RoutingService } from '../../../../../core/src/app/pure/services/routing.service';

@Directive({
    selector: '[appSelectByKeyboard]'
})
export class SelectByKeyboardDirective implements AfterViewInit {

    private observer: MutationObserver;
    currentDivElement;
    constructor(@Inject(Injector) private readonly injector: Injector, private el: ElementRef, private selectByKeyboardService: SelectByKeyboardService) {
    }

    // get routingService() {
    //     return this.injector.get(RoutingService);
    // }

    ngAfterViewInit() {

        this.currentDivElement = this.el.nativeElement;
        this.handle(this.currentDivElement);

        this.observer = new MutationObserver((mutationsList, observer) => {
            for (let mutation of mutationsList) {
                if (mutation.type === 'childList') {
                    this.handle(this.el.nativeElement);
                }
            }
        });
        this.observer.observe(this.currentDivElement, { childList: true });


    }

    isNone(ele) {
        if (!ele) return true;
        const isValid = ele.offsetWidth > 0 && ele.offsetHeight > 0
        return window.getComputedStyle(ele).display === 'none' || !isValid;
    }

    handle(divElement) {

        if (!divElement['_id_']) {
            if (!this.isNone(divElement)) {
                divElement['_id_'] = new Date().getTime();
                this.selectByKeyboardService.addData(divElement);
            }
        } else {
            if (this.isNone(divElement)) {
                this.selectByKeyboardService.removeData(divElement);
            }
        }


    }

    ngOnDestroy() {
        this.selectByKeyboardService.removeData(this.currentDivElement);
        this.observer?.disconnect && this.observer?.disconnect();
    }
}


@Injectable({
    providedIn: 'root'
})
export class SelectByKeyboardService {

    private datas = [];
    private static _currentAlert;
    className = "active_aigens";
    constructor(@Inject(Injector) private readonly injector: Injector) {

        this.listenKeyboard();

    }

    insertStyle(element) {
        var styleContent = `.${this.className} { background: ${this.getActiveBgColor(element)} !important; }`;
        const id = "device-style-active-key-id";
        var style = document.getElementById(id);
        if (style) {
            style.innerText = styleContent;
            return;
        }
        style = document.createElement("style");
        style.innerText = styleContent;
        style.setAttribute('id', id);
        // style.appendChild(document.createTextNode(styleContent));
        document.head.appendChild(style);
    }

    static setCurrentAlert(alert) {
        SelectByKeyboardService._currentAlert = alert;
        alert?.afterClosed()?.subscribe((respone: any) => {
            this.clearCurrentAlert();

        });
    }
    static clearCurrentAlert() {
        SelectByKeyboardService._currentAlert = null;
    }

    // get routingService() {
    //     return this.injector.get(RoutingService);
    // }
    listenKeyboard() {
        window.addEventListener('keydown', event => {
            const key: any = event?.key;
            const enabledKeyboardAction = window?.['aigensDeviceNormal']?.keyboardAction?.enabledKeyboardAction;
            if (enabledKeyboardAction) this.handleKeycode(key);
        })
    }

    isNone(ele) {
        if (!ele) return true;
        const isValid = ele.offsetWidth > 0 && ele.offsetHeight > 0
        return window.getComputedStyle(ele).display === 'none' || !isValid;
    }

    processStatus(targetIndex, status: 'down' | 'up') {
        // this.clearInvaild();
        if (!this.datas || !this.datas.length) return;
        let target = this.datas?.[targetIndex];
        if (target?.classList?.contains(this.className)) return;
        target?.classList?.add(this.className);
        this.insertStyle(target);
    }

    handleKeycode(key: 'ArrowUp' | 'ArrowDown' | 'ArrowLeft' | 'ArrowRight' | 'Enter') {
        this.clearInvaild();
        const datas = this.datas || [];
        if (!datas.length) return;
        let targetIndex = 0;
        switch (key) {
            case 'ArrowUp':
            case 'ArrowLeft':
                for (let i = 0; i < datas.length; i++) {
                    const data = datas[i];
                    if (data?.classList?.contains(this.className)) {
                        targetIndex = (i - 1 < 0) ? 0 : i - 1;
                    }
                    data?.classList?.remove(this.className);
                }
                this.processStatus(targetIndex, 'up');
                break;
            case 'ArrowDown':
            case 'ArrowRight':
                for (let i = 0; i < datas.length; i++) {
                    const data = datas[i];
                    if (data?.classList?.contains(this.className)) {
                        targetIndex = (i + 1 === datas.length) ? 0 : i + 1;
                    }
                    data?.classList?.remove(this.className);
                }
                this.processStatus(targetIndex, 'down');
                break;
            case 'Enter':
                // if (this.routingService.isAux()) {
                //     this.routingService.navigateCloseAllModal();
                //     return;
                // }

                this.datas.forEach(d => {
                    if (this.isInputFocused(d)) {
                        d.blur();
                    }
                })


                if (SelectByKeyboardService._currentAlert) {
                    SelectByKeyboardService._currentAlert?.close?.();
                    return;
                }

                SelectByKeyboardService.clearCurrentAlert();

                const active = this.datas?.find(d => d?.classList?.contains(this.className));

                active?.click?.();
                break;
        }
        this.clearInvaild();
    }



    isInputFocused(ele) {
        return !ele?.classList?.contains(this.className) && ele?.tagName?.toLowerCase().includes('input') && ele === document.activeElement;
    }

    addData(data) {
        if (!this.datas) this.datas = [];
        this.datas.push(data);
    }
    getAllDatas() {
        return this.datas;
    }

    removeData(data) {
        this.datas = this.datas.filter(item => item['_id_'] !== data['_id_']);
    }

    clearInvaild() {
        for (var i = this.datas.length - 1; i >= 0; i--) {
            if (this.isNone(this.datas[i])) {
                this.datas.splice(i, 1);
            }
        }
    }

    clear() {
        this.datas = [];
    }

    getActiveBgColor(ele) {

        const configBg = window?.['aigensDeviceNormal']?.keyboardAction?.['activeBg'];
        if (configBg) return configBg;
        if (!ele) return "";
        const originBg = window.getComputedStyle(ele).backgroundColor;
        const a = window?.['aigensDeviceNormal']?.keyboardAction?.['activeBgTransparency'] || 0.3;
        if (originBg.startsWith('rgb(')) {
            return originBg.replace("rgb(", "rgba(").replace(')', `,${a})`)
        }

        let bigint = parseInt(originBg.replace('#', ''), 16);
        let r = (bigint >> 16) & 255;
        let g = (bigint >> 8) & 255;
        let b = bigint & 255;
        return `rgba(${r},${g},${b},${a})`
    }

}
