    processActionsInFirstRoute = true;

    constructor(private route: ActivatedRoute, private router: Router, private subscribeService: SubscribeService, private platformService: PlatformService, private lazyService: LazyService) {
    }


    popOrderActions() {
        var result = this.orderActions;
        this.orderActions = [];
        return result;
    }

    processUrl(removeParams: boolean = false) {
        var params = this.route.snapshot.queryParams;

        var keepParams = {};

        var count = 0;

        for (const field in params) {
            const value = params[field];

            switch (field) {
                case 'addItemId':
                    this.addItemIds(value);
                    count++;
                    break;
                case 'addDiscountCode':
                    this.addDiscountCodes(value);
                    count++;
                    break;
                case 'addDiscount':
                    this.addDiscount(value);
                    count++;
                    break;
                case 'addOfferId':
                    this.addOfferId(value);
                    count++;
                    break;
                case 'sessionId':
                case 'sid':
                    this.addSid(value);
                    count++;
                    break;
                case 'addCouponCode':
                    this.addPendingCouponCode(value, true, 'any');
                    count++;
                    break;
                /*
                Uncomment this when miniapp is stable
                case "random":

                    count++;
                    break;
                */
                default:
                    keepParams[field] = value;
                    break;
            }
        }

        if (removeParams && count > 0) {
            this.removeParams(keepParams);
        }

        console.log('pending order actions', this.orderActions);
    }

    private removeParams(keepParams) {
        console.log('keeping params', keepParams);

        this.router.navigate([], {
            relativeTo: this.route,
            queryParams: keepParams,
            replaceUrl: true,
        });
    }

