    private addItemIds(itemIds: string) {
        console.log('addItemIds', itemIds);

        var splits = itemIds.split(',');

        for (let itemId of splits) {
            this.addPendingItem(itemId, false);
        }
    }

    addPendingItem(itemId, autoExecute: boolean, isRecommend: boolean = false, target: string = this.defaultTarget) {
        var action = {
            action: 'addItem',
            params: {
                itemId: itemId,
                isRecommend,
            },
            auto: autoExecute,
            target
        };

        // this.orderActions.push(action);
        this.pushToOrderActions(action);
    }

