import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Component, Injector } from '@angular/core';
import { Data } from 'aigens-ng-core';
import { Observable } from 'rxjs';
import { map, timeout, tap } from 'rxjs/operators';
import { ApiBridgeService } from '../services/api-bridge.service';
import { ConfigService } from '../services/config.service';
import { BasicAuth } from './basic-auth';

@Component({ template: '' })
export class AQuery {

    host: string;
    http: HttpClient;
    method: string;
    params: {};
    url: string;
    headers: {};
    configs: ConfigService;
    body: {};
    shouldAuth: boolean = false;
    authorization: BasicAuth;
    data: ArrayBuffer;
    file: Blob;
    apiBridge: ApiBridgeService;
    timeoutTime: number;

    constructor(injector: Injector) {

        this.http = injector.get(HttpClient);
        this.configs = injector.get(ConfigService);
        this.apiBridge = injector.get(ApiBridgeService);

        this.clear();
    }


    private clear() {
        this.method = "get";
        this.params = {};
        this.url = null;
        this.body = null;
        this.headers = {};
        this.shouldAuth = false;
        this.authorization = null;
        this.data = null;
        this.file = null;
        this.timeoutTime = 55000;
    }

    private getDefaultHeaders(inputs: {}): HttpHeaders {

        let headers = new HttpHeaders();

        for (var key in inputs) {
            var value = inputs[key];

            headers = headers.set(key, value + "");
        }

        if (this.authorization) {
            headers = headers.set('AUTHORIZATION', 'Basic ' + this.authorization.getAuth());
        } else {
            if (this.shouldAuth) {
                //allow header have sid already
                var sid = inputs['sid'] || this.configs.getSessionId();

                if (sid) {
                    headers = headers.set('sid', sid);
                }
            }
        }

        return headers;
    }

    auth(auth: boolean): AQuery {
        this.shouldAuth = auth;
        return this;
    }

    basicAuth(auth: BasicAuth): AQuery {
        this.authorization = auth;
        return this;
    }


    setTimeoutTime(time: number): AQuery {
        this.timeoutTime = time;
        return this;
    }

    protected getParams(inputs: {}): HttpParams {

        Object.keys(inputs).forEach((key) => (inputs[key] == null) && delete inputs[key]);


        let params: HttpParams = new HttpParams({
            fromObject: inputs
        });

        var lang = this.configs.getLocale();

        if (lang && !inputs["locale"] && this.url.indexOf("locale") == -1) {
            params = params.set('locale', lang);
        }

        return params;
    }

    public getJson(): Observable<Object> {

        var result = this.doHttp();
        this.clear();
        return result;
    }

    private getHost() {
        if (this.host) return this.host;
        return this.configs.getApi();
    }



    private doHttp(): Observable<Object> {



        var url = this.url;

        if (url.indexOf("/") == 0) {
            url = this.getHost() + url;
        }

        if (this.apiBridge) {
            url = this.apiBridge.translateUrl(url, this);
        }

        var params = this.params;
        var headers = this.headers;
        var method = this.method;

        var searchParams = this.getParams(params);
        var httpHeaders = this.getDefaultHeaders(headers);

        if ("get" == method) {
            return this.http.get(url, { params: searchParams, headers: httpHeaders, observe: 'response' }).pipe(this.timeoutError()).pipe(map(r => this.processJson(r)));
        } else if ("post" == method || "put" == method || "delete" == method) {

            var body = this.body;
            // File upload
            if (this.file) {
                // httpHeaders = httpHeaders.set('Content-Type', 'multipart/form-data');

                const formData = new FormData();

                formData.set('data', this.file);

                //body = JSON.stringify(this.body);

                if ("post" == method) {
                    return this.http.post(url, formData, {
                        params: searchParams, headers: httpHeaders, reportProgress: true, observe: 'events'
                    });
                } else if ("put" == method) {
                    return this.http.put(url, formData, { params: searchParams, headers: httpHeaders, reportProgress: true, observe: 'events' });
                }
            }
            else if (this.body) {

                httpHeaders = httpHeaders.set("Content-Type", "application/json");

                body = JSON.stringify(this.body, Data.JsonReplacer);

                //body = JSON.stringify(this.body);

                if ("post" == method) {
                    return this.http.post(url, body, { params: searchParams, headers: httpHeaders, observe: 'response' }).pipe(this.timeoutError()).pipe(map(r => this.processJson(r)));
                } else if ("put" == method) {
                    return this.http.put(url, body, { params: searchParams, headers: httpHeaders, observe: 'response' }).pipe(this.timeoutError()).pipe(map(r => this.processJson(r)));
                } else if ("delete" == method) {
                    return this.http.delete(url, { params: searchParams, headers: httpHeaders, observe: 'response' }).pipe(this.timeoutError()).pipe(map(r => this.processJson(r)));
                }
            } else if (this.data) {
                console.log("raw bytes not supported, fix me");
                /*
                if ('post' == method) {
                    return this.http.post(url, this.data).pipe(map(r => this.processJson(r)));
                }*/
            } else {

                httpHeaders = httpHeaders.set('Content-Type', 'application/x-www-form-urlencoded');


                var str = searchParams.toString();
                str = str.replace(/\+/g, "%2b");

                //console.log("urlencoded body", str);


                if ("post" == method) {
                    return this.http.post(url, str, { headers: httpHeaders, observe: 'response' }).pipe(this.timeoutError()).pipe(map(r => this.processJson(r)));
                } else if ("put" == method) {
                    return this.http.put(url, str, { headers: httpHeaders, observe: 'response' }).pipe(this.timeoutError()).pipe(map(r => this.processJson(r)));
                } else if ("delete" == method) {
                    return this.http.delete(url, { params: searchParams, headers: httpHeaders, observe: 'response' }).pipe(this.timeoutError()).pipe(map(r => this.processJson(r)));
                }


            }


        }/* else if ("put" == method) {

            var body = this.body;
            if (this.body) {
                httpHeaders.set("Content-Type", "application/json"),
                    body = JSON.stringify(this.body);

                return this.http.put(url, body, { search: searchParams, headers: httpHeaders }).map(r => r.json());
            } else {
                httpHeaders.set('Content-Type', 'application/x-www-form-urlencoded');
                body = searchParams.toString();
                return this.http.put(url, body, { headers: httpHeaders }).map(r => r.json());
            }

        }*/

        return null;

    }

    processJson(response) {



        var dateStr = response.headers.get('Date');

        var json = response.body;


        if (dateStr && json) {
            var date = new Date(dateStr);

            json['time'] = date.getTime();
        }


        return json;
    }

    timeoutError() {
        if(this.timeoutTime === 0){
            return tap({complete: () => console.log('no timeout')})
        }
        return timeout({
            each: this.timeoutTime,
            with: () => { throw new Error('timeout error') }
        })
    }


}
