import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { ApplicationRef, Injectable, Injector, isDevMode } from '@angular/core';


//WARNING: DO NOT MODIFY THIS CLASS
//WARNING: DO NOT ADD DEPENDENCY

@Injectable({
    providedIn: 'root'
})
export class PlatformService {


    platform: string = "mobile";
    platformClasses: string = "mobile";
    platformLimited: boolean = false;
    private container: string; // raw value of the container
    menuChannel: string = 'mobile';
    source: string;
    webp: boolean = false;
    network: string;
    referral: string;
    qrId: string;
    alipayAppId: string;
    onpremise: boolean = false;

    breakpointObserver: BreakpointObserver;
    appRef: ApplicationRef;

    constructor(injector: Injector) {

        this.breakpointObserver = injector.get(BreakpointObserver);
        this.appRef = injector.get(ApplicationRef);

        //special handling to remember network = cn
        if (this.haveQuery("network=cn") || window.location.host.indexOf(".cn") != -1) {
            this.network = "cn";
            var aigens = window['aigens'];
            if (aigens) {
                aigens.network = "cn";
            }

        }

        //recognize onpremise flag
        if (this.haveQuery("onpremise=true")) {
            this.setReferral("scan");
            this.onpremise = true;
        }

        this.qrId = this.getQueryParam("qrId");

        this.alipayAppId = this.getQueryParam("alipayAppId");

        var channel = this.getQueryParam("channel");
        if (channel && !this.isSDK()) {
            this.menuChannel = channel;
            this.source = channel;
        }

        var container = this.getQueryParam("container");
        if (container) {
            this.container = container;
        }

        console.log("PlatformService construct", this.network, this.referral, this.qrId);

        this.checkWebp();

        this.breakpointObserver.observe([

            Breakpoints.HandsetPortrait,
            Breakpoints.HandsetLandscape,
            Breakpoints.TabletPortrait,
            Breakpoints.TabletLandscape,
            Breakpoints.Web
        ]).subscribe(result => {
            if (result.matches) {

                //console.log("BREAKPOINTS", result);

                this.updateLayout();
            }
        });

        this.updateLayout();

    }

    setReferral(referral) {
        this.referral = referral;
        var aigens = window['aigens'];
        if (aigens) {
            aigens.referral = referral;
        }
    }

    isMobile() {
        var width = window.innerWidth;
        return width < 600;
    }

    isTablet() {
        var width = window.innerWidth;
        return width >= 600 && width < 840;
    }

    isSmallTabletHeight() {
        var height = window.innerHeight;
        return height >= 500 && height < 875;
    }

    isDesktop() {
        var width = window.innerWidth;
        return width >= 840;
    }

    isKiosk(): boolean {
        return this.platform == 'kiosk'
    }


    isTestBuild() {

        return isDevMode();
    }

    isNative() {
        return false;
    }

    isSDK() {

        return window['isSDK'] == true;
    }

    static isSDK(){
        return window['isSDK'] == true;
    }

    getNativePlatform() {
        return "web";
    }

    getAppInfo(): Promise<any> {
        return Promise.resolve({});
    }

    getAppInfoInstant() {
        return null;
    }

    getNativeAppId() {
        return null;
    }

    getVersion() {
        var aigens = window['aigens'];
        if (aigens?.version) {
            return aigens.version;
        }
        return "unknown";
    }

    isEmbedded() {

        //return true;

        //check if current web app is embedded by other containers
        return this.isAliApp();

    }

    private haveQuery(keyValue) {
        var search = window.location.search;
        if (search && search.indexOf(keyValue) >= 0) {
            return true;
        }
        return false;
    }

    getQueryParam(name) {

        var p = null;
        try {
            const urlParams = new URLSearchParams(window.location.search);
            p = urlParams.get(name);

        } catch (e) {
        }
        return p;
    }

    isOnPremise() {

        if (!this.referral) {
            return false;
        }

        return "scan" == this.referral;

    }

    getMenuChannel() {

        if (this.isAliApp()) return "miniapp";

        if (this.isSDK()) return 'app';

        if (this.menuChannel) return this.menuChannel;

        return "mobile";

    }
    //channel is null for non specific, it's not same as source
    getChannel() {
        if (this.isAliApp() || this.getSource() == 'miniapp') {
            return "miniapp";
        }else if (this.isSDK()) {
            return 'app';
        }
        return this.menuChannel || null;
    }

    getSource() {

        if (this.isSDK()) return 'app';

        if (this.source) return this.source;

        if (this.isAliApp()) {
            return "miniapp";
        }

        return "mobile";
    }

    getReferral() {

        if (this.referral) return this.referral;
        return null;

    }

    getQrId() {

        if (this.qrId) return this.qrId;
        return null;

    }

    setContainer(container) {
        this.container = container;
        localStorage.setItem("miniContainer", container);
    }

    //TODO: fix me not hardcode hope
    getContainer() {

        if (this.container) return this.container;

        if (this.isAliApp()) {
            let localStorageContainer = localStorage.getItem("miniContainer");
            if (localStorageContainer) return localStorageContainer;
            if (this.getQueryParam("container")) {
                this.setContainer(this.getQueryParam("container"));
                return this.getQueryParam("container"); // assume miniapp DSL already added &network=cn&channel=miniapp&container= in URL

            }
        }

        return null;
    }

    getAlipayAppId() {

        if (this.alipayAppId) return this.alipayAppId;


        if (this.getQueryParam("alipayAppId")) {
            return this.getQueryParam("alipayAppId");

        }


        return null;
    }

    isAliApp() {
        var ua = navigator.userAgent.toLowerCase();
        var terms = ["miniprogram", "alipayide"];

        for (let t of terms) {
            if (ua.indexOf(t) >= 0) {
                return true;
            }
        }

        return false;

    }

    redrawUI() {
        this.appRef.tick();
    }

    enums: string[] = ["mobile", "tablet", "desktop"];

    updateLayout() {

        var pt = "mobile";

        if (this.isDesktop()) {
            pt = "desktop";
        } else if (this.isTablet()) {
            pt = "tablet";
        }

        //if the platform is alipayApp
        if (this.isAliApp()) {
            pt = 'mobile'
        }


        if (pt != this.platform) {

            this.setPlatform(pt);

        }


    }

    setPlatform(pt: string) {

        this.platform = pt;
        this.platformClasses = pt;

        var body = document.body;

        var aigens = window['aigens'];
        if (aigens) aigens.platform = this.platform;

        for (let p of this.enums) {
            body.classList.remove("plt-" + p);
        }

        body.classList.add("plt-" + this.platform);

        console.log("platform", this.platform);
    }


    checkLimited(platforms: string[]) {

        if (platforms.length > 0) {
            let limited = true;

            if (platforms.indexOf('mobile') >= 0 && this.isMobile()) {
                limited = false;
            }

            if (platforms.indexOf('tablet') >= 0 && this.isTablet()) {
                limited = false;
            }

            this.platformLimited = limited;
        }

    }

    isApp(): boolean {
        return this.menuChannel === 'app';
    }

    private setSupport(support: boolean) {
        var aigens = window['aigens'];
        if (aigens) {
            aigens.webp = support;
        }
        this.webp = support;
    }

    private checkWebp(feature = 'lossy') {

        const webpSupport = localStorage.getItem('webpSupport');
        if (webpSupport != null) {


            this.setSupport(webpSupport == 'true');

            return;
        }


        var kTestImages = {
            lossy: 'UklGRiIAAABXRUJQVlA4IBYAAAAwAQCdASoBAAEADsD+JaQAA3AAAAAA',
            lossless: 'UklGRhoAAABXRUJQVlA4TA0AAAAvAAAAEAcQERGIiP4HAA==',
            alpha: 'UklGRkoAAABXRUJQVlA4WAoAAAAQAAAAAAAAAAAAQUxQSAwAAAARBxAR/Q9ERP8DAABWUDggGAAAABQBAJ0BKgEAAQAAAP4AAA3AAP7mtQAAAA==',
            animation: 'UklGRlIAAABXRUJQVlA4WAoAAAASAAAAAAAAAAAAQU5JTQYAAAD/////AABBTk1GJgAAAAAAAAAAAAAAAAAAAGQAAABWUDhMDQAAAC8AAAAQBxAREYiI/gcA'
        };
        var img = new Image();
        img.onload = () => {
            //var result = (img.width > 0) && (img.height > 0);
            //callback(feature, result);
            localStorage.setItem('webpSupport', 'true');

            this.webp = true;
            console.log("webp supported");
            //if (aigens) aigens.webp = true;
            this.setSupport(true);
        };
        img.onerror = () => {
            //callback(feature, false);
            localStorage.setItem('webpSupport', 'false');
            //this.webp = false;
            this.setSupport(false);
            console.log("webp NOT supported");
        };
        img.src = "data:image/webp;base64," + kTestImages[feature];
    }

    openSettingsPage(): Promise<any> {
        return Promise.resolve();
    }

    openExternalLink(url: string): void {
        window.open(url, '_blank', 'noopener');
    }
}
