

import { Data } from "./data";

export class PrintJob extends Data {

    class: string = "PrintJob";
    id: string;
    name: string;
    body: string;
    printer: string;
    status: string;
    groupId: number;
    brandId: number;
    storeId: number;

    orderNo: string;
    orderId: string;
    printerId: string;
    printerCode: string;
    vendor: string;
    mode: string;
    test: boolean = false;
    create: number;
    update: number;

    callbackUrl: string;

}