import { Injectable } from '@angular/core';
import { Capacitor } from '@capacitor/core';
import { Geolocation } from '@capacitor/geolocation';
import { LocationService } from '../../../../../core/src/app/framework';
import { CommonService } from '../../../../../core/src/app/pure/services/common.service';

@Injectable({
    providedIn: 'root'
})
export class NativeLocationService extends LocationService{


    constructor() {
        super();
    }

    public static locationServiceFactory(){

        var native = Capacitor.isNativePlatform();

        if(native){
            return new NativeLocationService();
        }

        return new LocationService();

    }

    getGps(callback: Function): void {

        Geolocation.getCurrentPosition().then((result)=>{

            if(!result) {
                callback(null);
                return;
            }

            var coords = result.coords;

            if(coords.latitude){

                var gps = {
                    latitude: this.round(coords.latitude, 4),
                    longitude: this.round(coords.longitude, 4)
                };

                CommonService.setLocal("lastGps", gps);
                callback(gps);
            }else{
                callback(null);
            }


        }).catch(() => {
            callback(null);
        })

    }

    requestPermissions() {
        Geolocation.requestPermissions().then(rsp => {
            console.log("requestPermissions rsp :", rsp);
        }, err => {
            console.log("requestPermissions error :", err);

        })
    }

}
