import { Data } from './data';
import { Member } from './member';

export class Membership extends Data {

    class: string = "Membership";
    id: number;
    memberId: number;
    qrcode: string;
    marketing: boolean = false;
    groupMarketing: boolean = false;
    recency: number;
    points: number;
    frequency: number;
    monetary: number;
    usablePoints: number;
    reservePoints: number;
    redemptions: number;
    member: Member;
    create: Date;
    update: Date;
    expire: Date;
    status: string;
    pointsExpire: Date;
    nextExpire: Date;
    tier: string;
    brandId: number;
    backendId: string;
    activateFee: number;
    referralCode: string;
    tierPoints: number;
    memberTierPolicy: any;
    rfmTier: string;

    protected toProperty(name: string, value) {

        switch (name) {

            case "member":
                value = Data.toData(Member, value);
                break;
            default:
                value = super.toProperty(name, value);
                break;

        }
        

        return value;
    }
}
