import { PageParams } from '@aigens/core/src/app/pure/base/page-params';
import { PlatformService } from '@aigens/core/src/app/pure/services/platform.service';
import { RoutingService } from '@aigens/core/src/app/pure/services/routing.service';
import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { LazyService } from '../../../../../../core/src/app/pure/services/lazy.service';

@Component({
    selector: 'app-footer',
    templateUrl: './footer.component.html',
    styleUrls: ['./footer.component.scss'],
})
export class FooterComponent implements OnInit {
    @Input() showPaymentIcons = false;

    context: any;

    brand: any;

    agreements: any[];

    payments: string[] = [];

    navParams: PageParams;

    constructor(private routingService: RoutingService, private lazyService: LazyService, private platformService: PlatformService, private route: ActivatedRoute) {
        this.context = window['aigens'].context;

        console.log('FooterComponent construct');
    }

    ngOnInit(): void {
        this.navParams = new PageParams();
        this.navParams.route = this.route;

        if (this.context.agreements) {
            this.agreements = this.agreementSorting('home', null, this.navParams.get('mode') ?? null);
        }

        const paymentIcons = this.context?.config?.data?.paymentIcons;
        if (typeof paymentIcons === 'object') {
            Object.keys(paymentIcons).forEach((x) => {
                if (paymentIcons[x] === true && this.getPaymentIcon(x) !== '') {
                    this.payments.push(x);
                }
            });
        }
        if (Array.isArray(paymentIcons)) {
            paymentIcons?.forEach((x) => {
                if (this.getPaymentIcon(x) !== '') {
                    this.payments.push(x);
                }
            });
        }
    }

    getPaymentIconWithPath(paymentMethod: string): string {
        const paymentIcon = this.getPaymentIcon(paymentMethod);
        if (paymentIcon === '') {
            return '';
        }
        return `assets/icons/svg/${paymentIcon}.svg`;
    }

    getPaymentIcon(paymentMethod: string): string {
        switch (paymentMethod) {
        case 'creditcard':
            return 'ios-payment-ccard';
        case 'googlepay':
        case 'google':
            return 'ios-payment-gpay';
        case 'applepay':
            return 'ios-payment-apple';
        case 'gopay':
            return 'ios-payment-gopay';
        case 'ewallet':
            return 'ios-payment-ewallet';
        case 'qris':
            return 'ios-payment-qris';
        case 'virtualaccount':
            return 'ios-payment-virtualaccount';
        case 'alipay':
            return 'ios-payment-alipay';
        case 'alipayHK':
            return 'ios-payment-alipay-hk';
        case 'alipayCN':
            return 'ios-payment-alipay-cn';
        case 'cash':
            return 'ios-payment-cashpay';
        case 'gcash':
            return 'ios-payment-gcash-new';
        case 'paymaya':
            return 'ios-payment-paymaya';
        case 'master':
            return 'ios-payment-mastercard';
        case 'visa':
            return 'ios-payment-visa';
        case 'JCB':
            return 'ios-payment-JCB';
        case 'chargetoroom':
            return 'ios-payment-chargetoroom';
        case 'payme':
            return 'ios-payment-payme';
        case 'wechat':
            return 'ios-payment-wechat';
        case 'octopus':
            return 'ios-payment-octopus';
        case 'unionpay':
            return 'ios-payment-unionpay';
        default:
            return '';
        }
    }

    agreementSorting(trigger: string, feature?: string, mode = '') {
        if (feature == 'active') {
            return this.context?.agreements?.filter((item) => this.isAgreementValid(item, trigger, mode) && !item.passive);
        }
        if (feature == 'passive') {
            return this.context?.agreements?.filter((item) => this.isAgreementValid(item, trigger, mode) && item.passive);
        }

        return this.context?.agreements?.filter((item) => this.isAgreementValid(item, trigger, mode));
    }

    isAgreementValid(agreementItem: any, trigger: string, mode = ''): boolean {
        if (!agreementItem.enabled) {
            return false;
        }

        if (agreementItem?.trigger?.indexOf(trigger) < 0) {
            return false;
        }

        const channel = this.platformService.getMenuChannel();
        if (agreementItem.channels?.length > 0 && agreementItem.channels?.indexOf(channel) < 0) {
            return false;
        }

        if (mode && agreementItem.modes?.length > 0 && agreementItem.modes?.indexOf(mode) < 0) {
            return false;
        }

        const storeId = this.routingService.getQuery('storeId') || this.context.storeId;
        if (agreementItem.storeIds && storeId && agreementItem.storeIds.indexOf(storeId) < 0){
            return false;
        }

        return true;
    }

    // (click) = "agreementClicked([agreement])"
    agreementClicked(agreement: any[]): void {
        // Peter: Pass in a event emitter or find other way to do it for now

        // this.agreementService.showAgreement(agreement, "home")
        const agreementService = this.lazyService.getService('AgreementService');
        if (!agreementService) return;

        console.log('agg clicked', agreement);

        agreementService.showAgreement(agreement, 'home');
    }

    getDeviceConfig(key: string): any {
        return this.context?.config?.data?.[key] ?? null;
    }
}
