import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Item, ItemGroup, Store } from '../../data';
import { ConfigService } from './config.service';
import { DataService } from './data.service';
import { MemberContextManager } from './member-context-manager.service';
import { MemberContext } from './member-context.service';

@Injectable({
  providedIn: 'root'
})
export class EwalletService {

  // Topup
  virtualStore: Store;
  virtualStoreId: string;

  ewalletGroupId: string;
  ewalletGroup: ItemGroup;
  ewalletItems: Item[];

  ewalletMode = 'takeaway';

  private ewalletTopUpItem: Item;

  private ecreditPolicy; // moneyToEcreditRatio

  constructor(public dataService: DataService, public context: MemberContext, public memberContextManager: MemberContextManager, public config: ConfigService) { }

  setVirtualStoreId(storeId: string): void {
    this.virtualStoreId = storeId;
  }

  setEwalletGroupId(groupId: string = '500-IGecredit'): void {
    this.ewalletGroupId = groupId;
  }

  getFreeInputMax(): number {
    return this.virtualStore?.crm?.['ecreditPolicy']?.freeInputMax;
  }

  setEwalletMode(mode): void {
    this.ewalletMode = mode;
  }


  setVirtualStore(store): void {
    this.virtualStore = store;
  }

  setEwalletTopUpItem(item): void {
    this.ewalletTopUpItem = item;
  }

  setEcreditPolicy(ecreditPolicy: any): void {
    this.ecreditPolicy = ecreditPolicy;
  }

  getEcreditRatio(isUseContextStoreCurrencyUnit = false): number {
    let unit: number = this.ecredit?.smallestCurrencyUnit || this.virtualStore?.['smallestCurrencyUnit'];
    if (isUseContextStoreCurrencyUnit && !unit && unit !== 0) {
      unit = this.context?.store?.['smallestCurrencyUnit']; // fallback context store smallestCurrencyUnit
    }
    return (unit || unit === 0) ? Math.pow(10, -unit) : 1;
  }

  getTopUpPoints(): number {
    // return this.ewalletTopUpItem?.points || 0;
    return this.ewalletTopUpItem?.price / this.getEcreditRatio();
  }

  getVirtualStoreId(): string {
    return this.virtualStoreId;
  }

  getVirtualStore(): Observable<any> {
    if (!this.virtualStoreId) {
      return new Observable(null);
    }

    const url = '/api/v1/menu/store/' + this.virtualStoreId + '.json';
    const params = {
      menu: this.ewalletMode || 'takeaway',
      locale: 'multi',
      // nocache: true
    };
    return this.dataService.getRaw(url, params);
  }


  getEwalletItems(): Item[] {
    return this.ewalletItems || [];
  }

  updateMember(complete = () => { }, error = () => { }): any {
    if (!this.context.member) {
      error();
      return;
    }

    const url = '/api/v1/crm/member.json';

    const params = {
      brandId: this.context.brand.id,
      fetchAllSc: this.crmHasFeature('stampcard'),
    };
    this.dataService.get(url, params).subscribe(
      (data) => {
        console.log(data);
        // this.context.setMember(data);
        this.memberContextManager.updateMemberData(data);

        complete();
      },
      (err) => {
        console.log(err);

        if (err.status === 401) {
          this.context?.logout();
        }

        error();
      }
    );
  }

  crmHasFeature(feature: string): boolean {
    return (
      this.context.crm &&
      this.context.crm.features &&
      this.context.crm.features.indexOf(feature) >= 0
    );
  }

  // update the ecredit balance
  handleEwallet(order): void {
    // may be call member.json to update the member
    this.context.member.ecredit.balance = order?.['afterEcredit'];
    if (this.context?.member) {
      const sid = this.config.getSessionId();
      // this.memberContextManager?.setMemberData( sid ? {...this.context?.member, sessionId: sid } : this.context?.member);
      this.memberContextManager.updateMemberData(sid ? {...this.context?.member, sessionId: sid } : this.context?.member);
    }
  }

  get ecredit(): any {
    return this.context?.member?.ecredit || this.context?.membership?.member?.ecredit;
  }

  getMemberEwallectTotal(): number { // new logic : ecredit?.balance only for BE , FE should use getDisplayEwalletNumber()
    return this.ecredit?.balance || 0;
  }

  // display ewallet total
  getDisplayEwalletNumber(): number {
    return this.transformEwalletBalanceToPrice(this.getMemberEwallectTotal());
  }

  // display ewallet currency(HKD)
  getMemberEwallectCurrency(): string {
    return this.ecredit?.currency || this.context?.currency;
  }

  // display ewallet currencySign($)
  getMemberEwallectCurrencySign(): string {
    return this.ecredit?.currencySign || this.context?.currencySign;
  }

  // display ewallet top up item price
  getTopUpItemPriceByPoints(points: number): number {
    return this.transformEwalletBalanceToPrice(points);
  }

  transformEwalletBalanceToPrice(val: number): number {
    const res = val * this.getEcreditRatio();
    const result = res?.toString().indexOf('.') ? parseFloat(res?.toFixed(2)) : res;
    return result;
  }

}
