
import { Injectable } from '@angular/core';
import { Item} from 'aigens-data';



@Injectable({ providedIn: 'root' })
export class InventoryManager {

    inventory: any = {};
    version: number = 0;

    constructor() {

    }

    isEmpty(): boolean {

        return Object.keys(this.inventory).length === 0;
    }

    getInv(item: Item) {

        var q = null;
        var liveInv = this.inventory;

        if (liveInv && !this.isEmpty()) {


            q = this.getQty(liveInv, item.backendId);// liveInv[item.backendId];
            if (q == null) q = this.getQty(liveInv, item.id); //liveInv[item.id];
            if (q == null) q = -1;

        } else {
            q = item.inventory;
        }


        return q;
    }

    isHidden(item: Item){

        let qty = this.getInv(item);
        if (qty == -1000) {
            return true;
        }

        return false;

    }

    getQty(inv: any, itemid: string) {

        let qty = inv[itemid];

        if (typeof qty === 'undefined') {
            return null;
        }

        return qty;
    }

    isPublish(item: Item): boolean {


        let qty = this.getInv(item);
        if (qty == -1000) {
            return false;
        }

        return item.published;



    }


    isSoldout(item: Item, qty: number = 1): boolean {


        var inv = this.getInv(item);

        if (inv == -1 || inv == -999) return false; //-1=unlimited , -999 = preparing
        if (inv <= 0)  return true;

        return qty > inv;


    }

    isPreparing(item: Item): boolean {

        var inv = this.getInv(item);

        if (inv == -999) return true;

        return false;

    }
    getSoldoutCount(items: Item[], qty: number = 1): number {

        let count: number = 0;

        for (let item of items) {

            let soldout = this.isSoldout(item, qty);

            if (soldout) {
               count++;
            }
        }

        return count;

    }


    set(inventory: any, version: number) {

        console.log("inventory", inventory, version);

        if (!inventory) inventory = {};

        this.inventory = inventory;
        this.version = version;

    }

    needUpdate(version: number): boolean {

        console.log("inv version:" + version + ">" + this.version);
        let update = version > this.version;

        console.log("need update inv", update);
        return update;
    }
}
