import { Component, Injector } from '@angular/core';
import { Router } from '@angular/router';
import { Capacitor } from '@capacitor/core';
import { TranslateService } from '@ngx-translate/core';
import { ConfigService } from '../../../core/src/app/framework';
const fromEntries = require('fromentries');
// import { AppTrackingTransparency } from 'capacitor-plugin-app-tracking-transparency';

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.scss']
})
export class AppComponent {

    init = {

        "profile": {
            "country": "HK",
            "production": false,
            "applePay": "https://us-central1-aigensstoretest.cloudfunctions.net/applePaySession",
            "groupId": 500,
            "api": "https://aigensstoretest.appspot.com",
            "locale": "en"
        },
        "context": {

        }
    };

    constructor(private injector: Injector, private t: TranslateService, private router: Router, private configService: ConfigService) {

        if (!Object['fromEntries']) {
            Object['fromEntries'] = fromEntries;
        }


        window['aigens'] = this.init;


        this.showTrackingPopup();


        this.initTranslation();

    }

    initTranslation() {
        const locale = this.configService.getLocale() || 'en';
        this.t.setDefaultLang(locale);
        this.t.use(locale);
        this.configService.setLocale(locale);
    }

    showTrackingPopup() {

        // if (Capacitor.isNativePlatform() && Capacitor.getPlatform() == 'ios') {
        //     setTimeout(() => {
        //         AppTrackingTransparency.requestPermission().then(r => {
        //         })
        //     }, 500);
        // }
    }

}
