import { Injectable, Injector } from '@angular/core';
import { Capacitor } from '@capacitor/core';
import { TTSOptions, TextToSpeech, SpeechSynthesisVoice} from '@capacitor-community/text-to-speech';

@Injectable({
    providedIn: 'root',
})
export class TextToSpeechService {
    speechSynthesisAvailable: boolean = false;

    constructor(injector: Injector) {

    }

    isAvailable(): boolean {
        return true;
    }

    speak = async (options: TTSOptions) => {
        try {
            return await TextToSpeech.speak(options);
        } catch (error) {
            console.error(error);
        }
    };

    stop = async () => {
        await TextToSpeech.stop();
    };

    getSupportedLanguages = async () => {
        return await TextToSpeech.getSupportedLanguages();
    };

    getSupportedVoices = async () => {
        return  TextToSpeech.getSupportedVoices();
    };

    isLanguageSupported = async (lang: string) => {
        return await TextToSpeech.isLanguageSupported({ lang });
    };
}
