import { Injectable, Injector } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Ad, Brand, Court, Crm, Group, Member, Membership, Store } from 'aigens-data';
import { get, merge } from 'lodash-es';
import { Location } from '../../data/data/location';
import { BaseContext } from '../base/base-context';

@Injectable({
    providedIn: 'root'
})
export class Context extends BaseContext {

    //hold the app context
    app: any;

    member: Member;
    membership: Membership;

    config: any;

    userLang: string = "en";
    contentLang: string;

    language: string;
    languages: string[];
    country: string;

    groupId: number;
    brandId: number;
    storeId: number;
    courtId: number;
    ads: Ad[];
    discounts: any[];

    group: Group;
    brand: Brand;
    store: Store;
    court: Court;
    //property: any;

    location: Location;

    contextContent: any;

    currencySign: string = "$";
    currency: string;

    agreements: any[];
    campaigns: any[];

    settings: any;

    locale: string;
    level: string;
    timezone: string;
    adminLevel: string;
    embed: boolean = false;

    admin: boolean = false;

    crm: Crm;
    form: any;

    payments: any[] = [];
    sessionId: string;

    t: TranslateService;

    domain: any;
    version: string;
    singleStore: boolean;
    featuredItems: any[];
    defaultStoreMode: string;

    station:string;

    // The native app may be set this, which represents redirect to the specified app scheme
    appSchemesOrigin: string;
    constructor(injector: Injector) {

        super(injector);

        this.t = injector.get(TranslateService);

        console.log("constructing context, this should only happen once");

        this.initFromContext();

        this.bind(["member", "membership", "sessionId"]);

    }

    initFromContext(){
        this.initFromJs();
        this.setTranslation();
    }


    private initFromJs() {

        if (!window['aigens']) {
            window['aigens'] = {};
        }

        var aigens = window['aigens'];

        if (aigens && aigens.context) {
            this.version = aigens.version;
            this.setContextData(aigens.context);

        }


    }



    //clear the context, likely due to logout or switching to different brand
    clear() {

        /*
        this.membership = null;
        this.groupId = null;
        this.group = null;
        this.brandId = null;
        this.brand = null;
        this.store = null;
        this.storeId = null;
        this.courtId = null;
        this.court = null;
        this.currencySign = null;
        this.currency = null;
        this.crm = null;
        this.form = null;
        this.payments = null;
        this.sessionId = null;
        */
    }

    public logout(): void {
        console.log('logout');

        this.setLocal("member", null);
        this.setLocal("session", null);

        this.next("member", null);
        this.next("membership", null);
        this.next("sessionId", null);

        this.sentryService.removeMember();

        this.save();
    }

    protected makeSaveData(): {} {

        if (!this.groupId) {
            console.log("cannot save incomplete context");
            return null;
        }

        var data = {
            member: this.member,
            membership: this.membership,
            userLang: this.userLang,
            contentLang: this.contentLang,
            language: this.language,
            languages: this.languages,
            country: this.country,
            group: this.group,
            groupId: this.groupId,
            brandId: this.brandId,
            brand: this.brand,
            store: this.store,
            storeId: this.storeId,
            court: this.court,
            courtId: this.courtId,
            currencySign: this.currencySign,
            currency: this.currency,
            crm: this.crm,
            form: this.form,
            sessionId: this.sessionId,
            payments: this.payments,
            agreements: this.agreements,
            config: this.config,
            locale: this.locale,
            defaultStoreMode: this.defaultStoreMode
        };
        return data;
    }

    protected restoreSavedData(data: any) {

        console.log("init context doesn't restore saved data");

    }

    /**
     * @deprecated Use settingManager.get() instead
     */
    getDeviceConfig(key: string): any {

        var result = null;

        if (this.config?.data) {

            var config = this.config.data;
            result = get(config, key);
        }

        return result;
    }


    private setTranslation() {
        let userLang;

        const languages = window['aigens']?.context?.languages;

        if (!languages || languages?.includes(this.getLocal('locale'))) {
            userLang = this.getLocal('locale');
        }

        var lang = userLang || window['aigens']?.context?.language || 'en';
        this.setLocal('locale', lang);


        this.t.onLangChange.subscribe((res) => {

            this.mergeTranslation();
        });

        this.t.setDefaultLang('en');
        this.t.use(lang);

        this.userLang = userLang;


        this.mergeTranslation(); // initial merge the translations, in case just one lang and no onLangChange trigger

        console.log("lang setup done");

    }



    mergeTranslation() {

        var lang = this.t.currentLang;
        var currentTrans = this.t.translations[lang];

        //console.log("current lang", lang);
        //console.log("currentTrans", currentTrans);
        this.userLang = lang;

        var overwrites = this.config?.data?.translations;

        if (overwrites) {
            var newTrans = overwrites[lang];
            //console.log("overwrites", newTrans);
            if (newTrans) {
                merge(currentTrans, newTrans);
            }

            //console.log("mergedTrans", currentTrans);
        }




    }


    setMember(member: any) {

        this.member = member;
        this.setLocal("member", member);

        if(member?.membership) {
            this.membership = member?.membership;
        }

        this.next("member", member);

        try {
            this.sentryService.setMember(member);
        } catch(err) {
            console.log(err);
        }
    }

    isSignedIn() {
        return this.member && this.member.sessionId && !this.member.guest;
    }

    setContextData(data): void {

        if (!data) {
            return;
        }

        const context = this;


        // invalid data
        if (!data.groupId) {
            console.log("INVALID CONTEXT", data);
            return;
        }

        Object.assign(context, data);

        context.contentLang = context.language;
        context.settings = context.config?.data;

        this.contextId = this.makeGenericKey(data, "init", data.locale);


        context.save();

    }

    switchStore(brand: Brand, store: Store) {

        if (this.brand && this.brand.id != store.brandId) {
            console.log("CANNOT SWITCH store due to brand mismatch");
            return;
        }

        this.store = store;
        this.storeId = store.id;

        this.switchBrand(brand);

    }

    switchBrand(brand: Brand) {

        if (this.group && this.group.id != brand.groupId) {
            console.log("CANNOT SWITCH brand due to group mismatch");
            return;
        }

        this.brand = brand;
        this.brandId = brand.id;

    }
}

