import { Data } from './data';

export class Crm extends Data {
    class: string = "Crm";
    brandId: number;
    groupId: number;
    id: number;
    name: string;
    username: string;
    password: string;
    accessToken: string;
    addPoints: boolean;
    type: string;
    links: any;
    pointRatio: number;
    addstamps: boolean;
    extra: any;
    pointToMoneyRatio: number;
    profile: boolean;
    redeemPointFactor: number;
    redeemPointMin: number;
    stampRatio: number;
    activateFee: number;
    features: string[];
    showPointExpiry: boolean;
    modes: string[];
    smallestPointsUnit: number;
    showMemberQr: boolean;
    verificationType: 'email' | 'phone';
    accountDeleteType: string;
    pointIcon;
    googleClientId: string;
    facebookClientId: string;

    public static hasFeature(crm: Crm, feature: string): boolean {
        if (crm?.features) {
            return crm.features.indexOf(feature) >= 0;
        }
        return false;
    }
    public static getPointIcon(crm: Crm): any {
        if (crm?.pointIcon?.url) {
            return crm.pointIcon.url;
        }
    }
}
