import { Injectable, Injector } from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class CommonService {


    constructor(injector: Injector) {


    }

    public static getLocal(key: string): any {

        try{

            const str = localStorage.getItem(key);
            if (str == null || str == 'undefined') {
                return null;
            }
            return JSON.parse(str);
        }catch(e){
            console.log(e);
        }

        return null;
    }

    public static setLocal(key: string, object: any) {

        try{
            localStorage.setItem(key, JSON.stringify(object));
        }catch(e){
            console.log(e);
        }
    }

    public static getSessionStorage(key: string): any {

        try{

            const str = sessionStorage.getItem(key);
            if (str == null || str === 'undefined') {
                return null;
            }
            return JSON.parse(str);
        }catch (e){
            console.log(e);
        }

        return null;
    }

    public static setSessionStorage(key: string, object: any): void {
        try{
            sessionStorage.setItem(key, JSON.stringify(object));
        }catch(e){
            console.log(e);
        }
    }



    public static deserialize(str: string): any {

        try{

            if (str == null || str == 'undefined') {
                return null;
            }
            return JSON.parse(str);
        }catch(e){
            console.log(e);
        }

        return null;
    }

    public static serialize(object: any):string{

        return JSON.stringify(object);

    }

}
