import { Component, OnInit } from '@angular/core';
import { DialogService } from '@aigens/core/src/app/framework';
import { MatDialog } from '@angular/material/dialog';
import { ProgressDialogComponent } from '../components/ProgressDialogComponent/ProgressDialogComponent.component';
import { AlertDialogComponent } from '../components/alert-dialog/alert-dialog.component';
import { ConfirmDialogComponent } from '../components/confirm-dialog/confirm-dialog.component';
@Component({
    selector: 'app-root',
    templateUrl: './root.component.html',
    styleUrls: ['./root.component.scss']
})
export class RootComponent implements OnInit {

    constructor(private dialogService: DialogService, matDialog: MatDialog) {

        this.dialogService.dismissPreLoader();

        this.dialogService.setDialogController(matDialog);
        this.dialogService.setSuperLoaderComponent(ProgressDialogComponent);
        this.dialogService.setAlertComponent(AlertDialogComponent);
        this.dialogService.setConfirmComponent(ConfirmDialogComponent);

    }

    ngOnInit(): void {
    }

}
