import { Component, Input, OnInit } from '@angular/core';

@Component({
    selector: 'app-img',
    templateUrl: './img.component.html',
    styleUrls: ['./img.component.scss']
})
export class ImgComponent implements OnInit {

    @Input('src') src: string;
    @Input('content') content: any;
    @Input('imgName') imgName: string = 'default';
    @Input('ratio') ratio: number = 1;
    @Input('icon') icon: string = "";
    @Input('animate') animate: boolean = false;
    @Input('pw') pw: number = 500;
    @Input('class') class: string = "";
    @Input('fallback') fallback: string = "";
    @Input('background') background: string = "transparent";
    @Input('type') type: string = "";
    @Input('constrain') constrain: 'cover' | 'contain' = 'cover';
    @Input('hideEmpty') hideEmpty: boolean = false;
    // @Input('locale') locale: string = null;
    oldContent;

    @Input()
    set locale(_locale: string) {
        if(_locale !== this.locale){
            this._locale = _locale;
            this.initData();
        }
    }

    get locale(): string{
        return this._locale;
    }


    loaded: boolean = false;
    useAnimate: boolean = false;

    //this control the ratio
    paddingTop: string = "100%";

    height: string = "";

    startTime: number;
    loadedTime: number;

    _locale: string = null;


    constructor() {

        this.startTime = new Date().getTime();

    }

    ngOnInit(): void {

        this.useAnimate = this.animate;
        var ratio = (1 / this.ratio) * 100;
        this.paddingTop = ratio + "%";

        this.initData();
    }

    ngDoCheck(): void {
        if(this.oldContent !== this.content){
            this.initData();
            this.oldContent = this.content;
        }
    }

    initData(): void {
        if(this.content){

            //console.log("IMG CONTENT", this.content);

            var imgs =  this.content.images  || this.content[0]?.images;

            if(imgs){
                var img = imgs[this.imgName] || imgs['logo'];
                if(img){
                    this.src = this.getImgLocaleUrl(img);
                }
            }

        }

        if(this.src){
            this.src = ImgComponent.patchUrl(this.src, this.pw);
        }

        if(this.src && this.type == 'background'){

            this.background = 'no-repeat center url(' + this.src + ') ';
            this.paddingTop = "0px";
            this.height = "100%";
        }

    }

    //https://lh3.googleusercontent.com/4U4a6TeGMNnCUk4hWhSMR90lPT7GZNkBLk-YmD2QRC-nRnRZGdug5vv8hwrF7ClO0wjTW4zDQo7jeTcyScLx3pmxaUsP
    public static patchUrl(url: string, width: number): string{

        var aigens = window['aigens'];

        if (url) {

            var isGoogle = url.indexOf("googleusercontent.com") != -1 || url.indexOf("image.order.place") != -1 || url.indexOf("image.aigens.com") != -1 || url.indexOf("imagetest.aigens.com") != -1;

            //if url is google and not already enchanced
            if(isGoogle){

                var eqIndex = url.lastIndexOf("=");
                if(eqIndex != -1){
                    var x = url.substring(0, eqIndex);
                    url = x;
                }

                var segs = [];

                if (width > 0 && url.indexOf('=w') == -1) {
                    segs.push('w' + width);
                }

                if (width == 0 && url.indexOf('=s') == -1) { // use original size if width is 0
                    segs.push('s0');
                }

                var webp = aigens?.webp;
                if(webp){
                    segs.push("rw");
                }

                if(segs.length > 0){
                    url += "=" + segs.join("-");
                }
            }

            if(isGoogle && aigens?.network == 'cn'){

                var imgHost = aigens.profile.imgHost2;
                if(imgHost){

                    var path = url.substring(url.lastIndexOf('/'));

                    url = imgHost + path;
                }
            }

        }

        return url;
    }

    getImgLocaleUrl(img: any): string {
        if(!this.locale) {
            return img.url;
        }

        const urlName = 'url_' + this.locale;
        if(urlName in img) {
            return img[urlName];
        }
        return img.url;
    }

    onload(){

        this.loadedTime = new Date().getTime();

        var diff = this.loadedTime - this.startTime;

        if(diff < 500){
            this.useAnimate = false;
        }

        this.loaded = true;
    }

}
