import { Injectable } from '@angular/core';
import { CommonService } from '../../pure/services/common.service';
import { Geolocation } from '@capacitor/geolocation';
@Injectable({
    providedIn: 'root'
})
export class LocationService {

    DEBUG_GPS = {
        latitude: 22.3193,
        longitude: 114.1694
    };

    constructor() {

    }

    round(num, decimals) {
        var x = Math.pow(10, decimals);
        return Math.round(num * x) / x;
    }

    getLast():any{
        return CommonService.getLocal("lastGps");
    }

    getGps(callback: Function): void {
        if (!(navigator?.geolocation)) {
            callback(null);
            return;
        }

        // Please use Geolocation plugin, If on Web it will use : navigator.geolocation.getCurrentPosition as well , but on native , it will use native code.
        Geolocation.getCurrentPosition({
            enableHighAccuracy: false,
            timeout: 10000,
            maximumAge: Infinity
        }).then(pos => {
            const result = {
                latitude: this.round(pos.coords.latitude, 4),
                longitude: this.round(pos.coords.longitude, 4)
            }
            if(result.latitude){
                CommonService.setLocal("lastGps", result);
                callback(result);
            }else{
                callback(null);
            }
        }).catch(err  => {
            console.error(err);
            callback(null);
        })
        // navigator.geolocation.getCurrentPosition(
        //     (pos) => {
        //         const result = {
        //             latitude: this.round(pos.coords.latitude, 4),
        //             longitude: this.round(pos.coords.longitude, 4)
        //         }

        //         if(result.latitude){
        //             CommonService.setLocal("lastGps", result);
        //             callback(result);
        //         }else{
        //             callback(null);
        //         }

        //     }, (err) => {
        //         console.error(err);
        //         callback(null);
        //     },
        //     {
        //         enableHighAccuracy: false,
        //         timeout: 10000,
        //         maximumAge: Infinity
        //     }
        // );
    }

    requestPermissions() {
        console.warn("web does not have this method");
    }

}
