import { Pipe, PipeTransform } from '@angular/core';
import { Item, OrderItem } from 'aigens-ng-core';


@Pipe({
    name: 'resortOrFilterUnlockItem'
})
export class ResortOrFilterUnlockItemPipe implements PipeTransform {

    constructor() {
    }

    // resort the unlock item or hide them in shop cart
    transform(orderItems: OrderItem[], filter = false): OrderItem[] {
        let normalOIs: OrderItem[] = [];
        let unlockOIs: OrderItem[] = [];
        // console.log('resortOrFilterUnlockItem');
        // resort the unlock item position in Array
        if (orderItems) {
            orderItems.forEach(oi => {
                if (!oi) return;
                if (OrderItem?.isUnlockOrderItem(oi)) {
                    if (filter) return; // hide the unlock item in shop cart
                    unlockOIs.push(oi);
                } else {
                    normalOIs.push(oi);
                }
            })
        } else {
            console.warn('the order item input seems to be empty?', orderItems);
        }
        return normalOIs.concat(unlockOIs);
    }
}
