import { TranslateService } from '@ngx-translate/core';
import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'formErrorMessage',
})
export class FormErrorMessagePipe implements PipeTransform {
    constructor(public translation: TranslateService) {
    }

    transform(content: string): string {
        const translate = this.translation.instant(content) as string;
        const isTranslationFound = translate !== content;
        if (!isTranslationFound) {
            let defaultTranslation = 'form.errorMsg';
            if (content.includes('regex')) {
                defaultTranslation += '.regex';
                return this.translation.instant(defaultTranslation) as string;
            }
            return this.translation.instant(`${defaultTranslation}.default`) as string;
        }
        return translate;
    }
}
