import { DialogService } from './dialog.service';
import { HttpErrorResponse } from '@angular/common/http';
import { Injectable, Injector, NgZone, ErrorHandler, Inject } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Observable } from 'rxjs';
import { RoutingService } from '../../pure/services/routing.service';
import { BaseErrorHandlerService } from '../../pure/services/base-error-handler.service';

@Injectable({
    providedIn: 'root'
})
export class ErrorHandlerService extends BaseErrorHandlerService implements ErrorHandler {

    constructor(private injector: Injector, private zone: NgZone, private t: TranslateService) {
        super(injector);
    }

    handleError(error) {
        super.handleError(error);
        this.customErrorHandler(error)
    }

    /**
     * Need to get DialogService from injector rather than constructor injection to avoid cyclic dependency error
     * @returns {}
     * ref: https://github.com/ngneat/hot-toast/issues/70
     */
     private get dialogService() {
        return this.injector.get(DialogService);
    }

    customErrorHandler(param) {
        const name = param?.name;
        const layout = param?.layout;
        const isDefaultLayout = layout === 'dialog';
        const error = new Error()

        error.message = this.t.instant(`error-codes.${name}.message`)
        error.name = this.t.instant(`error-codes.${name}.title`)
        if (!isDefaultLayout) {
            layout === 'toast' && this.errorToast(error.message, 1000, 'bottom');
            layout === 'page' && '';
            return;
        }

        this.errorDialog(error.name, error.message)
    }

    errorDialog(name, message) {
        this.zone.run(() =>
            this.dialogService.showAlert(name, message)
        );
    }

    errorToast(message, time, position) {
        this.zone.run(() =>
            this.dialogService.showToast(message, time, position)
        );
    }
}
