import { Pipe, PipeTransform } from '@angular/core';
import { Item, OrderItem } from 'aigens-ng-core';
import * as _ from 'lodash-es';


@Pipe({
    name: 'removeItemVariations'
})
export class RemoveItemVariationsPipe implements PipeTransform {

    constructor() {
    }

    // remove the item variations
    transform(items: Item[]): Item[] {
        let resultItems = []
        items?.forEach(item => {
            let item_new = _.cloneDeep(item);
            if (item?.variations) {
                item_new.variations = [];
            }
            resultItems.push(item_new);
        });
        return resultItems;
    }

}
