import { Injectable, Injector } from '@angular/core';
import { App } from '@capacitor/app';
import { Browser } from '@capacitor/browser';
import { Capacitor } from '@capacitor/core';
import { Device } from '@capacitor/device';
import { AndroidSettings, IOSSettings, NativeSettings } from 'capacitor-native-settings';
import { PlatformService } from '../../../../../core/src/app/pure/services/platform.service';

@Injectable({
    providedIn: 'root'
})
export class NativePlatformService extends PlatformService {


    appInfo: any;

    constructor(injector: Injector) {

        super(injector);


        console.log("NativePlatformService constructor2");

        this.init();


        /*
        App.addListener('appStateChange', ({ isActive }) => {
            console.log('App state changed. Is active?', isActive);
        });

        App.addListener('appUrlOpen', data => {
            console.log('App opened with URL:', data);
        });

        App.addListener('appRestoredResult', data => {
            console.log('Restored state:', data);
        });
        */

    }

    private init() {

        if (this.isNative()) {

            console.log("getting native app info!");

            App.getInfo().then(appInfo => {
                this.appInfo = appInfo;
                console.log("info", appInfo);

            });
        }

    }

    isNative() {
        var native = Capacitor.isNativePlatform();
        return native;
    }

    getVersion(){
        return this.appInfo?.version ?? '';
    }

    getAppInfo(): Promise<any> {


        if(this.appInfo){
            return Promise.resolve(this.appInfo);
        }else if(!this.isNative()){
            return Promise.resolve({});
        }else{
            return App.getInfo();
        }

    }

    getAppInfoInstant(){
        return this.appInfo;
    }

    getNativePlatform() {
        var platform = Capacitor.getPlatform();

        return platform;
    }

    getNativeAppId() {
        return this.appInfo?.id;
    }

    /**
     * open phone's setting page
     * @returns
     */
    async openSettingsPage(): Promise<any> {
        const platform = (await Device.getInfo()).platform;
        if(platform === 'ios') {
            return NativeSettings.openIOS({
                option: IOSSettings.App,
              });
        } else if(platform === 'android') {
            return NativeSettings.openAndroid({
                option: AndroidSettings.ApplicationDetails,
            });
        }
    }

    openExternalLink(url: string): void {
        Browser.open({ url });
    }

}
