export const nameDefaultRegex = (maxChar: number) => {
    return {
        'no-punctuation': /^[^,;]+$/,
        ...maxChar && { 'limit-character': new RegExp(`^[a-zA-Z\\u4e00-\\u9eff\\s]{1,${maxChar}}$`, '') },
    }
};

// 8-12 letters and numbers
export function passwordFormatValidator(
    control: AbstractControl
): ValidationErrors | null {
    return isPasswordVaild(control.value) ? null : { invalidPassword: true };
}

export function confirmPasswordFormatValidator(
    control: AbstractControl
): ValidationErrors | null {
    const input = control.value;
    const isValid = control.root.value['password'] === input;
    if (!isValid) {
        return { passwordNotEqual: true };
    } else {
        return null;
    }
}

export function confirmEmailFormatValidator(
    control: AbstractControl
): ValidationErrors | null {
    const input = control.value;
    const isValid = control.root.value['email'] === input;
    if (!isValid) {
        return { emailNotMatch: true };
    } else {
        return null;
    }
}

export function isPasswordVaild(value: string): Boolean {
    let isVaild = false;
    if (value) {
        isVaild =
            (/[a-z]/.test(value) && /[0-9]/.test(value)) ||
            (/[0-9]/.test(value) && /[A-Z]/.test(value));
    }
    return isVaild;
}

// by '852' number-string return 'hk'
export function findCountryByCodes(value: string) {
    let key;
    for (let k in countryCallingCodes) {
        if (countryCallingCodes[k] === value) {
            key = k;
            break;
        }
    }
    return key || 'HK';
}

export function phoneValidator(control): ValidationErrors | null {
    if (isEmptyInputValue(control.value)) {
        return null; // don't validate empty values to allow optional controls
    }
    return isPhoneNumberVaild(control.value.toString())
        ? null
        : { 'mobile phone invaild': 'phone number invaild' };
}

export function isEmptyInputValue(value: string): boolean {
    return (
        value == null ||
        (typeof value === 'string' && value.length === 0) ||
        value === undefined
    );
}

export function isPhoneNumberVaild(value): Boolean {
    let isVaild = false;
    if (value !== undefined) {
        //  0/1 
        // isVaild = value.length == 8 && /^([2-9])\d{7}/.test(value);

        const newValue = value.trim();

        if (newValue) {
            isVaild = newValue.length > 7 && /^\d+$/.test(newValue);
        } else {
            isVaild = value.length > 7 && /^\d+$/.test(value);
        }
    }
    return isVaild;
}

export function isPhoneValid(phone: string, country = 'HK', customPhoneRegex = null): boolean {
    let isValid = false;
    if (customPhoneRegex) {
        return new RegExp(customPhoneRegex).test(phone);

    }
    if (countryPhoneReg[country]) {

        return new RegExp(countryPhoneReg[country]).test(phone);
    }

    if (!onlyHasNumber(phone)) {
        return false;
    }

    if (phone !== undefined && phone !== '') {
        let length = String(getPhoneLength(country));
        let min = length.split('-')[0];
        let max = length.split('-')[1];
        if (max) {
            if (phone.length >= Number(min) && phone.length <= Number(max)) {
                isValid = true;
            }
        } else {
            if (phone.length === getPhoneLength(country)) {
                isValid = true;
            } else {
                //   85244090771  44090771
                let countryCode = phone.slice(
                    0,
                    phone.length - getPhoneLength(country)
                );

                isValid =
                    phone.length ===
                    getCountryCallingCode(country).length +
                    getPhoneLength(country) &&
                    countryCode === getCountryCallingCode(country);
            }
        }
    }


    return isValid;
}

//  isPhoneValid()
export function isPhoneIncludingCode(phone, country = 'HK') {
    let isSeparate = false; // 
    let isIncludingCode = false; // has country code
    let separateSymbol = null; // 
    let noCodePhone = '';
    if (isPhoneValid(phone, country)) {
        if (phone.length === getPhoneLength(country)) {
            return {
                isSeparate,
                isIncludingCode,
                separateSymbol,
                noCodePhone: phone,
            };
        } else {
            // including code
            if (
                phone.length ===
                getCountryCallingCode(country).length + getPhoneLength(country)
            ) {
                // not has separateSymbol
                isIncludingCode = true;
                noCodePhone = phone.slice(
                    getCountryCallingCode(country).length
                );
                return {
                    isSeparate,
                    isIncludingCode,
                    separateSymbol,
                    noCodePhone,
                };
            } else {
                // has separateSymbol
                isIncludingCode = true;
                isSeparate = true;
                separateSymbol = phone.slice(
                    getCountryCallingCode(country).length,
                    getCountryCallingCode(country).length + 1
                );
                noCodePhone = phone.slice(
                    getCountryCallingCode(country).length + 1
                );
                return {
                    isSeparate,
                    isIncludingCode,
                    separateSymbol,
                    noCodePhone,
                };
            }
        }
    } else {
        return {};
    }
}

export function isEmailValid(value: string): boolean {
    try {
        value = value.toLowerCase();
        let isValid = false;
        if (value !== undefined) {
            const regExp =
                /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;
            //    isVaild = value.includes("@") && (value.includes(".com") || value.includes(".net"));
            isValid =
                regExp.test(value) &&
                !value.includes('.web') &&
                !value.includes('..') &&
                value.indexOf('.') !== 0;
            isValid =
                isValid &&
                !(
                    value.includes('<') &&
                    value.indexOf('>') === value.length - 1
                ) &&
                value.indexOf('@') === value.lastIndexOf('@') &&
                value.indexOf(')') !== value.length - 1;
            isValid =
                isValid &&
                !(
                    /^\d+$/.test(value[value.length - 1]) &&
                    value.length - 1 - value.lastIndexOf('.') >= 4
                );
        }
        return isValid;
    } catch (err) {
        return false;
    }
}

export function emailFormatValidator(control): ValidationErrors | null {
    return isEmailValid(control.value)
        ? null
        : { 'email format': 'email invaild' };
}

export function firstNameFormatValidator(
    control: AbstractControl
): ValidationErrors | null {
    return isNameVaild(control.value)
        ? null
        : { 'first name format': 'first name is english and space' };
}

export function isNameVaild(username: string): boolean {
    let isVaild = false;
    if (username) {
        isVaild = /^[A-Za-z\s]+$/.test(username);
    } else {
        isVaild = true;
    }
    return isVaild;
}

export function lastNameFormatValidator(
    control: AbstractControl
): ValidationErrors | null {
    return isNameVaild(control.value)
        ? null
        : { 'last name format': 'first name is english and space' };
}

export function onlyHasNumber(string: string): Boolean {
    const regExp = /^\d+$/;
    return regExp.test(string);
}

// by country return '852': number-string
export function getCountryCallingCode(country = 'HK'): string {
    let tmp = countryCallingCodes[country];
    if (!tmp) {
        tmp = '852';
    }
    return tmp;
}

export function getPhoneLength(country): number {
    return phoneLength[getCountryCallingCode(country)];
}

export function isUnitNumberFormat(string: string): boolean {
    const regExp = /[^A-Za-z0-9 /-]/;
    let onlyNumberAndLetterAndSpace: boolean;
    onlyNumberAndLetterAndSpace = !regExp.test(string);
    return onlyNumberAndLetterAndSpace;
}

export function asLetter(string: string): Boolean {
    const regExp = /[A-Za-z]/;
    return regExp.test(string);
}

export function hasNumber(string: string): Boolean {
    const regExp = /[0-9]/;

    return regExp.test(string);
}

export function getDateStringLabel(milliseconds: number): string {
    const date = new Date(milliseconds);
    let timeString = date.getFullYear().toString() + '-';
    if (date.getMonth() + 1 <= 9) {
        timeString = timeString + '0' + (date.getMonth() + 1).toString() + '-';
    } else {
        timeString = timeString + (date.getMonth() + 1).toString() + '-';
    }

    if (date.getDate() <= 9) {
        timeString = timeString + '0' + date.getDate().toString();
    } else {
        timeString = timeString + date.getDate().toString();
    }

    return timeString;
}

export function currentTimeString(): string {
    return timeStringLabel(null, true);
}

export function timeStringLabel(
    pickUpTime?: number,
    withSecond: boolean = false
): string {
    let date: Date;
    if (pickUpTime) {
        date = new Date(pickUpTime);
    } else {
        date = new Date();
    }
    let timeString = date.getHours() + ':';
    if (date.getMinutes() === 0) {
        timeString = timeString + '00';
    } else if (date.getMinutes() <= 9) {
        timeString = timeString + '0' + date.getMinutes();
    } else {
        timeString = timeString + date.getMinutes();
    }

    if (withSecond) {
        timeString = timeString + ':';
        if (date.getSeconds() === 0) {
            timeString = timeString + '00';
        } else if (date.getSeconds() <= 9) {
            timeString = timeString + '0' + date.getSeconds();
        } else {
            timeString = timeString + date.getSeconds();
        }
    }
    return timeString;
}

export function getClosedTime(store: Store, time?: number): string {
    let storeCloseTime: string;
    const today: Date = time ? new Date(time) : new Date();
    if (store.openings && store.openings['weekdays']) {
        const weekday = today.getDay();
        storeCloseTime = store.openings['weekdays'][weekday]['endTime'];
    }

    if (!storeCloseTime) {
        return '24:00';
    }

    return storeCloseTime;
}

export function getPickupDelayTime(store: Store): number {
    if (!store) {
        return 0;
    }
    if (!store.pos) {
        return 0;
    }
    return Number(store.pos['pickupDelay']);
}

export function getDeliveryDelayTime(store: Store): number {
    if (!store) {
        return 0;
    }
    if (!store.pos) {
        return 0;
    }
    return Number(store.pos['deliveryDelay']);
}

export function getTodayMillisecondsFromTimeString(
    timeLabel: string,
    time?: number
): number {
    const date = time ? new Date(time) : new Date();
    if (!isNaN(Number(timeLabel.split(':')[0]))) {
        if (timeLabel.split(':').length === 2) {
            date.setHours(
                Number(timeLabel.split(':')[0]),
                Number(timeLabel.split(':')[1]),
                0,
                0
            );
        } else if (timeLabel.split(':').length === 3) {
            date.setHours(
                Number(timeLabel.split(':')[0]),
                Number(timeLabel.split(':')[1]),
                Number(timeLabel.split(':')[2]),
                0
            );
        } else if (timeLabel.split(':').length === 4) {
            date.setHours(
                Number(timeLabel.split(':')[0]),
                Number(timeLabel.split(':')[1]),
                Number(timeLabel.split(':')[2]),
                Number(timeLabel.split(':')[3])
            );
        }
    } else {
        return 0;
    }

    return Number(timeLabel.split(':')[0]) === 0
        ? date.getTime() + 24 * 60 * 60000
        : date.getTime();
}

export function isAvailableOffer(offer: Offer): boolean {
    const current = new Date().getTime();
    const start = offer.start ? offer.start : current;
    const end = offer.expire ? offer.expire : current;

    return start <= current && end >= current;
}

export function isAvailableReward(reward: Reward): boolean {
    const current = new Date().getTime();
    const start = reward.start ? reward.start : current;
    const end = reward.end ? reward.end : current;

    return start <= current && end >= current;
}

