function checkPrestopFromStart(useTime, period): boolean {
    const { prestopFromStart, startTime } =
        (period?.openings?.weekdays &&
            period.openings.weekdays[useTime.getDay()]) ||
        {};
    if (!startTime || !prestopFromStart) {
        return true;
    }

    const time = new Date(useTime);
    const periodStartTime = time.setHours(
        Number(startTime.split(':')[0]),
        Number(startTime.split(':')[1]),
        0,
        0
    );
    if (periodStartTime <= useTime.getTime()) {
        return true;
    } else {
        return (
            periodStartTime - (prestopFromStart || 0) * 60000 >
            useTime.getTime()
        ); // true is available
    }
}

export function getWeekdayStartAndEnd(
    period: Period
): [Date, string, Date, string, boolean] {
    const today: Date = new Date();
    today.setHours(0, 0, 0, 0);
    const weekday = period['savedWeekday'] || today.getDay();
    const weekdays = period.openings.weekdays;
    let start: Date = new Date();
    let end: Date = new Date();
    let startTime: string;
    let endTime: string;
    let isOpen = true;
    if (weekdays[weekday]['startTime'] && weekdays[weekday]['endTime']) {
        startTime = weekdays[weekday]['startTime'];
        endTime = weekdays[weekday]['endTime'];
        isOpen = weekdays[weekday].type === 'open';
    } else {
        startTime = period.startTime;
        endTime = period.endTime;
    }
    start.setHours(
        Number(startTime.split(':')[0]),
        Number(startTime.split(':')[1]),
        0,
        0
    );
    end.setHours(
        Number(endTime.split(':')[0]),
        Number(endTime.split(':')[1]),
        0,
        0
    );
    if (today.getDay() !== weekday) {
        if (end < start) {
            start = new Date(start.getTime() - 24 * 60 * 60000);
        } else {
            start = new Date(start.getTime() - 24 * 60 * 60000);
            end = new Date(end.getTime() - 24 * 60 * 60000);
        }
    } else {
        if (end < start) {
            console.log('stepped over time to next day!!!');
            end = new Date(end.getTime() + 24 * 60 * 60000);
        }
    }

    return [start, startTime, end, endTime, isOpen];
}

export function hasHoliday(
    brand: Brand,
    oh: OpeningHours,
    time = new Date().getTime()
) {
    if (!oh) {
        return null;
    }

    if (oh.overwrites) {
        for (const slot of oh.overwrites) {
            if (Slot.isWithinDayRange(slot, time)) {
                console.log('within overwrites', slot);
                return slot;
            }
        }
    }

    if (brand.openings) {
        if (brand.openings.ph1) {
            for (const slot of brand.openings.ph1) {
                if (Slot.isWithinDayRange(slot, time)) {
                    console.log('within holiday1', slot);
                    return oh.holidays[0];
                }
            }
        }

        if (brand.openings.ph2) {
            for (const slot of brand.openings.ph2) {
                if (Slot.isWithinDayRange(slot, time)) {
                    console.log('within holiday2', slot);
                    return oh.holidays[1];
                }
            }
        }
    }

    return null;
}

export function isPeriodClosed(
    store: Store,
    period: Period,
    currentTime: number
): boolean {
    if (!period) {
        return false;
    } else {
        const preStop = period['prestop'] ? period['prestop'] : 0;

        if (preStop === 1440) {
            return true;
        }
        let endTime = new Date(currentTime); // let the date same with currentTime param. Just need to alter hours later.
        const holidaySlot = hasHoliday(store.brand, period.openings);

        if (holidaySlot) {
            const startTime = new Date(currentTime);
            // endTime.setHours(Number(holidaySlot.endTime.split(":")[0]), Number(holidaySlot.endTime.split(":")[1]));
            startTime.setHours(
                Number(holidaySlot.startTime.split(':')[0]),
                Number(holidaySlot.startTime.split(':')[1]),
                0,
                0
            );
            endTime.setHours(
                Number(holidaySlot.endTime.split(':')[0]),
                Number(holidaySlot.endTime.split(':')[1]),
                0,
                0
            );
            if (endTime <= startTime) {
                // console.log('stepped over time to next day!!!');
                // if (new Date() > endTime) {
                endTime = new Date(endTime.getTime() + 24 * 60 * 60000);
                // }
            }
        } else if (period.openings && period.openings.weekdays) {
            const [dateStart, startString, dateEnd, endString] =
                getWeekdayStartAndEnd(period);
            // keep the date. Make sure the date same with currentTime param
            endTime.setHours(
                dateEnd.getHours(),
                dateEnd.getMinutes(),
                dateEnd.getSeconds(),
                dateEnd.getMilliseconds()
            );
            // endTime = dateEnd;
        } else {
            const startTime = new Date(currentTime);

            startTime.setHours(
                Number(period.startTime.split(':')[0]),
                Number(period.startTime.split(':')[1]),
                0,
                0
            );
            endTime.setHours(
                Number(period.endTime.split(':')[0]),
                Number(period.endTime.split(':')[1]),
                0,
                0
            );
            if (endTime <= startTime) {
                // console.log('stepped over time to next day!!!');
                // if (new Date() > endTime) {
                endTime = new Date(endTime.getTime() + 24 * 60 * 60000);
                // }
            }
        }
        // console.log('endTime:' + (endTime.getTime() - preStop * 60000));
        // console.log('currentTime:' + currentTime);
        return endTime.getTime() - preStop * 60000 < currentTime;
    }
}

export function getTandCUrl(brand: Brand, name: string): string {
    if (!brand.links) {
        return null;
    }
    const link = brand.links[name] as Link;
    if (!link) {
        return null;
    }

    let url = link.url;

    if (!url) {
        return null;
    }

    if (url.indexOf('http://') === 0 && url.indexOf('localhost') === -1) {
        url = url.replace('http:', 'https:');
    }

    return url;
}

export function convertNowDateToStoreTimeZone(timeZone, time?: number): Date {
    timeZone = timeZone ? timeZone : 'GMT+8';
    let timeZoneValue = timeZone.split('GMT')[1];
    // let isPositive = timeZoneValue.startsWith('+');
    let integerTimeZoneValue = parseFloat(timeZoneValue);
    let now = time ? new Date(time) : new Date();
    now.setHours(now.getUTCHours() + integerTimeZoneValue);
    return now;
}


export const maxDate: ValidatorFn = (group: FormGroup): ValidationErrors | null => {
    let days = group.get('dayDate');
    let month = group.get('monthDate');
    const dt = new Date();
    let year = dt.getFullYear();

    let d = days.value;
    let m = month.value;

    let maximumDate;


    const daysInMonth = (m, y) => {
        switch (m) {
            case 1:
                return (y % 4 == 0 && y % 100) || y % 400 == 0 ? 29 : 28;
            case 8:
            case 3:
                return 30;
            case 5:
            case 10:
                return 30;
            default:
                return 31;
        }
    }

    if (m >= 0 && m < 12) {
        maximumDate = daysInMonth(Number(m), year);
    }

    if (days.value < '0' || days.value > maximumDate) {
        return {
            dateMaximum: true
        }
    }

    return null;
};

export function emailValidator(
    control: AbstractControl
): ValidationErrors | null {
    if (!control.value) {
        return null;
    }

    let REGEXP: RegExp;
    REGEXP =
        /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

    return REGEXP.test(control.value) ? null : { email: true };
}


export function isEmptyOrWhiteSpaceValidator(control: AbstractControl): ValidationErrors | null {

    if (isEmptyInputValue(control.value)) {
        return null; // don't validate empty values to allow optional controls
    }

    return isEmptyOrWhiteSpace(control.value.toString()) ? { 'emptyOrWhiteSpaceOnly': true } : null;
}

export function isEmptyOrWhiteSpace(str: string): boolean {
    // return true if the string is empty or only white space
    return (str.match(/^\s*$/) || []).length > 0;
}

export function getSanitizePhone(phone, countryCode): string {
    const regex = new RegExp(countryPhoneReg[countryCode])
    const result = regex.exec(phone);
    return result?.length > 0 ? result[1] : phone;
}

export const parseRegex = (option: Record<string, string>) => {
    let finalRegex: Record<string, RegExp> = {};
    Object.keys(option).forEach((x) => {
        finalRegex = {
            ...finalRegex,
            [x]: new RegExp(option[x]),
        };
        return finalRegex;
    });
    return finalRegex;
}

export const isValidJson = (regex): boolean => {
    try {
        var json = JSON.parse(regex);
        return (typeof json === 'object');
    }
    catch (error) {
        return false;
    }
};


