import { Scan } from '@aigens/aigens-sdk-scan';
import { CameraService } from '@aigens/core/src/app/framework/services/camera.service';
import { Injectable } from '@angular/core';
import { BarcodeScanner, SupportedFormat } from '@capacitor-community/barcode-scanner';
import { Capacitor } from '@capacitor/core';

@Injectable({
    providedIn: 'root'
})
export class NativeCameraService {

    havePermission: boolean = false;

    constructor() {

    }

    public static cameraServiceFactory() {

        var native = Capacitor.isNativePlatform();

        if (native) {
            return new NativeCameraService();
        }

        return new NativeCameraService();

    }

    isAvailable(): boolean {
        return true;
    }

    async checkPermission(callback: Function) {

        // check or request permission
        await BarcodeScanner.checkPermission({ force: true }).then((result) => {

            console.log("checkPermission Result", result);

            if (result.granted) {
                console.log("GRANTED");
                this.havePermission = true;
            }

            callback(result);
        });

    }

    scan(callback: Function, options = {}){

        Scan.scan(options).then((res)=>{

            callback(res);
        });

    }

    startScan(callback: Function, rejectPermissionCallback?): void {

        if(this.havePermission){
            this.scanRaw(callback);
        }else{


            this.checkPermission((permissionResult)=>{

                console.log(permissionResult);

                if(this.havePermission){
                    this.scanRaw(callback);
                }else{
                    rejectPermissionCallback && rejectPermissionCallback();
                }

            });

        }

    }

    stopScan(): void {
        BarcodeScanner.showBackground();
        BarcodeScanner.stopScan();
    }

    private async scanRaw(callback: Function) {

        console.log("Scanning");


        BarcodeScanner.hideBackground(); // make background of WebView transparent

        await BarcodeScanner.startScan({ targetedFormats: [SupportedFormat.QR_CODE] }).then((result) => {

            console.log("Result", result);

            // if the result has content
            if (result.hasContent) {
                console.log(result.content); // log the raw scanned content

                BarcodeScanner.stopScan();

                callback(result);
            }
        });




    }



}
