import { PlatformService } from '@aigens/core/src/app/pure/services/platform.service';
import { RoutingService } from '@aigens/core/src/app/pure/services/routing.service';
import { Location } from '@angular/common';
import { Component, DoCheck, EventEmitter, Injector, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { firstValueFrom } from 'rxjs';
import { SettingManager } from '@aigens/core/src/app/pure/managers/setting-manager';
import { LazyService } from '@aigens/core/src/app/pure/services/lazy.service';


@Component({
    selector: 'app-nav-bar',
    templateUrl: './nav-bar.component.html',
    styleUrls: ['./nav-bar.component.scss']
})
export class NavBarComponent implements OnInit, OnChanges, DoCheck  {

    @Input() title: string = '';
    @Input() menu: boolean = false;
    @Input() home: boolean = false;
    @Input() homeUrl: string;
    @Input() back: boolean = false;
    @Input() language: boolean = false;
    @Input() user: boolean = false;
    @Input() fullWidth: boolean = false;
    @Input() fullBackgroundWidth: boolean = false;
    @Input() hasSignedIn: boolean = false;
    @Input() menuPath: string = 'side/menu';
    @Input() refresh: boolean = false;
    @Input() logo: boolean = true;
    @Input() favorite: boolean = false;

    // for sdk: after paied to orderDetailPage
    @Input() forceShowExit: boolean = false;
    @Input() embedded: boolean = false;
    @Input() homePage = false; // flag to state this navbar is used in home page or not
    @Input() search: boolean = false;
    @Output() refreshClick = new EventEmitter();

    @Input() login = true;

    sdk: boolean = true;
    showExit: boolean = false;

    context: any;

    oldMemberId = null;
    oldMemberGuest = null;

    constructor(private injector: Injector, private router: Router, private location: Location, private lazyService: LazyService,
                private routingService: RoutingService, private route: ActivatedRoute, private platformService: PlatformService , public settingManager: SettingManager ) {
        this.context = window['aigens'].context;
        this.embedded = this.platformService.isEmbedded();
        this.sdk = this.platformService.isSDK();

    }

    ngOnInit(): void {
        this.injectRouteData();

    }

    ngDoCheck(): void {
        if(this.context.member?.id !== this.oldMemberId && this.context.member?.guest !== this.oldMemberGuest){
            this.oldMemberGuest = this.context.member?.guest;
            this.oldMemberId = this.context.member?.id
        }

        var canBack = this.routingService.canBack();

        // console.log("canBack", canBack);
        // console.log('forceShowExit', this.forceShowExit);

        // is sdk or pass forceShowExit = true & missing the backicon
        this.showExit = (this.sdk && !canBack) || (this.forceShowExit && !this.back);
        // console.log('forceShowExit', this.forceShowExit);
    }

    ngOnChanges(changes: SimpleChanges): void {
        this.injectRouteData();
    }

    get languages(): any[] {
        return this.context?.languages ?? this.context?.store?.languages ?? [];
    }

    async injectRouteData(): Promise<any> {

        const routeData = (await firstValueFrom(this.route.data))?.navbarConfig ?? {};

        const injectableFields = ['menu', 'home', 'back', 'language', 'user', 'menuPath'];

        for(let index in injectableFields) {
            const field = injectableFields[index];
            if(field in routeData) {
                this[field] = routeData[field];
            }
        }

    }

    canLogin(){

        if(!this.user) return false;

        if(this.courtHasFeature('login')) {
            return true;
        }

        if (this.context?.brand?.features?.indexOf('login') < 0) {
            return false;
        }

        if(!this?.context?.crm) {
            return false;
        }

        if(this.context?.crm?.['disabled']) {
            return false;
        }

        return true;
    }

    courtHasFeature(feature: string): boolean {
        if (!this.context?.court) {
            return false;
        }
        return this.context?.court?.features?.indexOf(feature) >= 0;
    }

    menuClicked() {
        var extras = {
            //    skipLocationChange: true
        };


        const auxInput: any = {};
        if (this.homePage) {
            auxInput.homePage = true;
        }

        const params = {
            ...this.route.snapshot.queryParams,
            ...this.route.snapshot.params,
        }

        this.routingService.navigateModal(this.menuPath, auxInput, {queryParams: params});
        // this.router.navigate([{outlets: {modal: 'side/menu'}}], {queryParams: this.route.snapshot.queryParams});
    }

    backClicked() {


        this.location.back();
    }

    homeClicked(){
        console.log("going home natively", this.homeUrl);

        if (this.homeUrl) {
            if (this.settingManager.get('keepModeWhenRedirectToHome', false) && this.homeUrl.includes(`mode`)) {
                this.routingService.navigateByUrl(this.homeUrl, window.location.href, {back: null,clearOrder: null});
            } else {
                let mode = null;
                let canKeepModes = ['web', 'catering', 'byod'];
                canKeepModes.forEach((m)=> {
                    if (this.homeUrl.includes(`mode=${m}`)) {
                        mode = m;
                    }
                } )
                this.routingService.navigateByUrl(this.homeUrl, window.location.href, {back: null,clearOrder: null, mode});

            }
           
        } else {
            this.routingService.goHome(window.location.href);
        }
    }

    refreshClicked():void{
        this.refreshClick.emit(true);
    }

    closeClicked(){
        console.log("closing");

        var service = this.lazyService.getService("RoutingService") as any;
        service?.finish();
    }

    checkFavorite() {
         return this.favorite && this.settingManager.get('menu.showFav', false) && this.oldMemberGuest === false;
    }

}
