import { Component, Injector, OnInit, isDevMode } from '@angular/core';
import { Toast } from '@capacitor/toast';
import { BasePage } from 'aigens-framework';
import { NativeAppService } from '../../../../../../merchant/src/app/native/services/native.app.service';
import { NativeCameraService } from '../../../../../../native/src/app/native/services/native-camera.service';
import { NativeNotificationService } from '../../../../../../native/src/app/native/services/native-notification.service';
import { NativeStorageService } from '../../../../../../native/src/app/native/services/native-storage.service';
import { Device } from '@capacitor/device';
import { Camera } from '@capacitor/camera';
import { SelectByKeyboardService } from '../../../../../../core/src/app/framework/directives/select-by-keyboard';
import { merge } from 'lodash-es';
import { Adyen } from '@aigens/aigens-sdk-adyen';
import { SunmiPrinter } from '@aigens/aigens-sdk-sunmi-printer';

