import { Data } from './data';
import { Category } from './category';
import { Benefit } from './benefit';
import { Condition } from './condition';

export class Discount extends Data {
    class: string = "Discount";
    id: string;
    name: string; //discount name
    groupId: number;
    brandId: number;
    brandIds: string[];
    storeId: number;
    type: string; //discount type
    enabled: boolean;
    start: Date; //available time
    end: Date; //available time
    conditions: Condition[]; //discount conditions, support multiple(>1) conditions
    benefits: Benefit[]; //discount detail, support merely one benefit
    amount: number; //discount total amount
    backendId: string; //discount backend ID
    quantity: number; //
    desc: string; //display name
    available: number = 0; //used times
    level: string; //discount level
    unlocks: Category[]; //Categories can be unlocked
    gifts: Category[]; //Free gifts
    alone: boolean = false; //if must be 1st discount
    backendType: string;
    periods: string[];
    group: string;
    priority: number;
    overpay: number;
    allowZeroTotal: boolean;


    protected toProperty(name: string, value) {

        if (name == 'conditions') {
            return Data.toDataArray(Condition, value);
        } else if (name == 'benefits') {
            return Data.toDataArray(Benefit, value);
        } else if (name == 'unlocks') {
            return Data.toDataArray(Category, value);
        }

        return value;
    }

    static toDiscountData(discount:Discount):any{
        var json ={};
        json["id"] = discount.id;
        return json;

    }
}
