
import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'subStoreName',
    pure: true
})
export class SubStoreNameByIdPipe implements PipeTransform {
    transform(subStoreId: any, lang: string = "en", field: string = "name"): any {
        // console.log("asdasd123");


        let storeName = "";
        if (!subStoreId) { return "" }
        let store = window?.['aigens']?.context?.court?.stores?.find(x => x.id == subStoreId);
        if (store) {

            //var lang = window['aigens']?.context?.userLang;
            //console.log("i18n pipe", content, field,  lang);

            if (lang && lang != 'en') {
                storeName = store[field + "_" + lang];
            } else {
                storeName = store.name;
            }

        }

        return storeName;
    }
}
