import { Pipe, PipeTransform } from '@angular/core';
import { Context } from '../services/context.service';
import { CurrencyPipe } from '@angular/common';

@Pipe({
    name: 'pointToCash'
})
export class PointToCashPipe implements PipeTransform {

    redeemPointFactor: number = 1;
    smallestPointsUnit: number = 1;
    pointRatio: number = 1;

    pointsDigitsInfo: string;

    constructor(private context: Context, private currencyPipe: CurrencyPipe) {
        this.initCrmPoint();
        this.calculateDisplayScale();
    }

    private initCrmPoint(): void {
        this.redeemPointFactor = this.context?.membership?.['redeemPointFactor'] ?? this.context?.crm?.redeemPointFactor ?? 1;
        this.smallestPointsUnit = this.context?.membership?.['smallestPointsUnit'] ?? this.context?.crm?.smallestPointsUnit ?? 1;
        this.pointRatio = this.context?.membership?.['pointToMoneyRatio'] ?? this.context?.crm?.pointToMoneyRatio ?? 1;
        if (this.smallestPointsUnit <= 0) {
            this.smallestPointsUnit = 1;
        }
    }

    private convertPointToAmount(points) {
        return (points / this.smallestPointsUnit) / this.pointRatio;
    }

    private calculateDisplayScale(): void {
        // take log10 to get the digits in zero: log10(1) = 0, log10(100) = 2
        const log10 = Math.log10 || function(x) {
            return Math.log(x) * Math.LOG10E;
        };

        const digits = log10(this.smallestPointsUnit);

        this.pointsDigitsInfo = `1.${digits}-${digits}`;
    }

    private roundToPointFactor(points): number {
        const redeemPointFactor = this.redeemPointFactor ?? 1;
        return Math.floor(points / redeemPointFactor) * redeemPointFactor;
    }

    transform(points: number): string {
        const roundedPoint = this.roundToPointFactor(points);
        const finalAmount = this.convertPointToAmount(roundedPoint);
        return this.currencyPipe.transform(finalAmount,
            this.context.currency, 'symbol-narrow',
            this.pointsDigitsInfo);
    }

}
