    makeViewItemsJo(cat: Category, ig: ItemGroup) {

        const itemJos = [];

        for (const item of ig.items) {
            itemJos.push(this.makeViewItemJo(cat, ig, item));
        }

        return itemJos;
    }

    makeViewItemJo(cat: Category, ig: ItemGroup, item: Item) {

        const itemId = item.sku || item.backendId || item.id;
        const igId = ig.backendId || ig.id;
        const catId = cat.backendId || cat.id;

        let igPos = 0;
        if (item.igPos >= 0) { igPos = item.igPos; }

        const jo = {
            item_id: itemId,
            item_name: item.name,
            category_name: cat.name,
            category_id: catId,
            // item_category: catId,
            // item_category_name: cat.name,
            item_list_name: ig.name,
            item_list_id: igId,
            index: igPos
        };

        return jo;
    }

    makeItemJo(cat: Category, ig: ItemGroup, item: Item, includeAddon: boolean = true) {

        const itemId = item.sku || item.backendId || item.id;
        const igId = ig.backendId || ig.id;
        const catId = cat.backendId || cat.id;

        const q = item.quantity > 0 ? item.quantity : 1;

        let igPos = 0;
        if (item.igPos >= 0) { igPos = item.igPos; }

        let addon = {};
        if (includeAddon) {
            addon = {
                addon1: 'N/A',
                addon2: 'N/A',
                addon3: 'N/A',
                addon4: 'N/A',
                ...this.makeModifierItemJo(item)
            };
        }

        const jo = {
            item_id: itemId,
            item_name: item.name,
            category_name: cat.name,
            category_id: catId,
            // item_category: catId,
            // item_category_name: cat.name,
            item_list_name: ig.name,
            item_list_id: igId,
            price: item.price,
            currency: this.context.currency,
            quantity: q,
            index: igPos,
            ...addon
        };

        return jo;
    }

    makeModifierItemJo(item: Item): any {
        console.log('makeModifierItemJo', item.mgroups);
        if (!item.mgroups || item.mgroups?.length <= 0) {
            return {};
        }
        const addons = {};
        item.mgroups[0]?.items?.forEach((modifier, index) => {
            addons['addon' + (index + 1)] = modifier.name;
        });
        return addons;
    }

