    makeOrderJo(order: Order) {
        const jo = {
            currency: this.context.currency,
            value: order.total
        } as any;

        const items = [];

        for (const oi of order.orderitems) {

            const oiJo = this.makeOrderItemJo(oi);
            items.push(...oiJo.items);
        }

        jo.items = items;
        return jo;
    }


