import { Data } from './data';

export class Geo extends Data {
    id: number;
    admin1: string;
    admin2: string;
    admin3: string;
    admin4: string;
    name: string;
    fclass: string;
    latitude: number;
    longitude: number;
    fcode: string;
    country: string;
    subs: Geo[];

    protected toProperty(name: string, value) {

        if (name == "subs") {
            return Data.toDataArray(Geo, value);
        }

        return super.toProperty(name, value);
    }
}