import { Brand } from './brand';
import { Data } from './data';

export class Group extends Data {
    class: string = "Group";
    id: number;
    name: string;
    images: any;
    brands: Brand[];

    protected toProperty(name: string, value) {

        if (name == "brands") {
            return Data.toDataArray(Brand, value);
        }

        return super.toProperty(name, value);
    }
}