import { Data } from './data';

export class Member extends Data {

    class: string = "Member";
    id: number;
    sessionId: string;
    username: string;
    email: string;
    regions: string[];
    avatarUrl: string;
    language: string;
    name: string;
    gender: string;
    education:string;
    occupation:string;
    firstName: string;
    lastName: string;
    groupId: number;
    brandId: number;
    storeId: number;
    courtId: number;
    admin: boolean = false;
    readOnly: boolean = false;
    countryCode: string;
    phone: string;
    backendId: string;
    type:string;
    sessionTimeout: number = 0;
    birthdate: number;
    referral: any;
    marketing: boolean = false;
    guest: boolean = false;
    cardNo: string;
    extra: any;
    behavior: any;
    membership: any;
    tier: string;
    allowereceipt: boolean;
    method: string;
    livingDistrict: string;
    workingDistrict: string;
    featureLevel: string;
    standardRoles: string[];
    facebookId: string;
    bday: number;
    bmonth: number;
    gm: {
        programs?: string[];
        id?: string | number;
    };

    ecredit: any;

    static toMemberData(member:Member):any{
        var json ={};
        json["id"] = member.id;
        json["sessionId"] = member.sessionId;
        json["username"] = member.username;
        json["storeId"] = member.storeId;
        json["type"] = member.type;
        json["regions"] = member.regions;
        json["language"] = member.language;
        json["name"] = member.name;
        json["gender"] = member.gender;
        json["education"] = member.education;
        json["occupation"] = member.occupation;
        json["firstName"] = member.firstName;
        json["lastName"] = member.lastName;
        json["groupId"] = member.groupId;
        json["brandId"] = member.brandId;
        json["storeId"] = member.storeId;
        json["phone"] = member.phone;
        json["backendId"] = member.backendId;
        json["birthdate"] = member.birthdate;
        json["marketing"] = member.marketing;
        json["cardNo"] = member.cardNo;
        json["membership"] = member.membership;
        json["tier"] = member.tier;
        json["allowereceipt"] = member.allowereceipt;
        json['ecredit'] = member.ecredit;

        json["gm"] = member.gm;

        return json;

    }

    static toOrderMemberData(member: Member): any {
        var json = {};
        json['firstName'] = member.firstName;
        json['lastName'] = member.lastName;
    }
}
