import { Injectable } from '@angular/core';
import { PlatformService } from './platform.service';
import { SubscribeService } from './subscribe.service';

export interface NavigationMenuItem {
    /**
     * The translation key of the menu item
     */
    title: string;
    /**
     * The url of route, can be external or internal link
     */
    route?: string;
    /**
     * The name of external link defined at brand.link
     */
    link?: string;
    /**
     * Select which mode in URL to display
     */
    modes?: string[];
    /**
     * Do determin if mode is required to be present in the page
     */
    requireMode?: boolean;
    /**
     * Do deside which channel to display
     */
    channels?: string[];
    /**
     * Icon
     */
    icon?: {
        name: string;
        src?: 'mat' | 'custom';
    };
    /**
     * Icon when the route is active
     */
    activeIcon?: {
        name: string;
        src?: 'mat' | 'custom';
    };
    /**
     * To determin if the currect url match with the item
     */
    match?: string | RegExp;
    /**
     * To determin if the sdk url match with the item
     */
     sdkMatch?: string | RegExp;
    /**
     * To determin if the link is member only, if not, redirect to login page
     */
    member?: boolean;
    /**
     * To determin if displaying the item to member or guest
     */
    displayUserGroups?: ('guest' | 'member')[];
    /**
     * To set if this menu item point to home
     */
    isHome?: boolean;
    /**
     * If set to true, only display when crm is enabled
     */
    crmEnabled?: boolean;
    /**
     * Insert back=true in query param when navigation
     */
    back?: boolean;
    /**
     * Nested navigation, only works in side menu
     */
    children?: NavigationMenuItem[];
    /**
     * To filter item by brand feature or crm feature
     */
    features?: string[];
    /**
     * Route state to pass when navigation
     */
    state?: any;
    /**
     * To determin how to display the item
     */
     displayType?: string;
}

