@Injectable({
    providedIn: 'root'
})
export class NavigationMenuService {

    constructor(private context: SubscribeService, private platformService: PlatformService) {}

    private isMatchChannel(item: NavigationMenuItem, chennel: string): boolean {
        if (!item.channels || item.channels?.length <= 0) {
            return true;
        }
        return item.channels.indexOf(chennel) >= 0;
    }

    private patchTabs(items: NavigationMenuItem[]): NavigationMenuItem[] {
        const newItems: NavigationMenuItem[] = [...items];
        newItems.forEach((item) => {
            if (item.route) {
                item.route = this.patchRoute(item);
            }

            if(item.children?.length > 0) {
                item.children = this.patchTabs(item.children);
            }
        });
        return newItems;
    }

    private patchRoute(item): string {
        return item.route.replace(/@brandId/g, '' + this.context?.['brandId']).replace(/@groupId/g, '' + this.context?.['groupId']).replace(/@courtId/g, '' + this.context?.['courtId']);
    }

    private paramsToObject(url = ''): any {
        try {
            const paramsString = url.match(/\?\S+=\S+/g) ?? '';

            if (paramsString.length <= 0) {
                return {};
            }

            const entries = new URLSearchParams(paramsString[0]?.slice(1)) as any;

            const result = {};
            for (const [key, value] of entries) { // each 'entry' is a [key, value] tupple
                result[key] = value;
            }
            return result;
        } catch (e) {
            return {};
        }
    }

    public getNavParams(url: string = '', key: string = ''): any {
        const queryParams = this.paramsToObject(url);
        if (key in queryParams) {
            return queryParams[key];
        }

        const splited = url.replace(/\?\S+=\S+/g, '').split('/');
        const keyIndex = splited.indexOf(key);

        if (keyIndex < 0 || keyIndex >= splited.length) {
            return null;
        }

        return splited[keyIndex + 1] ?? null;
    }

    public getTabbarItems(channel: string): NavigationMenuItem[] {
        const nav = this.getDeviceConfig('nav');
        const tabbar = nav?.tabbar ?? DEFAULT_TABBAR;
        if(!tabbar) {
            return [];
        }

        const items = tabbar.filter(item => this.isMatchChannel(item, channel));
        return this.patchTabs(items);
    }

    public getSideMenuItems(channel: string): NavigationMenuItem[] {
        const nav = this.getDeviceConfig('nav');
        const sideMenu = nav?.['side-menu'] ?? DEFAULT_SIDE_MENU;
        if (!sideMenu) {
            return [];
        }
        const items = sideMenu.filter(item => this.isMatchChannel(item, channel));
        return this.patchTabs(items);
    }

    public getSideMenuStickyItems(channel: string): NavigationMenuItem[] {
        const nav = this.getDeviceConfig('nav');
        const sideMenu = nav?.['side-menu-sticky'] ?? DEFAULT_SIDE_MENU_STICKY;
        if (!sideMenu) {
            return [];
        }
        const items = sideMenu.filter(item => this.isMatchChannel(item, channel));
        return this.patchTabs(items);
    }

    public getHomeItems(channel: string): NavigationMenuItem[] {
        return this.patchTabs([]);
    }

    public matchUserGroups(item: NavigationMenuItem, isMember: boolean = false): boolean {
        if (!item.displayUserGroups || item.displayUserGroups.length < 0) {
            return true;
        }
        if (item.displayUserGroups.indexOf('guest') >= 0 && !isMember) {
            return true;
        }
        if (item.displayUserGroups.indexOf('member') >= 0 && isMember) {
            return true;
        }
        return false;
    }

    public shouldDisplay(item: NavigationMenuItem, isMember: boolean = false, currentUrl: string = ''): boolean {
        if (!this.matchUserGroups(item, isMember)) {
            return false;
        }

        const currentMode = this.getNavParams(currentUrl, 'mode');
        if (!currentMode && item.requireMode) {
            return false;
        }

        if (currentMode && item.modes?.length > 0) {
            if (item.modes.indexOf(currentMode) < 0) {
                return false;
            }
        }

        if (item.crmEnabled && !this.crmAvailable(currentUrl)) {
            return false;
        }

        if (!this.checkFeatures(item)) {
            return false;
        }

        if (!item.route) {
            if (item.link) {
                if(!(item.link in this.context['brand']?.['links'])) {
                    return false;
                }
            } else if (!item?.children || item?.children?.length <= 0) {
                return false;
            }
        }

        return true;
    }

    public getUrl(item: NavigationMenuItem): string {
        let url = item?.route;
        if (item.link) {
            url = this.context['brand']?.['links']?.[item.link]?.['url'];
        }
        if (!url) {
            console.error('URL not found');
            return '';
        }
        return url;
    }

    public getHomePath(channel: string): string {
        let menuItems = this.getSideMenuItems(channel);
        let homeItem = menuItems.find((item) => {
            return item?.isHome;
        });

        if(homeItem) {
            return homeItem?.route;
        }

        menuItems = this.getTabbarItems(channel);
        homeItem = menuItems.find((item) => {
            return item?.isHome;
        });

        return homeItem?.route ?? null;
    }

    public hasRouteMatch(item: NavigationMenuItem, currentUrl: string = ''): boolean {
        if (item.match instanceof RegExp) {
            return !!currentUrl.match(item.match);
        }
        if (this.platformService.isSDK() && item.sdkMatch) {
            if (item.sdkMatch instanceof RegExp) {
                return !!currentUrl.match(item.sdkMatch);
            }
            const sdkMatches = (item.sdkMatch).split(',');
            return sdkMatches.some(match => {
                return currentUrl.indexOf(match) >= 0;
            })
        }
        const matches = (item?.match || "").split(',');
        return item?.match && matches.some(match => {
            return currentUrl.indexOf(match) >= 0;
        })
        // return currentUrl.indexOf(item.match) >= 0;
    }

    public isRoot(currentUrl: string, channel: string): boolean {
        const items = this.getTabbarItems(channel);
        const back = this.getNavParams(currentUrl, 'back');
        return !back && items.some((item) => {
            return this.hasRouteMatch(item, currentUrl);
        });
    }

    public isRequireLogin(item: NavigationMenuItem, isMember: boolean = false): boolean {
        if (isMember) {
            return false;
        }
        return item?.member ?? false;
    }

    private crmAvailable(currentUrl: string = ''): boolean {

        if (this.context['brand']?.features?.indexOf('login') < 0) {
            return false;
        }

        const crm = this.context['crm'];
        if (!crm) { return false; }

        if (crm?.['disabled']) {
            return false;
        }

        if (crm.modes == null || crm.modes.length === 0) { return true; }

        const mode = this.getNavParams(currentUrl, 'mode');

        if (!mode) {
            return true;
        }

        return crm.modes.indexOf(mode) >= 0;

    }

    private checkFeatures(item: NavigationMenuItem): boolean {
        if (!item.features || item.features?.length <= 0) {
            return true;
        }

        const brandFeature = [];
        const crmFeatures = [];

        item.features.forEach((featureItem) => {
            const [key, name] = featureItem.split(':');
            if (key === 'brand') {
                brandFeature.push(name);
            } else if (key === 'crm') {
                crmFeatures.push(name);
            }
        });

        const brandMatch = brandFeature?.length <= 0 || brandFeature?.every((feature) => {
            return this.context['brand']?.features?.indexOf(feature) >= 0;
        });
        if (!brandMatch) {
            return false;
        }

        const crmMatch = crmFeatures?.length <= 0 || crmFeatures?.every((feature) => {
            return this.context['crm']?.features?.indexOf(feature) >= 0;
        });
        if (!crmMatch) {
            return false;
        }

        return true;
    }

    private getDeviceConfig(key: string): any {
        return this.context?.['config']?.data?.[key] ?? null;
    }
}
