import { DatePipe } from '@angular/common';
import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'dateLocale'
})
export class DateLocalePipe implements PipeTransform {

    localeMap = {
        'zh': 'zh',
        'zh-cn': 'zh'
    }

    transform(value: any, locale: string = 'en', pattern: string = 'mediumDate', timezone = null): any {
        const code = this.localeMap[locale] ?? 'en';
        const datePipe: DatePipe = new DatePipe(code, timezone );

        return datePipe.transform(value, pattern) != undefined ? datePipe.transform(value, pattern) : null;
      }

}
