import * as moment from 'moment';
import { Ad } from './ad';
import { Crm } from './crm';
import { Data } from './data';
import { Link } from './link';
import { OpeningHours } from './opening-hours';
import { Store } from './store';

export class Brand extends Data {
    class: string = "Brand";
    id: number;
    name: string;
    images: any;
    features: string[] = [];
    colors: {};
    groupId: number;
    handle: string;
    theme: string;
    themes: any = {};
    stores: Store[];
    ads: Ad[];
    extra: any = {};
    printName: string;
    openings: OpeningHours;
    links: { string: Link };
    payments: any[] = [];
    gatag: string;
    ganame: string;
    crmId: string;
    country: string;
    crm: Crm;
    timezone: string;
    orderModeSelection: string;
    modes: string[];
    pmsId: number;
    verificationGroups: any;



    protected toProperty(name: string, value) {

        if (name == "stores") {
            return Data.toDataArray(Store, value);
        } else if (name == "ads") {
            return Data.toDataArray(Ad, value);
        } else if (name == "crm") {
            return Data.toData(Crm, value);
        }
        return value;
    }

    public static hasFeature(brand: Brand, feature: string): boolean {
        if (brand?.features) {
            return brand.features.indexOf(feature) >= 0;
        }
        return false;
    }

    public static getBusiness(store: Store, time: Date) {

        var offset = 8;

        if (store.timezone) {
            offset = Number.parseInt(store.timezone.replace("GMT", ""));
        }

        return moment(time.getTime()).utcOffset(offset).format('YYYY-MM-DD');
    }
}
