import { Component, ContentChild, Input, OnInit, TemplateRef } from '@angular/core';

@Component({
  selector: 'app-row-list',
  templateUrl: './row-list.component.html',
  styleUrls: ['./row-list.component.scss']
})
export class RowListComponent implements OnInit {

    @ContentChild(TemplateRef) templateVariable: TemplateRef<any>;

    @Input('contents') contents: any[] = [];
    @Input('hideAfter') hideAfter: number = 1;
    @Input('flexDirection') flexDirection: string = "column";


    showAll: boolean = false;

    constructor() { }

    ngOnInit(): void {
    }

    toggle(){

        this.showAll = !this.showAll;

    }

}
