import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'sortBy'
})
export class SortByPipe implements PipeTransform {

  constructor() {
  }

  transform(input: any[]): any[] {
    if (!input) {
      return [];
    } else if (input.length < 1) {
      return input;
    } else {
      return input.sort((a, b) => {
        return a['name'] > b['name'] ? 1 : -1;
      });
    }
  }
}

