import { Injectable } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

@Injectable()
export class PageParams {


    params = {};
    querys = {};
    input = {};

    route: ActivatedRoute;

    constructor() {

    }

    set(params: any) {
        this.params = params;
    }

    get(name: string) {

        var result = null;

        if (this.input) {
            result = this.input?.[name];
        }

        if (this.route) {

            //console.log('get', name);
            //console.log('route', this.route);

            var snapshot = this.route?.snapshot;

            if (!result) {
                result = snapshot?.params?.[name];
            }

            //checking parent route as well
            if (!result) {
                result = snapshot?.parent?.params?.[name];
            }

            if (!result) {
                result = snapshot?.queryParams?.[name];
            }

            var hash = snapshot?.fragment;

            if(!result && hash){

                try{
                    console.log("HASH", hash);
                    const urlParams = new URLSearchParams(hash);
                    result = urlParams.get(name);
                }catch(e){
                    console.log(e);
                }

            }


        } else {
            if (!result) {
                result = this.params?.[name];
            }
            if (!result) {
                result = this.querys?.[name];
            }
        }

        if (!result) {
            result = history?.state?.[name];
        }

        return result;
    }

}
