import { Injectable, Injector } from '@angular/core';
import { lastValueFrom } from 'rxjs';
import { BaseContext } from '../base/base-context';


@Injectable({
    providedIn: 'root'
})
export class PreOrderContext extends BaseContext {
    order: any;
    orderId: any;
    data: any;
    storeId: any;
    hasPreOrderContext = true;

    constructor(private injector: Injector) {
        super(injector);

        console.log('constructing PRE order context, this should only happen once');

        this.initFromContext();
    }


    initFromContext() {

        var aigens = window['aigens'];
        if (aigens && aigens.context) {
            var context = aigens.context;
            this.contextId = this.makeGenericKey(context, 'preorder', context.locale);
        }

        this.restore();
    }

    clear() {
        this.storeId = null;
        this.data = null;
        this.order = null;
    }

    makeSaveData() {
        return this.data;
    }

    protected async restoreSavedData(data: any) {

        console.log('PreorderContext restoreSavedData', data);
        if (data.order) {
            this.order = data.order;
        }
        if (this.order === undefined && data.uuid) {
            this.orderId = data?.uuid;
            await lastValueFrom(this.getOrder()).then((x) => {
                this.order = x
                return x;
            })
            this.next('order', this.order)
        }
        console.log('restored PRE order', this.order);


        this.storeId = data?.storeId || this.order?.storeId;
    }

    getOrder() {
        const url = `/api/v1/menu/order/${this.orderId}.json`
        let param = {};
        param['locale'] = 'multi';
        return this.dataService.get(url, param)
    }
}

