    openBrowser(options: any) {

        var url = options.url;
        if (!url) return;

        //this.navigate(url);
        window.open(url, '_blank').focus();

    }

    mark() {

        return history.length;


    }

    backTo(marker) {


        var diff = history.length - marker;
        console.log('history diff', diff);

        if (diff < 0) {
            return;
        }

        history.go(-diff);

    }


