import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'textEllipsisPipe'
})
export class TextEllipsisPipe implements PipeTransform {
    constructor() {
    }

    transform(text, maxCharacters: number = 20, ellipsis: string = '...'): string {
        if (!text) {
            return '';
        }
        return text.length > maxCharacters ? text.substring(0, maxCharacters) + ellipsis : text;
    }
}
