import { Item, ItemGroup, MathUtils, OrderItem, Store } from '../../data';
import { OrderItemBuilder } from './orderitem-builder';

/**
 * A temporary modifier group manager
 * **/

export class ModifierBuilder {

    store: Store;
    orderitem: OrderItem;
    items: Item[] = null;
    /*

{
    "itemId":[

        {
            id: "mgroupId"

            items: []

        },

        {
            id: "mgroupId"
            items:[]
        }
    ]

}

    */


    private mods: any = {};
    private qmap: any = {};
    private pmap: any = {};

    private mmap: any = {}; // modifier item map

    constructor(store: Store, orderitem: OrderItem, items: Item[] = null) {
        this.store = store;
        this.orderitem = orderitem;

        this.addExisting();

        var allItems = this.findItems(orderitem);

        if (items) {
            this.items = allItems.filter(i => items.find(ii => ii.id === i.id) != null);
        } else {
            this.items = allItems;

        }
    }


    clear() {
        this.mods = {};
        this.qmap = {};
        this.pmap = {};
    }

    /**
     * Clear all modifiers selected for specific item and group
     * @param item
     */
    clearForItem(item: Item, group: ItemGroup) {
        /* this.mods[item.id] = null;
        Object.keys(this.qmap).filter(key => this.isKeyForItem(key, item)).forEach(key => {
            this.qmap[key] = null;
        }) */
        //
        let mgroups: ItemGroup[] = this.mods[this.createModifierMapKey(item)];

        let mgroup: ItemGroup = this.findById(mgroups, group.id);
        // let index: number = mgroups.indexOf(mgroup);
        if (!mgroup)
            return;

        if (mgroups) {
            let index: number = mgroups.indexOf(mgroup);
            mgroups.splice(index, 1);
        }

        mgroup.items.forEach(mod => {
            let qkey: string = this.createQuantityMapKey(item, mod, group);
            this.qmap[qkey] = 0;
            this.pmap[qkey] = 0;
        });
    }


    transfer(oi: OrderItem) {
        if (!oi || !oi.groups) {
            return;
        }

        oi.groups.forEach(group => {

            let items: Item[] = group.items;

            items
                .filter(item => item.mgroups && item.mgroups.length > 0)
                .forEach(item => {

                    let mgroups: ItemGroup[] = item.mgroups;

                    mgroups.forEach(mgroup => {

                        let mitems: Item[] = mgroup.items;
                        mitems.forEach(mitem => {

                            this.setMod(item, mgroup, mitem, mitem.quantity);

                        });

                    });

                });

        });
    }


    addExisting() {

        var items = OrderItem.getFlattenItems(this.orderitem, false);

        for (let item of items) {
            var mgroups = item.mgroups;
            if (mgroups && mgroups.length > 0) {

                for (let mgroup of mgroups) {

                    var mods = mgroup.items;

                    if (mods && mods.length > 0) {

                        for (let mod of mods) {
                            mod?.mgroup && (this.mmap[mod.mgroup] = mod);
                            this.setMod(item, mgroup, mod, mod.quantity, true);
                        }
                    }


                }

            } else {
                //  item mod( mitem.currentQty == 0 ) , so  auto add absentItem logic(mitem level)
                const origin: Item = this?.store?.menu?.['imap'][item?.id];
                if (origin?.mgroups) {
                    origin?.mgroups?.map(mg => mg?.items?.map(mItem => {
                        if (mItem?.baseQty === 0 && mItem?.absentId /*&& q === 0*/) { //  baseQty === 0 absentabsent mdBuidler
                            let absentItem: Item = this.findAbsentItemByAbsentid(mItem, mg?.items);
                            console.log('when new ModifierBuilder() auto add absentItem', absentItem?.name);
                            absentItem && this.plus(item, mg, absentItem);
                        }
                    }));
                }
            }

        }
    }

    private getQty(quantity: number, mod: Item, ignoreBaseQty: boolean = false): number {

        let fullmod = this.findItem(mod);

        if (!fullmod) return quantity;

        if (quantity == 0 && fullmod.absentId) return -1;

        if (!ignoreBaseQty) {
            if (fullmod.baseQty)
                quantity -= fullmod.baseQty;
        }

        if (quantity < 0) quantity = 0;

        return quantity;
    }

    minus(item: Item, group: ItemGroup, mItem: Item) {


        var q = this.getQuantity(item, group, mItem);

        if (q > mItem.min) {
            q--;
        }

        if (mItem?.absentId && q === 0) { //  mitem 0absentabsent mdBuidler
            // add the absentItem mod
            let absentItem: Item = this.findAbsentItemByAbsentid(mItem, group?.items);
            console.log('ModifierBuilder add absentItem', absentItem?.name);
            absentItem && this.plus(item, group, absentItem);
        }

        this.setMod(item, group, mItem, q);
    }

    plus(item: Item, group: ItemGroup, mItem: Item) {

        var q = this.getQuantity(item, group, mItem);
        const max = mItem.max || group.max;

        if (max > q) {
            q++;
        }

        console.log('ModifierBuilder plus mItem', mItem?.name, q);

        if (mItem?.absentId) {
            // clear the absentItem mod
            let absentItem: Item = this.findAbsentItemByAbsentid(mItem, group?.items);
            console.log('ModifierBuilder clear absentItem', absentItem?.name);
            absentItem && this.setMod(item, group, absentItem, 0);
        }

        this.setMod(item, group, mItem, q);

    }

    // find absent item
    findAbsentItemByAbsentid(mitem: Item, items: Item[] = []): Item {
        let a =items?.find(i => i?.id === mitem?.absentId);
        return a || items?.find(i => mitem?.id === i?.absentId);
    }


    setMod(item: Item, mgroup: ItemGroup, mod: Item, quantity: number, addExisting: boolean = false) {

        //var mgroups = this.mods[item.id];
        var mgroups = this.mods[this.createModifierMapKey(item)];
        if (!mgroups) {
            mgroups = [];
            //this.mods[item.id] = mgroups;
            this.mods[this.createModifierMapKey(item)] = mgroups;
        }

        var mg = this.findById(mgroups, mgroup.id); //TODO
        if (!mg) {
            //mg = this.cloneIG(mgroup);
            mg = OrderItemBuilder.cloneIG(mgroup);
            mgroups.push(mg);
        }

        var mods = mg.items;
        var existing = this.findById(mods, mod.id);

        if (!existing) {
            //existing = this.cloneMod(mod);
            existing = OrderItemBuilder.cloneItem(mod)

            mods.push(existing);
        }

        let qty = this.getQty(quantity, mod, true); //  getQuantity() baseQty logic, so now hardcode true

        console.log("qty", qty);

        /*
        if (quantity <= 0 && mod.baseQty <= 0) {
            this.removeObject(existing, mods);
        }*/

        let remove: boolean = false;

        if (qty == 0) {
            this.removeObject(existing, mods);
            remove = true;
            console.log("remove 0 qty mod");
        }

        if (qty == -1) {
            qty = 0;//need to route to absent item
        }

        existing.quantity = qty;

        if (mods.length == 0) {
            this.removeObject(mg, mgroups);
        }


        var qkey = this.createQuantityMapKey(item, mod, mgroup);

        if (!remove) {
            this.qmap[qkey] = qty;
            if (!!mod.baseQty) {
                if ((qty - mod?.baseQty) > 0) { qty = (qty - mod?.baseQty); }
                else { qty = 0; }
            }
            this.pmap[qkey] = MathUtils.round(mod.price * qty, 2);
        }
        else {
            delete this.qmap[qkey];
            delete this.pmap[qkey];
        }

        //console.log("qmap", this.qmap);
        //console.log("pmap", this.pmap);

    }

    getPrice(item: Item, group: ItemGroup, mItem: Item): number {

        let qty = this.getQuantity(item, group, mItem);

        if (mItem.baseQty && qty > mItem.baseQty)
            return mItem.price;
        else
            return 0;
    }

    getTotal(): number {

        let total: number = 0;

        Object.keys(this.pmap).forEach(p => {

            total += this.pmap[p];


        });

        total = MathUtils.round(total, 2);
        return total;
    }

    private createQuantityMapKey(item: Item, mod: Item, mgroup: ItemGroup): string {
        return item.addTime + '.' + item.id + '.' + mod.id + '.' + mgroup.id;
    }

    private createModifierMapKey(item: Item): string {
        return item.addTime + '.' + item.id;
    }

    getQuantity(item: Item, mgroup: ItemGroup, mod: Item) {
        var qkey = this.createQuantityMapKey(item, mod, mgroup);
        var q = this.qmap[qkey];
        if (q == null || isNaN(q)) {
            if (mod.baseQty)
                q = mod.baseQty;
            else
            q = 0;
        } else {
            // if (q > 0 && mod.baseQty)
            //     q += mod.baseQty;
        }

        return q;
    }

    getSelectedModifierCount(item: Item): number {
        if (!this.qmap) {
            return 0;
        }

        let count: number = 0;
        Object.keys(this.qmap)
            .filter(key => this.isKeyForItem(key, item))
            .forEach(key => {
                let quantity: number = this.qmap[key];
                if (quantity != null && !isNaN(quantity) && quantity > 0) {
                    count += quantity;
                }
            });

        return count;
    }

    private isKeyForItem(key: string, item: Item): boolean {
        let parts: string[] = key.split(".");
        let itemId: string = null;
        /*if (parts.length == 2) {
            itemId = parts[0];
        }*/
        let index: string;
        if (parts.length == 3) {
            itemId = parts[1];
            index = parts[0];
        }

        //return (itemId != null && itemId == item.id);
        return (itemId != null && itemId == item.id && Number(index) == item.addTime);
    }

    build(): OrderItem {

        var items = OrderItem.getFlattenItems(this.orderitem, false);

        for (let item of items) {

            //var mgroups = this.mods[item.id];
            var mgroups = this.mods[this.createModifierMapKey(item)];
            if (mgroups && mgroups.length > 0) {
                item.mgroups = mgroups;

            } else {
                item.mgroups = null;
            }
        }

        // replace item by using absentItem
        // ModifierBuilder.build(this.orderitem, this.store?.menu?.['imap']);

        return this.orderitem;

    }

    private static build(orderitem: OrderItem, imap: any): OrderItem {

        if (!imap) imap = {};

        orderitem.items.forEach(i => {

            let mgroups = i.mgroups;

            if (mgroups && mgroups.length > 0) {

                mgroups.forEach(mgroup => {

                    let newItems: Item[] = [];
                    let original = mgroup.items;

                    original.forEach(mitem => {

                        let fullItem: Item = imap[mitem.id];

                        if (mitem?.quantity == 0 && fullItem?.absentId && mitem?.baseQty) {

                            let absent: Item = orderitem?.items?.find(i => i?.id === fullItem?.absentId); // orderitem item   absent item

                            if (absent) { // 
                                absent.quantity = 1; // now absentItem quality only will be one ;todo: handle absentItem quanlity?

                            } else {
                                absent = imap[fullItem.absentId];
                                if (absent) {

                                    console.log("replace with absent item", mitem.backendId + "=>" + absent.backendId);

                                    let absentItem = OrderItemBuilder.cloneItem(absent);
                                    absentItem.quantity = 1;
                                    newItems.push(absentItem);

                                } else {

                                    console.log("absent item not found", fullItem.absentId);
                                }
                            }


                        }
                        else {
                            newItems.push(mitem);
                        }
                    });

                    mgroup.items = newItems;

                });


            }

        });

        OrderItem.flattenItems(orderitem);

        return orderitem;
    }


    findById(list: any, id: any) {
        if (!list)
            return null;
        for (let o of list) {
            if (id == o.id) {
                return o;
            }
        }

        return null;
    }

    /*private cloneMod(from: Item): Item {

        var item = new Item();

        item.id = from.id;
        item.name = from.name;
        item.price = from.price;
        item.quantity = 0;
        item.backendId = from.backendId;
        item.extra = from.extra;

        return item;

    }

    private cloneIG(from: ItemGroup): ItemGroup {

        var ig = new ItemGroup();

        ig.id = from.id;
        ig.items = [];
        ig.name = from.name;
        ig.backendId = from.backendId;
        ig.printers = from.printers;

        return ig;

    }*/

    private removeObject(object: any, array: any[]) {

        var idx = array.indexOf(object);

        if (idx != -1) {

            array.splice(idx, 1);

        }


    }


    /**
     * Find all modifiable items from current order item
     * @param oi order item
     */

    public static findItems(store: Store, oi: OrderItem): Item[] {

        let gmap = store.menu['gmap'];

        //console.log("modifier builder find items", gmap, oi);

        var itemsMap = {};

        for (let ig of oi.groups) {
            var realIg = gmap[ig.id];
            for (let item of realIg.items) {
                itemsMap[item.id] = item;
            }
        }

        var items = OrderItem.getFlattenItems(oi, false);

        let result: Item[] = [];

        for (let item of items) {
            //var realItem = itemsMap[item.id]; //!!!reference
            var realItem: Item = { ...itemsMap[item.id] };
            realItem.addTime = item.addTime;
            result.push(realItem);
        }

        //console.log("modable items", result);

        result = result.filter(item => {
            let mgroups = item.mgroups;
            if (!mgroups || mgroups.length == 0) {
                return false;
            }

            return mgroups.some(mgroup => {
                let mitems: Item[] = mgroup.items;
                return mitems && mitems.length > 0;
            });
        });

        return result;

    }

    findItem(i: Item): Item {

        let imap = this.store.menu['imap'];

        if (imap)
            return imap[i.id];
        else
            return null;

    }

    findItems(oi: OrderItem): Item[] {

        //let gmap = this.store.menu['gmap'];

        return ModifierBuilder.findItems(this.store, oi);

    }

    getMGroupQTY(mainItem: Item, group: ItemGroup): number {
        let quantities = 0; // this.mdBuilder.getSelectedModifierCount(item);
        for (const tmpItem of group?.items) {
            quantities += this.getQuantity(mainItem, group, tmpItem);
        }
        return quantities;
    }

    //  mitem  mdBuilder
    canAdd(item: Item, group: ItemGroup, mItem?: Item): boolean {
        const max: number = group?.max;
        let quantities = 0; // this.mdBuilder.getSelectedModifierCount(item);
        for (const tmpItem of group?.items) {
            quantities += this.getQuantity(item, group, tmpItem);
        }
        this.mmap = {};
        if (max <= quantities) {
            return false;
        }
        group?.items?.forEach((mitem) => {
            if (mitem.mgroup && this.has(item, group, mitem)) {
                this.mmap[mitem.mgroup] = mitem;
            }
        });
        if (max > quantities && mItem && this.mmap[mItem.mgroup] && mItem.id !== this.mmap[mItem.mgroup].id) {
            return true;
        }

        return max > quantities;
    }

    clearMmapBykey(key: string) {
        this.mmap[key] = null;
    }

    clearSameMGroup(item: Item, group: ItemGroup, mItem: Item) {
        const i = this.mmap[mItem.mgroup];
        if (i) {
            this.setMod(item, group, i, 0);
        }

        this.mmap[mItem.mgroup] = mItem;
    }

    //  mdBuilber mitem
    has(item: Item, group: ItemGroup, mItem: Item): boolean {
        return this?.getQuantity(item, group, mItem) > 0;
    }


}
