import { Image } from './image';
export class Data {

    static imageHost: string;
    static debug: boolean = false;

    images: { string: Image };

    constructor() {

    }

    public static toData<T extends Data>(cls: { new(): T }, json): T {

        var result = new cls();


        for (var propName in json) {
            var value = json[propName];

            if (value != null) {
                result[propName] = result.toProperty(propName, value);
            }
        }

        return result;
    }

    protected toProperty(name: string, value) {

        if (value == null) return value;

        return value;
    }



    private static log(tag, msg){
        if(!Data.debug) return;
        console.log(tag, msg);
    }

    public static toDataArray<T extends Data>(cls: { new(): T }, jarray): T[] {

        var result = [];

        Data.log("toDataArray", jarray);

        for (let jo of jarray) {
            var data = Data.toData(cls, jo);
            if (data) {
                result.push(data);
            }
        }

        return result;
    }

    public getImage(name: string, width: number, crop: boolean): string {
        return Data.getImage(this, name, width, crop);
    }



    public static getImage(data: Data, name: string, width: number, crop: boolean): string {

        if (!data.images) return null;
        var im = data.images[name] as Image;
        if (!im) return null;

        var url = im.url;
        if (!url) return null;

        return this.getImageUrl(url, width, crop);


    }

    public static getImageUrl(url: string, width: number, crop: boolean): string {

        if (!url) return null;

        width = Math.floor(width);

        if (url.indexOf("googleuser") > 0 && width) {
            var suffix = "=s" + width;
            if (crop) {
                suffix += "-c";
            }
            url += suffix;
        } else if ((url.indexOf("content.aigens.com.cn") > 0 || url.indexOf("cdn.aigens.com.cn") > 0 || url.indexOf("cdn.aigens.cn") > 0) && width) {
            var suffix = "?x-oss-process=image/resize";
            if (crop) {
                suffix += ",m_fill";
            }
            url += suffix + ",w_" + width;
        }


        if (url.indexOf("http://") == 0 && url.indexOf("localhost") == -1) {
            url = url.replace("http:", "https:");
        }

        if (Data.imageHost) {
            var replace = "lh3.googleusercontent.com";
            var i = url.indexOf(replace);
            if (i > 0) {
                var path = url.substring(i + replace.length, url.length);
                url = Data.imageHost + path;
            }
        }

        return url;
    }

    public clone<T>(type: { new(): T; }): T {
        var obj2: any = Object.assign(new type(), this);
        return obj2;
    }

    public static cloneArray<T>(type: { new(): T; }, array) {

        var result = [];

        for (let data of array) {
            result.push(data.clone(type));
        }

        return result;

    }

    public static remove(array: any[], obj: any) {
        var index = array.indexOf(obj, 0);
        if (index > -1) {
            array.splice(index, 1);
        }
    }

    public static replace(array: any[], obj: any) {
        for (var i = 0; i < array.length; i++) {
            var id = obj.id;
            if (id && id == array[i].id) {
                array[i] = obj;
                return;
            }
        }
    }


    public static clearCache(obj){

        for(let key of Object.keys(obj)){
            if(key && key.indexOf('_') == 0){
                delete obj[key];
            }
        }

    }

    public static JsonReplacer(key, value) {
        if (key && key.indexOf('_') == 0){
            return undefined;
        }
        return value;
    }

    evaluate(condition){
        return eval(condition);

    }


    public get name18n(){
        return this.lookup('name');
    }

    private lookup(name: string) {

        var result = null;

        var lang = window['aigens']?.context?.userLang;

        console.log("get String",lang);

        if (lang) {
            result = this[name + "_" + lang];
        }

        if (!result) {
            result = this[name];
        }

        return result;
    }

}
