

import { Data } from "./data";

export class Printer extends Data {

    class: string = "Printer";
    id: string;
    code: string;
    ip: string;
    model: string;
    name: string;
    enabled: boolean = false;
    dualReceipt: boolean;
    apply: string;
    showNo: boolean;
    vendor: string;
    locale: string;
    usage: string;
    status: string;
    cloud: boolean = false;
    base: boolean;
    lastOnline: number;
    lastPrint: number;
    media: string;
    url: string;
    errorCode: string;

}