import { Item } from 'aigens-ng-core';
import { Injectable, Injector } from '@angular/core';
import { Brand, Category, Court, Data, Member, Menu, Order, Pos, Store } from 'aigens-data';
import { OrderManager } from 'aigens-framework';
import { DeepLinkService } from '../../pure/services/deep-link.service';
import { BaseContext } from '../base/base-context';
import { MenuManager } from '../managers/menu-manager';
import { PreOrderContext } from '@aigens/core/src/app/framework/services/pre-order-context.service';
import { EwalletService } from './ewallet.service';


@Injectable({
    providedIn: 'root'
})
export class OrderContext extends BaseContext {

    store: Store;
    brand: Brand;
    court: Court;

    menu: Menu;

    currentCategory: Category;
    currenCatGroup: any = null;
    selectedMode: string;

    availableCategories: Category[];
    cgroups: any[] =[];

    storeId: number;
    courtId: number;

    mode: string;

    behavior: any;

    payments: any[] = [];

    currentPayment: any;

    previousOrder: any;
    completedOrder: Order;

    personalMenu: boolean = false;

    //byod QR code key
    key: string;

    //table
    spot: string;
    seats: number;

    session: any;

    pos: Pos;

    courier: any;
    memberCode: string;

    responsePostedOrderJson: any; // save the response after posting order success
    checkoutPageHandleParams: any; // save the result after running paymentUniqueHandle() in paymentComponent
    submitingRedirectForm: boolean = false; // save the result about whether sumbit redirect form to prevent the double order

    codCashNumber: number;

    fixedStoreId = false;
    confirmedStore = false;
    confirmedMode = false;
    viewMode = false;

    lastStoreId = null;
    threeSecureVerifyBody: any = null; // for SecureVerifyPageComponent to use iframe

    slimMenu: any;
    spotData: {}; // for byod order with spot&key

    verified = [];

    private cacheCourtStoreMap = {};

    constructor(public injector: Injector, public orderManager: OrderManager, public deeplinkService: DeepLinkService,
        public preOrderContext: PreOrderContext, public ewalletService: EwalletService) {

        super(injector);

        console.log('constructing order context, this should only happen once');

        this.initFromContext();
    }


    initFromContext() {

        var aigens = window['aigens'];
        if (aigens && aigens.context) {

            var context = aigens.context;
            if (context.store) {
                this.store = context.store;
                this.storeId = this.store.id;
            }

            this.contextId = this.makeGenericKey(context, 'order', context.locale);
        }

        this.restore();

        // for calling ewallet virtual storeId.json
        if (this.mode) {
            console.log('setEwalletMode for virtual storeId.json');
            this.ewalletService?.setEwalletMode(this.mode);
        }

    }

    public clearPreviousOrder() {
        this.previousOrder = null;
    }
    checkRestoreOrder() {

        var existingOrder = this.previousOrder;

        if (this.mode == 'byod' && !existingOrder && this.preOrderContext.order && this.preOrderContext.storeId == this.storeId) {
            existingOrder = this.preOrderContext.order;
        }


        console.log('checkRestoreOrder', existingOrder, this.mode);

        if (existingOrder) {
            if (existingOrder.type == this.mode) {

                if (existingOrder.storeId && existingOrder.storeId == this.store.id) {
                    this.orderManager.reconstructOrder(existingOrder);
                    console.log('reconsructred store order', this.orderManager.order);
                } else if (existingOrder.courtId && existingOrder.courtId == this.court?.id) {
                    this.orderManager.reconstructOrder(existingOrder);
                    console.log('reconsructred court order', this.orderManager.order);
                }
            } else if (existingOrder.type == 'preorder') {
                this.deeplinkService.addPendingPreorder(existingOrder, false);
            }
        }

        this.previousOrder = null;



    }

    getCompletedOrder(orderId: string) {

        if (this.completedOrder && this.completedOrder.id == orderId) {
            return this.completedOrder;
        }

        return null;
    }
    getOrderByStoreId(storeId): Store | undefined {
        const stores = this.court?.stores || [];
        if (this.cacheCourtStoreMap[storeId]) {
            return this.cacheCourtStoreMap[storeId];
        } else {
            const store = stores.find((s) => s.id === storeId);
            if (store) {
                this.cacheCourtStoreMap[storeId] = store;
            }
            return store;
        }
    }

    setMenu(store: Store, fromCache = false) {

        this.store = store;

        const menu = store.menu;

        if (!fromCache || this.lastStoreId !== this.store?.id) {
            const mm = new MenuManager(this.injector);
            mm.processMenu(menu);
            // this.reconfig();
        }
        this.reconfig();

        this.menu = menu;

        this.availableCategories = menu.categories;

        this.lastStoreId = this.store?.id;
    }

    // it should run after the check all cat isAvailable
    initMenuCatGroup(): void{
        // console.log('catGroup HTML TEMPLATE', this.availableCategories);
        if (this.store?.menu?.catGroup) {
            this.cgroups = this.store?.menu?.cgroups?.map(cgroup => ({
                ...cgroup,
                categoryIds: cgroup?.categoryIds?.filter(catid => !!this.findCatById(catid)), //  categoryIds
            }))?.filter(c => c?.categoryIds?.length > 0); //  categoryIds = [] cgroup

            // all cats not assigned to any cat-group will be placed under the other Group
            // other group logic start
            const otherGroupCats = this.availableCategories.filter(currentCat => {
                let isInCGroups = false;
                this.cgroups.map(cgroup => {
                    if (cgroup.categoryIds?.includes(currentCat?.id)/* && currentCat?.id !== '4aef9384-53ec-4fe3-bef4-b06e8f5e0389'*/) {
                        isInCGroups = true;
                    }
                });
                return !isInCGroups;
            });
            console.log('catGroup HTML TEMPLATE:', otherGroupCats);
            const otherCGroup = {
                name: 'Other group',
                categoryIds: otherGroupCats.map(cat => cat?.id)
            };
            console.log('catGroup HTML TEMPLATE:', otherCGroup);
            if (otherCGroup?.categoryIds?.length > 0) {
                this.cgroups?.push(otherCGroup);
            }
            // other group logic end

            console.log('catGroup HTML TEMPLATE:', this.cgroups);
            // console.log('catGroup HTML TEMPLATE:', this.store.menu?.cgroups);
            // console.log('catGroup HTML TEMPLATE:', otherGroupCats);
            this.currenCatGroup = this.cgroups?.[0];
        }
    }

    findCatById(catId: string): Category {
        return this.availableCategories?.find(cat => /*cat?.id !== '4aef9384-53ec-4fe3-bef4-b06e8f5e0389' &&*/ cat?.id === catId);
    }

    private reconfig() {

        this.brand = this.store.brand;
        this.payments = this.brand.payments || [];

    }


    //clear the context, likely due to logout or switching to different brand
    clear() {

        this.store = null;
        this.storeId = null;
        this.menu = null;
        this.currentCategory = null;
        this.availableCategories = null;
        this.cgroups = null;
    }

    makeSaveData() {
        if (this.copyOrder) {
            return this.copyOrder;
        }

        var order = this.orderManager.order;
        if (!order) {
            return null;
        }

        var slimOrder = Order.toOrderData(order);

        var data = {
            order: slimOrder
        };

        if (this.court) {
            data['courtId'] = this.court.id;
        } else {
            data['storeId'] = this.storeId;
        }

        if(this.verified?.length > 0){
            data['verified'] = this.verified;
        }

        return data;
    }

    protected restoreSavedData(data: any) {

        console.log('OrderContext restoreSavedData', data);

        if(data?.verified){
            this.verified = data.verified
        }

        if (data.order) {

            this.previousOrder = data.order;
            console.log('restored previous order', this.previousOrder);
        }

    }

    copyOrder;

    copyOrderDataToContextId(storeId, mode) {
        const currentOrder = JSON.parse(JSON.stringify(this.makeSaveData()));

        if (currentOrder) {
            currentOrder.storeId = storeId;
            currentOrder.order.storeId = storeId;
            currentOrder.order.type = mode;
            this.copyOrder = currentOrder;
            this.save();
            this.copyOrder = null;
        }

        return currentOrder;
    }

    setResponsePostedOrderJson(orderJson: Order | any): void {
        // this.responsePostedOrderJson_ = orderJson;
        this.next('responsePostedOrderJson', orderJson);
    }

    clearResponsePostedOrderJson(): void {
        this.next('responsePostedOrderJson', null);
    }

    setCheckoutPageHandleParams(pramas): void {
        console.log('set checkoutPageHandleParams');
        // this.miniAppService?.echoLog(['run setCheckoutPageHandleParams, it should go to orderDetailsPage'], () => { });
        this.next('checkoutPageHandleParams', pramas);
    }

    clearCheckoutPageHandleParams(): void {
        console.log('clear checkoutPageHandleParams');
        this.next('checkoutPageHandleParams', null);
    }

    checkCanPayByEwalletBalance(): boolean {
        const ewalletTotal = this.ewalletService?.getMemberEwallectTotal();
        const currenOrderSpendBalance = this.orderManager?.order?.eqvEcreditAmt;
        // console.log('ewalletTotal', this.orderManager?.order?.eqvEcreditAmt);
        return ewalletTotal >= currenOrderSpendBalance;
    }

    isVerified(cat: Category = undefined, item: Item = undefined) {

        if(item !== undefined && item.verificationGroupIds?.length > 0){
            const verificationIds = item?.verificationGroupIds
            if (this.verified.length === 0 && verificationIds?.length > 0) {
                return false;
            }

            if (this.verified?.length > 0 && verificationIds?.length > 0) {
                if (!this.isMatchAll(verificationIds, this.verified)) {
                    return false;
                }
            }
        }
        if(cat !== undefined){
            if (cat?.warning && !cat?.isWarningViewed) {
                return false;
            }

            const verificationIds = cat?.verificationGroupIds
            if (this.verified.length === 0 && verificationIds?.length > 0) {
                return false;
            }

            if (this.verified?.length > 0 && verificationIds?.length > 0) {
                if (!this.isMatchAll(verificationIds, this.verified)) {
                    return false;
                }
            }
        }



        return true;
    }

    /*
    getMenuDeviceConfig(key: string): any {

        var result = null;

        if (this.menu?.config) {

            let config = this.menu?.config;
            result = get(config, key);
        }

        return result;
    }
    */

    isMatchAll(arr1: String[], arr2: String[]){
        if(arr2.length < arr1.length){
            return false;
        }
        for (let i = 0; i < arr1.length; i++) {
            const isExist = arr2?.includes(arr1[i]);
            if(!isExist){
                return false;
            }
        }
        return true;
    }


}

