import { Data } from './data';
import { OpeningHours } from './opening-hours';
import { Member } from './member';
import { Store } from './store';

export class PriceRule extends Data {
    class: string = "PriceRule";
    level: number = 1;
    name: string;
    id: string;
    priority: number;
    enabled:boolean;
    openings: OpeningHours;
    modes:string[];
    users:string[];
    maxTotal:number;
    minTotal:number;
    tier: string;

    protected toProperty(name: string, value) {

        switch (name) {
            case "openings":
                value = Data.toData(OpeningHours, value);
                break;
            default:
                value = super.toProperty(name, value);
                break;
        }

        return value;
    }

    private static isActive(rule: PriceRule, time: number, store: Store): boolean {

        if(!rule.openings){
            return true;
        }

        let brandOpeningHours = null;
        if (store && store.brand && store.brand.openings) {
            brandOpeningHours = store.brand.openings;
        }

        return OpeningHours.isTimeOpen(brandOpeningHours, rule.openings, time);
    }

    private static getUserScore(rule: PriceRule, member: Member): number{

        console.log('getUserScore2', rule);

        var users = rule.users;
        if(!users || users.length == 0) return 0;

        //NOTE: Only support guest or member now

        var type = "guest";
        if (member?.membership) {
            type = 'member';
        }

        if(users.indexOf(type) >= 0){
            return 100;
        }else{
            return -10000;
        }

    }
    private static getMemberTierScore(rule: PriceRule, member: Member): number{

        var users = rule.tier;
        if(!users || users.length == 0) return 0;

        //NOTE: Support member tier now

        var type = member?.membership?.memberTierPolicy?.tier || member?.membership?.tier;
        if(type === users){
            return 100;
        }else{
            return -10000;
        }
    }

    private static getModeScore(rule: PriceRule, mode: string): number{

        var modes = rule.modes;

        console.log("getModeScore2", mode);

        if(!modes || modes.length == 0) return 0;

        if(rule.modes.indexOf(mode) >= 0){
            return 100;
        }else{
            return -10000;
        }


    }

    public static getMatch(rule: PriceRule, time: number, mode: string, member: Member, store: Store, checkTierPrice: boolean): number{

        var timeOk = PriceRule.isActive(rule, time, store);
        if (!timeOk) {
            console.log('not active');
            return -10000;
        }
        var userScore = PriceRule.getUserScore(rule, member);
        var modeScore = PriceRule.getModeScore(rule, mode);
        var memberTierScore = PriceRule.getMemberTierScore(rule, member);
        //var periodScore = PriceRule.getPeriodScore(rule);
        console.log('userScore', userScore, 'modeScore', modeScore);

        if(checkTierPrice){
            if (userScore > -10000 && modeScore > -10000 && memberTierScore > -10000) {
                return userScore + modeScore + memberTierScore;
            }
        } else {
            if (userScore > -10000 && modeScore > -10000) {
                return userScore + modeScore;
            }
        }

        return -10000;
    }
}
