import { Injectable, NgZone } from '@angular/core';
import {
    // Plugins,
    // PushNotification,
    // PushNotificationToken,
    // PushNotificationActionPerformed,
    Capacitor
} from '@capacitor/core';

import {
    PushNotifications,
    PermissionStatus,
    Token,
    ActionPerformed,
    PushNotificationSchema
} from "@capacitor/push-notifications";
import { FCM } from '@capacitor-community/fcm';
import { ConfigService, Context, DataService } from '../../../../../core/src/app/framework';
import { PlatformService } from '../../../../../core/src/app/pure/services/platform.service';

// const { PushNotifications, AigensSdk } = Plugins;

@Injectable({
    providedIn: 'root'
})
export class NativeNotificationService {

    tokenCb: (token: any) => void;
    notificationCb: (notification: any) => void;
    actionCb: (notification: any) => void;
    openNotificationCb: (notification: any) => void;
    openActionCb: (notification: any) => void;

    doneSetup: boolean = false;

    lastMessageId: string;

    appInfo;
    constructor(private ngZone: NgZone, private context: Context, private dataService: DataService, private configService: ConfigService, private platformService: PlatformService) {

        console.log("NativeNotificationService constructor");

        this.getAppInfo();
    }
    private async getAppInfo() {

        const info = await this.platformService.getAppInfo();
        console.log("getAppInfo:", info);

        this.appInfo = info;
    }

    openNotification(openNotificationCb: (notification: any) => void, openActionCb: (notification: any) => void) {
        this.openNotification = openNotificationCb;
        this.openActionCb = openActionCb;
    }

    register(tokenCb: (token: any) => void, notificationCb: (notification: any) => void, actionCb: (notification: any) => void) {

        this.tokenCb = tokenCb;
        this.notificationCb = notificationCb;
        this.actionCb = actionCb;

        if (!this.doneSetup) {
            this.setup();
        } else {
            PushNotifications.register();
        }

    }

    subscribeTopic(topic: string) {
        if (!topic) return
        FCM.subscribeTo({ topic: topic })
            .then((r) => {
                console.log(`subscribed to topic ` + topic);
            })
            .catch((err) => console.log(err));

    }

    public subscribeGlobalTopic(brandId) {
        if (!brandId) return;

        var topic = "B" + brandId;
        if (!this.configService.isPrd()) {
            topic = topic + "-test";
        }
        console.log("try subscribe topic", topic);
        this.subscribeTopic(topic);
    }

    public hasPermission(callback: (granted: boolean) => void) {
        if (!Capacitor.isNativePlatform()) {
            callback && callback(false);
            return;
        }
        // if (Capacitor.getPlatform() == 'android') {
        //     var message = {
        //         method: "checkNotificationPermissions",
        //         service: "TempService",
        //         params: {

        //         }
        //     }
        //     AigensSdk.invoke(message).then((result) => {
        //         console.log("SDK Result", result);
        //         let granted = result && result.granted || false;
        //         callback && callback(granted);
        //     });
        //     return;
        // }
        PushNotifications.requestPermissions().then((result: PermissionStatus) => {

            callback && callback(result?.receive == "granted");
        });
    }

    private async setup() {

        console.log("notification setup called");


        // Request permission to use push notifications
        // iOS will prompt user and return if they granted permission or not
        // Android will just grant without prompting
        let permStatus = await PushNotifications.checkPermissions();

        if (permStatus.receive === 'prompt') {
            permStatus = await PushNotifications.requestPermissions();
        }

        if (permStatus.receive !== 'granted') {
            console.warn('User denied permissions!');
            return
        }

        this.doneSetup = true;
        await PushNotifications.register();


        // PushNotifications.requestPermissions().then((result: PermissionStatus) => {
        //     if (result?.receive == "granted") {
        //         // Register with Apple / Google to receive push via APNS/FCM
        //         this.doneSetup = true;
        //         PushNotifications.register();
        //     } else {
        //         // Show some error
        //     }
        // });

        // Get FCM token instead the APN one returned by Capacitor
        /**
        * Get fcm token to eventually use from a serve
        *
        * Recommended to use this instead of
        * @usage
        * ```typescript
        * PushNotifications.addListener("registration", (token) => {
        *   console.log(token.data);
        * });
        * ```
        * because the native capacitor method, for apple, returns the APN's token
        */
        if (Capacitor.getPlatform() === 'ios') {
            FCM.getToken()
                .then((r) => {
                    if (this.tokenCb) {
                        this.ngZone.run(() => {
                            this.tokenCb(r.token);
                        });
                    }
                })
                .catch((err) => console.log(err));
        } else if (Capacitor.getPlatform() === 'android') {
            // On success, we should be able to receive notifications
            PushNotifications.addListener('registration',
                (token: Token) => {
                    console.log('Push registration success, token: ' + token.value);
                    if (this.tokenCb) {
                        this.ngZone.run(() => {
                            this.tokenCb(token.value);
                        });
                    }
                }
            );
        }




        // Some issue with our setup and push will not work
        PushNotifications.addListener('registrationError',
            (error: any) => {
                console.log('Error on registration: ' + JSON.stringify(error));
                //alert('Error on registration: ' + JSON.stringify(error));

            }
        );

        // Show us the notification payload if the app is open on our device
        PushNotifications.addListener('pushNotificationReceived',
            (notification: PushNotificationSchema) => {
                console.log('Push received: ' + JSON.stringify(notification));
                //alert('Push received: ' + JSON.stringify(notification));

                // if (this.lastMessageId === notification.id) {
                //     return;
                // }

                // this.lastMessageId = notification.id ;

                if (this.notificationCb) {
                    this.ngZone.run(() => {
                        this.notificationCb(notification);
                        this.openNotificationCb?.(notification);
                    });
                }
            }
        );

        // Method called when tapping on a notification
        PushNotifications.addListener('pushNotificationActionPerformed',
            (notificationAction: ActionPerformed) => {
                console.log('Push action performed: ' + JSON.stringify(notificationAction));
                //alert('Push action performed: ' + JSON.stringify(notification));
                if (this.actionCb) {
                    this.ngZone.run(() => {
                        this.actionCb(notificationAction.notification);
                        this.openActionCb?.(notificationAction.notification);
                    });
                }
            }
        );


    }

    async registerPushToken(token: string,) {
        if (!Capacitor.isNativePlatform()) return;

        if (!this.appInfo) {
            const info = await this.platformService.getAppInfo();
            this.appInfo = info;
        }

        const appId = this.appInfo?.id;
        if (!this.context?.brandId || !this.context?.member || !appId) {
            console.log("missing something");
            return;
        }

        const url = '/api/v1/crm/pushregister.json';

        const params = {
            brandId: this.context.brandId,
            token: token,
            app: appId
        };

        this.dataService.postWithParams(url, params).subscribe(
            data => {
                console.log("Reged push to server", data);
                if (data) {
                    this.configService.setLocal("lastRegToken", token);

                }
            },
            err => {
                console.log("registerPushToken err:", err);

            }
        );

    }

}
