import { Injectable, Injector } from '@angular/core';
// import { Storage } from '@capacitor/storage';
import { Preferences } from '@capacitor/preferences';
import * as LZUTF8 from 'lzutf8';
import { StorageService } from '../../../../../core/src/app/framework/services/storage.service';
import { CommonService } from '../../../../../core/src/app/pure/services/common.service';

@Injectable({
    providedIn: 'root'
})
export class NativeStorageService extends StorageService {


    public static prohibitWritingToStorage = false;
    public static fromLogout = false;
    constructor(injector: Injector) {

        console.log("NativeStorageService constructor");

        super(injector);


    }


    setStorage(key: string, object: any, compress: boolean = false): Promise<any> {

        /**
         * Sometimes we need to prohibit writing to storage
         * * If get storage takes 3 seconds, get to order and then write into storage.
         * * It takes 1 second to remove order, and there will be a problem. The order is deleted, but it still exists in storage, so we need to write an attribute to prohibit writing to storage.
         * ** get storage  3, get  order  storage
         * ** remove order 1, . , storage ,  storage.
         */
        if (NativeStorageService.prohibitWritingToStorage) {
            return Promise.resolve(null);
        }

        if(object == null){

            return Preferences.remove({key: key});

        }

        if(compress){

            return this.setStorageCompress(key, object);
        }else{

            return Preferences.set({key: key, value: CommonService.serialize(object)});
        }
    }


    getStorageCompress(key: string): Promise<any>{
        return this.getStorage(key, true);
    }


    setStorageCompress(key: string, object: object): Promise<any> {

        var promise = new Promise((resolve, reject) => {

            var str = JSON.stringify(object);
            console.log("before compress", str.length);

            LZUTF8.compressAsync(str, { outputEncoding: "StorageBinaryString" }, function (result, error) {
                if (error === undefined){
                    console.log("after compress", result.length);

                    //localForage.setItem(key, result);
                    Preferences.set({key: key, value: result});

                    resolve(result);
                }
                else{
                    console.log("Compression error", error);
                    reject(error.message);
                }

            });

        });

        return promise;
    }

    async removeItem(key) {
        if (!key) return;
        await Preferences.remove({key: key});
    }

    async getStorageKeys() {
        const r = await Preferences.keys();
        return r.keys || [];
    }

    getStorage(key: string, compress: boolean = false): Promise<any>{


        var promise = new Promise((resolve, reject) => {


            Preferences.get({key: key}).then(result =>{

                var value = result.value;

                if(value == null || value == 'undefined'){
                    resolve(null);
                }else if(!compress){

                    resolve(CommonService.deserialize(value));
                }else{

                    console.log("compressed length", value['length']);

                    LZUTF8.decompressAsync(value, { inputEncoding: "StorageBinaryString" }, function (result, error) {
                        if (error === undefined){
                            console.log("decompressed length", result.length);

                            //var obj = JSON.parse(result);
                            var obj = CommonService.deserialize(result)
                            resolve(obj);
                        }
                        else{
                            console.log("Decompression error", error);
                            resolve(null);
                        }

                    });
                }

            });


        });

        return promise;
    }


}
