import { Data } from './data';
import { Store } from './store';
import { OpeningHours } from 'aigens-data';

export class Court extends Data {

    class: string = "Court";
    id: number;
    backendId: string;
    name: string;
    images: any;
    features: string[] = [];
    handle: string;
    colors: {};
    timezone: string;
    country: string;
    groupId: number;
    languages: string[];
    language: string;
    closed: boolean;
    closedMessage: string;
    stores: Store[];
    allowZeroTotalType: string[];
    discounts: any[];
    openings: OpeningHours;

    public static hasFeature(court: Court, feature: string): boolean {
        if (court.features) {
            return court.features.indexOf(feature) >= 0;
        }
        return false;
    }

    hasFeature(feature: string): boolean {
        if (this.features) {
            return this.features.indexOf(feature) >= 0;
        }
        return false;
    }

    protected toProperty(name: string, value) {

        switch (name) {

            case "stores":
                value = Data.toDataArray(Store, value);
                break;
            default:
                value = super.toProperty(name, value);
                break;

        }

        return value;
    }

}
