import {Data} from "./data";
import {Order} from "./order";
import {Discount} from "./discount";
import {Benefit} from "./benefit";
import {OrderItem} from "./order-item";
import {Store} from "./store";


export class Condition extends Data {

    class: string = "Condition";
    type: string;
    code: string; //item code, itemgroup code, coupon(promotion) code
    member: boolean; //if member
    amount: number;
    applicable: number = 1; //# times of meeting the condition
    discounted: boolean = false; //used for amount condition
    display: string;
    prefix: string; //todo not yet applied
    level: string; // order/item level
    tiers: string []; //member levels
    count: number = 1;// itemgroup count
    source: string []; //order source, kiosk or mobile
    secret: string;//for type coupon
    couponCode: string;//for type coupon
    couponPoint: number;//for type point
    applyHandling: boolean;//used for amount condition
    traverseItemsDiscountable: boolean;//ignore item.discountable?
    ignoreCase: boolean; // ignore case checking on coupon code

    constructor() {
        super();
    }

    /**Clone original condition as a new condition object**/
    static cloneCondition(cond: Condition) {
        let c = new Condition();

        if (cond.type) c.type = cond.type;
        if (cond.code) c.code = cond.code;
        if (cond.member) c.member = cond.member;
        if (cond.amount) c.amount = cond.amount;
        if (cond.applicable) c.applicable = cond.applicable;
        if (cond.discounted) c.discounted = cond.discounted;
        if (cond.applyHandling) c.applyHandling = cond.applyHandling;
        if (cond.display) c.display = cond.display;
        if (cond.prefix) c.prefix = cond.prefix;
        if (cond.level) c.level = cond.level;
        if (cond.tiers) c.tiers = [...cond.tiers];
        if (cond.source) c.source = cond.source;
        if (cond.couponPoint) c.couponPoint = cond.couponPoint;
        if (cond.count) c.count = cond.count;
        if (cond.traverseItemsDiscountable) c.traverseItemsDiscountable = cond.traverseItemsDiscountable;
        if (cond.ignoreCase) c.ignoreCase = cond.ignoreCase;

        c.getLevel();
        c.getDisplay();


        return c;
    }

    /**
     * applying discount and get discount amount
     * @param order: current order
     * @param promo: discount
     * @param benefit: discount's benefit
     * **/
    apply(order: Order, promo: Discount, benefit: Benefit, store?: Store, roundPrecision: number = 1): number {
        if (this.getLevel() == 'item') {
            let total = 0;

            if (this.type == "itemgroup_count" || this.type == 'item_count') {

                if (benefit.max > 1) {
                    let benefitMax = benefit.max;
                    //sort order item with price to set discount to the min value orderitem
                    order.orderitems.sort((a, b) => a.total - b.total);
                    let ois: OrderItem[] = [];
                    ois = JSON.parse(JSON.stringify(order.orderitems));
                    // find the oi which has the condition code
                    ois = ois.filter((oi) => {
                        return this.oiContainsCode(oi)
                    })
                    let totalCount = ois.reduce((a, b) => a + b.quantity, 0)
                    this.applicable = Math.floor(totalCount / this.count);
                    //set orderitems from min to max
                    // ois = ois.sort((a, b) =>a.total - b.total);
                    let count = 0;
                    order.orderitems.forEach((oi) => {
                        if (this.oiContainsCode(oi) && count < this.applicable) {
                            let applyCount;
                            let discount;
                            if (((oi.quantity - oi['discountClaimed']) > 0)) {
                                if ((oi.quantity - oi['discountClaimed']) >= benefitMax) {
                                    count = benefit.max
                                    discount = benefit.apply(order, this, promo, oi, benefitMax, store, roundPrecision);
                                    oi['discountClaimed'] += benefitMax;
                                    benefitMax = 0;
                                } else {
                                    applyCount = (this.applicable - count) < oi.quantity ? (this.applicable - count) : oi.quantity;
                                    discount = benefit.apply(order, this, promo, oi, applyCount, store, roundPrecision);
                                    oi['discountClaimed'] += applyCount;
                                    benefitMax = benefitMax - oi.quantity;
                                }
                            }
                            count += applyCount;

                            //let discount = benefit.apply(order, this, promo, oi, 0, store, orderManager);
                            total += discount;
                            if (discount && promo.backendId) {
                                oi.discountCode = promo.backendId;
                            }
                            if (discount || benefit.applied) oi.discountDesc = promo.desc ? promo.desc : promo.name;
                        }
                    });

                } else if (benefit.max == 1) {

                    let minOi: OrderItem;
                    let minTotal;

                    for (let oi of order.orderitems) {
                        if (this.oiContainsCode(oi) && !this.hasExistingItemLevelDiscount(oi, promo)) {
                            if ((!minTotal || minTotal > oi.total) && (oi['discountClaimed'] < oi.quantity)) {
                                minTotal = oi.total;
                                minOi = oi;
                            }
                        }
                    }

                    if (minOi) {
                        minOi['discountClaimed']++;
                    }

                    if (minOi) {
                        let discount = benefit.apply(order, this, promo, minOi, 1, store, roundPrecision);
                        total += discount;
                        if (discount && promo.backendId) {
                            minOi.discountCode = promo.backendId;
                        }
                        if (discount || benefit.applied) minOi.discountDesc = promo.desc ? promo.desc : promo.name;
                    }
                }

            } else {

                var benefitMax = benefit.max;
                order.orderitems.forEach(oi => {

                    if (this.oiContainsCode(oi) && !this.hasExistingItemLevelDiscount(oi, promo)) {
                        var discount = 0;
                        if (((oi.quantity - oi['discountClaimed']) > 0) && benefitMax > 0) {
                            if ((oi.quantity - oi['discountClaimed']) >= benefitMax) {
                                discount = benefit.apply(order, this, promo, oi, benefitMax, store, roundPrecision);
                                oi['discountClaimed'] += benefitMax;
                                benefitMax = 0;
                            } else {
                                discount = benefit.apply(order, this, promo, oi, oi.quantity, store, roundPrecision);
                                oi['discountClaimed'] += oi.quantity;
                                benefitMax = benefitMax - oi.quantity;
                            }
                        }

                        total += discount;

                        if (discount && promo.backendId) {
                            oi.discountCode = promo.backendId;
                        }
                        // oi.discounts.push(promo); //no use
                        if (discount || benefit.applied) oi.discountDesc = promo.desc ? promo.desc : promo.name;
                    }
                });
            }

            return total;

        } else {

            return benefit.apply(order, this, promo, null, 0, store, roundPrecision);
        }
    }

    /**check conditions to use the discount**/
    isSatisfied(order: Order, promo: Discount): boolean {
        let ok: boolean = false;
        if (!promo) {
            console.log("isSatisfied: ", ok);
            return ok;
        } 

        switch (this.type.toLowerCase()) {
            //unconditional
            case 'all': {
                ok = true;
                break;
            }

            case 'coupon': {
                ok = true;
                break;
            }

            case 'guest': {
                this.applicable = 0;
                if (!this.member && !order.member) {
                    ok = true;
                    this.applicable = 1;
                }
                break;
            }

            //check member type, check member tier
            case 'member': {
                this.applicable = 0;
                if (this.member && order.member && order.member.method != 'device') {
                    if (this.tiers) {
                        if (!order.member.tier || this.tiers.indexOf(order.member.tier) < 0) {
                            ok = false;
                            break;
                        }
                    }
                    this.applicable = 1;
                    ok = true;
                }
                break;
            }

            //TODO: maintotal???
            case 'total':
            case 'maintotal': {
                if (this.amount) {
                    let total = this.discounted ? order.grandTotal : order.total;

                    total = this.applyHandling ? total + order.handling : total;
                    //unlock benefit special deal
                    if (!this.discounted && promo.benefits && promo.benefits[0] && promo.benefits[0].type == "unlock") {
                        if (promo.benefits[0].code) {
                            let code = promo.benefits[0].code;
                            order.orderitems.forEach(oi => {
                                if (oi.categoryId == code && oi.quantity > 0) {
                                    total -= oi.total * oi.quantity;
                                }
                            });
                        }
                    }

                    if (total >= this.amount) {
                        this.applicable = Math.floor(total / this.amount);
                        ok = true;
                    } else {
                        this.applicable = 0;
                    }
                }
                break;
            }

            //ordered mainitem(s)
            case 'item': {
                if (this.code) {
                    this.applicable = 0;

                    if (this.code.toLowerCase() == "all") {
                        ok = true;
                        order.orderitems.forEach(oi => {
                            this.applicable += oi.quantity;
                        });
                        break;
                    }

                    let codes: string[] = this.code.split(",");
                    let total = 0;
                    let minTotal = 0;

                    if (codes && codes.length > 0) {

                        for (let oi of order.orderitems) {
                            let item = oi.items[0]; //Main Item
                            if (codes.indexOf(item.id) >= 0) {
                                if (!this.hasExistingItemLevelDiscount(oi, promo)) {
                                    total += oi.quantity;
                                    oi['tmpApplied'] = 0;
                                    if (oi['tmpApplied'] < oi.quantity && !(minTotal >= this.count)) {
                                        let q = oi.quantity;
                                        if ((q - oi['tmpApplied']) > 0) {
                                            if (q < this.count) {
                                                oi['tmpApplied'] += q;
                                                minTotal += q;
                                            } else {
                                                oi['tmpApplied'] += this.count;
                                                minTotal += this.count;
                                            }
                                        }
                                    }
                                }

                            }


                        }
                        ok = (total >= this.count && minTotal >= this.count);

                        this.applicable = Math.floor(total / this.count);
                    }
                }
                break;
            }

            //ordered items as Mainitem within specific itemgroup(s)
            case 'itemgroup': {
                if (this.code) {
                    this.applicable = 0;

                    if (this.code.toLowerCase() == "all") {
                        ok = true;
                        order.orderitems.forEach(oi => {
                            this.applicable += oi.quantity;
                        });
                        break;
                    }

                    let codes: string[] = this.code.split(",");

                    if (codes && codes.length > 0) {

                        for (let oi of order.orderitems) {
                            if (oi.groups && oi.groups.length > 0) {
                                let group = oi.groups[0]; //Main itemgroup
                                if (codes.indexOf(group.id) >= 0) {
                                    if (!this.hasExistingItemLevelDiscount(oi, promo)) {
                                        this.applicable += oi.quantity
                                    }
                                }
                            }
                        }
                    }

                    ok = this.applicable > 0;
                }
                break;
            }

            //Purchase X in specific one item group (X > 0)
            case "itemgroup_count": {

                //reset
                this.applicable = 0;


                if (this.code && this.count) {
                    if (this.code.toLowerCase() == "all") {
                        ok = true;
                        order.orderitems.forEach(oi => {
                            this.applicable += oi.quantity;
                        });
                        break;
                    }

                    let codes: string[] = this.code.split(",");

                    let total = 0;
                    let minTotal = 0;
                    let minOi: OrderItem;
                    if (codes && codes.length > 0) {
                        for (let oi of order.orderitems) {
                        if (oi.groups && oi.groups.length > 0) {
                            let group = oi.groups[0]; //Main itemgroup
                            if (codes.indexOf(group.id) >= 0) {
                                if (!this.hasExistingItemLevelDiscount(oi, promo)) {
                                    total += oi.quantity;
                                    oi['tmpApplied'] = 0;
                                    if (!(minTotal >= this.count)) {
                                        let q = oi.quantity;
                                        if ((q - oi['tmpApplied']) > 0) {
                                            if (q < this.count) {
                                                oi['tmpApplied'] += q;
                                                minTotal += q;
                                            } else {
                                                oi['tmpApplied'] += this.count;
                                                minTotal += this.count;
                                            }
                                        }
                                    }
                                }

                                } else {
                                    //check for sub item group
                                    for (let ig of oi.groups) {
                                        if (codes.indexOf(ig.id) >= 0) {
                                            if (!this.hasExistingItemLevelDiscount(oi, promo)) {
                                                //check all item quantity
                                                for (let item of ig.items) {
                                                    total += item.quantity;
                                                }
                                                if (total >= this.count) {
                                                    ok = true;
                                                    this.applicable = Math.floor(total / this.count);
                                                    break;
                                                }
                                            }

                                        }
                                    }

                                    ok = (total >= this.count && minTotal >= this.count);

                                    this.applicable = Math.floor(total / this.count);

                                    if (ok) {
                                        return ok;
                                    }
                                }
                            }
                        }
                    }
                    ok = (total >= this.count && minTotal >= this.count);

                    this.applicable = Math.floor(total / this.count);
                }
                break;

            }

            case 'item_count': {
                this.applicable = 0;


                if (this.code && this.count) {
                    if (this.code.toLowerCase() == "all") {
                        ok = true;
                        order.orderitems.forEach(oi => {
                            this.applicable += oi.quantity;
                        });
                        break;
                    }
                    let codes: string[] = this.code.split(",");
                    let total = 0;
                    let minTotal = 0;
                    if (codes && codes.length > 0) {
                        for (let oi of order.orderitems) {
                            let mainitem = OrderItem.getMainItem(oi); //mainitem
                            if (codes.indexOf(mainitem.id) >= 0) {
                                if (!this.hasExistingItemLevelDiscount(oi, promo)) {
                                    total += oi.quantity;
                                    oi['tmpApplied'] = 0;
                                    if (!(minTotal >= this.count)) {
                                        let q = oi.quantity;
                                        if ((q - oi['tmpApplied']) > 0) {
                                            if (q < this.count) {
                                                oi['tmpApplied'] += q;
                                                minTotal += q;
                                            } else {
                                                oi['tmpApplied'] += this.count;
                                                minTotal += this.count;
                                            }
                                        }
                                    }
                                }

                            }
                            //TODO: Check subitems
                        }
                    }
                    ok = (total >= this.count && minTotal >= this.count);

                    this.applicable = Math.floor(total / this.count);
                }
                break;
            }

            //ordered mainitems not under specific group
            case 'exitemgroup': {
                let total = 0;
                this.applicable = 0;
                ok = true;
                if (this.code && order.orderitems) {

                    // for (let oi of order.orderitems) {
                    //     if (oi.groups && oi.groups.length > 0){
                    //         let group = oi.groups[0]; //Main itemgroup
                    //         if (this.code !== group.id) total += oi.quantity;
                    //     }
                    // }

                    for (let oi of order.orderitems) {
                        if (oi.groups && oi.groups.length > 0) {
                            let group = oi.groups[0]; //Main itemgroup
                            if (!this.oiContainsCode(oi)) { //revert
                                ok = false;
                                break;
                            } else {
                                total += oi.quantity;
                            }
                        }
                    }
                }

                if (ok && total > 0) {
                    this.applicable = total;
                }

                break;
            }

            //not order mainitems
            case
            'exitem'
            : {
                let total = 0;

                if (this.code && order.orderitems) {

                    for (let oi of order.orderitems) {
                        if (oi.items && oi.items.length > 0) {
                            let mainitem = oi.items[0]; //Main item
                            if (this.code !== mainitem.id) total += oi.quantity;
                        }
                    }
                }

                ok = total > 0;
                this.applicable = total;

                break;
            }

            //promotion code
            case
            'code'
            : {
                this.applicable = 0;
                if (this.code && order.promoCode && this.promoCodeCheck(order.promoCode, this.code)) {
                    ok = true;
                    this.applicable = 1;
                } else if (this.code && order.promoCodes && order.promoCodes.length > 0) {
                    for (let pC of order.promoCodes) {
                        if (this.promoCodeCheck(pC, this.code)) {
                            ok = true;
                            this.applicable = 1;
                            break;
                        }
                    }
                }
                break;
            }

            //TODO check when payment -test if card no can be got??????? when check
            case
            'cardprefix'
            : {
                if (order.payment && order.payment == "creditcard" && this.prefix) {
                    //TODO
                    let payment = order.payments[0];
                    if (payment.cardNo.indexOf(this.prefix) === 0) {
                        ok = true;
                    }
                }
                break;
            }

            //TODO
            case
            "offer"
            : {
                let offer = order.offers.find(offer => {
                    return (offer.backendId === this.code) || (offer.campaignId === parseInt(this.code));
                });

                if (offer) ok = true;
                break;
            }

            //TODO
            case
            "reward"
            : {
                this.applicable = 0;
                let offer = order.offers.find(offer => {
                    return (offer.rewardId === parseInt(this.code));
                });

                if (offer) {
                    this.applicable = 1;
                    ok = true;
                }
                break;
            }

            //TODO
            case
            "welcome"
            : {
                if (order.member) {
                    //TODO get welcomed???
                    // if (order.member.membership)
                }
                break;
            }

            //TODO?? byod?? code = dinein / takeaway / delivery / byod
            case
            "ordertype"
            : {
                if (this.code) {

                    let codes: string[] = this.code.split(",");

                    if (codes && codes.length > 0) {
                        if (codes.indexOf("dinein") >= 0 && !order.takeout) {
                            ok = true;
                        } else if (codes.indexOf("takeaway") >= 0 && order.takeout) {
                            ok = true;
                        } else if (codes.indexOf("delivery") >= 0 && order.type === "delivery") {
                            ok = true;
                        } else if (codes.indexOf("byod") >= 0 && order.type === "byod") {
                            ok = true;
                        }

                    } else {
                        ok = true;
                    }
                }
                break;
            }

            case
            "seats"
            : {
                if (order.seats && order.seats > this.amount) {
                    ok = this.applicable > 0;
                }
                break;
            }

            case
            "source"
            : {
                if (this.source.indexOf(order.source) > -1) ok = true;
                break;
            }

            case
            "points"
            : {
                if (order.usedPoints == this.couponPoint) ok = true;
                break;
            }

            default:
                break;
        }
        console.log("isSatisfied: ", ok);

        return ok;
    }

    private hasExistingItemLevelDiscount(oi: OrderItem, promo: Discount) {
        let occupied = false;
        // if already applied an item level discount, do not apply another item level discount
        if (this.getLevel() === 'item') {
            if (oi.discounts && oi.discounts.length > 0) {
                occupied = oi.discounts.some(discount => {
                    return discount.id === promo.id || discount.level === 'item'
                })
                console.log('occupied: ' + promo.name, occupied)

            }
        }
        return occupied;
    }

    /**Decide discount's level, item or order level**/
    getLevel() {
        if (this.level) return this.level;

        switch (this.type) {
            case 'item':
            case 'itemgroup':
            case 'exitemgroup':
            case 'exitem':
            case "itemgroup_count":
            case "item_count": {
                this.level = "item";
                break;
            }
            case 'all':
            case 'ordertype':
            case 'coupon':
            case 'guest':
            case 'member':
            case 'total':
            case 'code':
            case 'seats':
            case 'points':
            case 'source':
            case 'reward':
            case 'cardprefix': {
                this.level = "order";
                break;
            }

            default: {
                this.level = "";
                break;
            }
        }

        return this.level;
    }

    getDisplay() {
        if (this.display) return this.display;

        switch (this.type) {

            case 'guest': {
                this.display = "Guest";
                break;
            }
            case 'member': {
                this.display = "Member";
                break;
            }
            case 'all':
            case 'coupon':
            case 'total':
            case 'itemgroup':
            case 'item':
            case 'code':
            case 'cardprefix':
            case 'exitemgroup':
            case 'source':
            case 'ordertype':
            default: {
                this.display = "";
                break;
            }
        }

        return this.display;
    }

    /**For item level discount,
     * check if order items have specific items/itemgroups with id == code
     * **/
    oiContainsCode(oi: OrderItem) {
        if (!this.code) return false;
        if (this.code.toLowerCase() == "all") return true;

        let contain: boolean = false;

        switch (this.type) {
            case "item": {
                let codes: string[] = this.code.split(",");

                if (codes && codes.length > 0) {
                    let item = oi.items[0]; //Main Item
                    if (codes.indexOf(item.id) >= 0) {
                        contain = true;
                    }
                }
                break;
            }
            case "itemgroup": {
                let codes: string[] = this.code.split(",");
                if (codes && codes.length > 0) {
                    //let item = oi.items[0]; //Main Item
                    if (codes.indexOf(oi.groups[0].id) >= 0) {
                        contain = true;
                    }
                }
                break;
            }
            case "itemgroup_count": {
                let codes: string[] = this.code.split(",");
                if (oi.groups && oi.groups.length > 0) {
                    let group = oi.groups[0]; //Main itemgroup
                    if (codes && codes.length > 0) {
                        if (codes.indexOf(group.id) >= 0) {
                            contain = true;
                        } else {
                            //check for sub item group
                            for (let ig of oi.groups) {
                                if (codes.indexOf(group.id) >= 0) {
                                    contain = true;
                                    break;
                                }
                            }
                        }
                    }

                }
                break;
            }
            case "item_count": {
                let codes: string[] = this.code.split(",");

                let mainitem = OrderItem.getMainItem(oi);//mainitem
                if (codes && codes.length > 0) {
                    if (codes.indexOf(mainitem.id) >= 0) {
                        contain = true;
                    } else {
                        //TODO: check subitem
                    }
                }


                break;
            }
            case "exitemgroup": {

                let codes: string[] = this.code.split(",");
                if (codes && codes.length > 0) {
                    //let item = oi.items[0]; //Main Item
                    if (codes.indexOf(oi.groups[0].id) < 0) {
                        contain = true;
                    }
                }
                break;
            }
            case "exitem": {
                if (oi.items && oi.items.length > 0 && (oi.items[0].id !== this.code)) {
                    contain = true;
                }
                break;
            }

            default:
                break;
        }

        return contain;
    }

    setCode(code: string) {
        this.code = code;
    }

    setDisplay(display: string) {
        this.display = display;
    }

    //TODO??
    isMainItemOnly(): boolean {
        if (this.type === 'total')
            return true;
        if (this.type == 'itemgroup' || this.type == 'itemgroup_count' || this.type == 'item_count')
            return !this.traverseItemsDiscountable;
        return false;
    }

    protected toProperty(name: string, value) {
        return value;
    }

    public promoCodeCheck(promoteCode: string, code: string): boolean {
        if(this.ignoreCase) {
            return promoteCode.toLowerCase() === code.toLowerCase();
        }
        return promoteCode === code;
    }


}
