import { Pipe, PipeTransform } from '@angular/core';
import { Context } from '../services/context.service';
import { NumberDisplayPipe } from './number-display.pipe';
import { EwalletService } from '../services/ewallet.service';

@Pipe({
    name: 'ewallet'
})
export class EwalletPipe implements PipeTransform {

    // to do(ewallet) Topup
    constructor(private context: Context, public numberDisplay: NumberDisplayPipe, public ewalletService: EwalletService) {

    }

    transform(member: any): string {
        console.log('ecreditBalance member:', member);
        // return this.ewalletService.getMemberEwallectCurrencySign() + str;
        const total = this.ewalletService?.getDisplayEwalletNumber();
        return this.ewalletService.getMemberEwallectCurrencySign() + this.numberDisplay.transform(total);

    }

}
