import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Location } from '@angular/common';
import { RoutingService } from '@aigens/core/src/app/pure/services/routing.service';

@Component({
    selector: 'app-sub-bar',
    templateUrl: './sub-bar.component.html',
    styleUrls: ['./sub-bar.component.scss']
})
export class SubBarComponent implements OnInit {

    @Input() title: string = '';
    @Input() dialog: boolean = true;
    @Input() back: boolean = true;
    @Input() close: boolean = true;
    @Input() closeBackBehaviour: boolean = false;

    @Output() backed: EventEmitter<any> = new EventEmitter();

    @Output() closed: EventEmitter<any> = new EventEmitter();

    @Input() customCloseLogic = false;

    constructor(private location: Location, private routingService: RoutingService) {
    }

    ngOnInit(): void {


    }

    backClicked() {

        if (this.backed.observers.length > 0) {
            this.backed.emit({});
        } else {
            this.routingService.navigateCloseModal();
        }


    }

    closeClicked() {
        if (!this.customCloseLogic) {
            this.routingService.navigateCloseAllModal();
        }
        this.closed.emit();

    }
}
