import { Pipe, PipeTransform } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

@Pipe({
    name: 'i18n',
    pure: true
})
export class LocaleStringPipe implements PipeTransform {

    constructor(private translateService: TranslateService) {

    }

    transform(content, lang: string = null, field: string = "name") {

        if(!content) return "";

        if(typeof content == 'string'){
            return this.translateService.instant(content);
        }

        //return value.name;
        return this.lookup(content, lang, field);
    }

    private lookup(content, lang:string, field: string) {

        var result = null;

        //var lang = window['aigens']?.context?.userLang;
        //console.log("i18n pipe", content, field,  lang);

        if (lang) {
            result = content[field + "_" + lang];
        }

        if (!result) {
            result = content[field];
        }

        return result;
    }

}
