import { Component, Injector, OnInit } from '@angular/core';
import { Capacitor } from '@capacitor/core';
import { BasePage } from 'aigens-framework';
import { LocationService } from '../../../../../../core/src/app/framework';
import { NativeCameraService } from '../../../../../../native/src/app/native/services/native-camera.service';
import { NativeLocationService } from '../../../../../../native/src/app/native/services/native-location.service';

@Component({
    selector: 'app-test-page',
    templateUrl: './test-page.component.html',
    styleUrls: ['./test-page.component.scss']
})
export class TestPageComponent extends BasePage implements OnInit {

    platform: string;
    native: any;
    scanning: boolean = false;

    constructor(private injector: Injector, private locationService: LocationService, private nativeLocationService: NativeLocationService, 
        private cameraService: NativeCameraService) { 

        super(injector);
    }

    ngOnInit(): void {

        this.platform = Capacitor.getPlatform();
        this.native = Capacitor.isNativePlatform();
    }

    getLocation(){

        this.locationService.getGps((result)=>{

            var msg = JSON.stringify(result);

            this.dialogService.showAlert("Result", msg);

        });

    }

    scanQR(){

        this.scanning = true;

        this.cameraService.startScan((result)=>{

            var msg = JSON.stringify(result);

            this.stopScan();
            this.dialogService.showAlert("Result", msg);


        });

    }

    stopScan(){

        this.scanning = false;

        this.cameraService.stopScan();
        

    }

    checkPermission(){

        

        this.cameraService.checkPermission((result)=>{

            var msg = JSON.stringify(result);

            this.dialogService.showAlert("Result", msg);

        });

    }

    getNativeLocation(){

        this.nativeLocationService.getGps((result)=>{

            var msg = JSON.stringify(result);

            this.dialogService.showAlert("Result", msg);

        });

    }

}
