import { Data } from './data';

export class TaxRule extends Data {
    class: string = "TaxRule";
    id: string;
    enabled: boolean;
    piority: number;
    name: string;
    itemType: string;
    storeId: string;
    groupId: string;
    brandId: string;
    memberId: string;
    create: number;
    locale:string;

    alias1: string;
    alias2: string;
    alias3: string;
    rate1: number;
    rate2: number;
    rate3: number;
    min1: number;
    min2: number;
    min3: number;
    inclusive: boolean;
    priority: number;

    protected toProperty(name: string, value) {

        switch (name) {
            default:
                value = super.toProperty(name, value);
                break;
        }

        return value;
    }

}