import { Data } from './data';

export class Field extends Data {
    id: number;
    name: string;
    value: any;
    type: string;
    ptype: string;
    ptypes: string[];
    input: string;
    sort: string;
    title: string;
    example: string;
    readonly: boolean = false;
    desc: string;
    locale: string;
    subname: string;
    content: any;
    required: boolean = false;
    condition: string;
    validation: string;
    action: string;
    options: any[];
    admin:boolean = false;
    i18n: string;
    time: any;
    moment: any;
    section: string;
    stage: boolean = false;
    show: boolean = false;
    resolved: boolean = false;
    feature: boolean = false;
    old: any;
    invalid: boolean = false;
    stype: string;
    published: boolean = false;
    enabled: boolean = false
    isSelected?: boolean;

    clear(){
        this.value = null;
        this.time = null;
        this.moment = null;
    }
}
